//These are base font rules for trs-icon-font
//and any other custom font that may come. If you
//need to modify the font (add or subtract characters)
//please go to the appropriate font css

/***************************** http://blog.fontspring.com/2011/02/further-hardening-of-the-bulletproof-syntax/ */
@font-face {
    font-family: 'trs-icons';
    src: url('fonts/trs-icons.eot');
    src: url('fonts/trs-icons.eot?#iefix') format('embedded-opentype'),
         url('fonts/trs-icons.woff') format('woff'),
         url('fonts/trs-icons.ttf') format('truetype'),
         url('fonts/trs-icons.svg#trs-icons') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^='trs-icons-'],
[class*=' trs-icons-'],
[data-icon] {
    position: relative;
    top: 1px;
    display: inline-block;
    font-family: 'trs-icons';
    font-style: normal;
    font-weight: 400;
    line-height: 1;
    text-transform: none;
    speak: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

[data-icon]::before {
    content: attr(data-icon);
}
