//* Display helpers */
.top-spacer {
    height: 15px;
}

.display-table {
    display: table !important;
}

.display-block {
    display: block !important;
}

.display-inline {
    display: inline !important;
}

.display-inline-block {
    display: inline-block !important;
}

// shadows
.no-shadow {
    box-shadow: none;
    text-shadow: none;
}

//* Borders */
.no-border {
    border: 0 !important;
}

.no-border-right {
    border-right: 0 !important;
}

.no-border-left {
    border-left: 0 !important;
}

.no-border-top {
    border-top: 0 !important;
}

.no-border-bottom {
    border-bottom: 0 !important;
}

.border,
.table-border {
    border: 1px solid $gray-light-3;
}

.left-content-border {
    border-left: 1px solid $left-border;
}

.border-radius {
    border-radius: 4px;
}

.border-no-radius {
    border-radius: 0 !important;
}

.border-left {
    border-left: 1px $gray-light-4 solid;

    @media screen and (max-width: $tablet - 1) {
        margin-left: -1px; // account for the extra 1px border
    }
}

//* Clearfix */
.cf {
    *zoom: 1;

    &::before,
    &::after {
        content: ' ';
        display: table;
    }

    &::after {
        clear: both;
    }
}

// Simple clear
.clear {
    clear: both;
}

//* Completely remove from the flow and screen readers. */
.is-hidden {
    display: none !important;
    visibility: hidden !important;
}

//* Completely remove from the flow but leave available to screen readers. */
// This is the same as Bootstrap's .sr-only
.is-vishidden {
    position: absolute !important;
    overflow: hidden;
    width: 1px;
    height: 1px;
    padding: 0;
    border: 0;
    clip: rect(1px, 1px, 1px, 1px);
}

//* Floats */
.right {
    float: right;
    padding: 0 0 $pad $pad;
}

.right-search {
    float: right;
    padding: 0 0 $pad;
}

.left {
    float: left;
    padding: 0 $pad $pad 0;
}

//* Text-Align */
.align-right {
    text-align: right !important;
}

.align-center {
    text-align: center !important;
}

.align-left {
    text-align: left !important;
}

//* Display Classes */
.clicked {
    display: block;
}

.hide {
    display: none;
}

.hide-small {
    @media all and (max-width: $bp-med) {
        display: none;
    }
}

.hide-med {
    @media all and (min-width: $bp-med) and (max-width: $bp-large) {
        display: none;
    }
}

.hide-large {
    @media all and (min-width: $bp-large) {
        display: none;
    }
}

//* DISPLAY CLASSES MEDIA */
@media (max-width: $mobile) {
    .hide-mobile {
        display: none;
    }

    .text-center_mobile {
        text-align: center;
    }
}

@media (min-width: $tablet) and (max-width: $desktop - 1) {
    .hide-tablet {
        display: none;
    }
}

@media (min-width: $desktop) {
    .hide-desktop {
        display: none;
    }
}

// Bootstrap already have this
.hidden {
    display: none !important;
}

@media (min-width: $tablet) and (max-width: $desktop - 1) {
    .col-sm-float {
        position: absolute;
        z-index: 10;
    }
}

@media (min-width: $mobile + 1) {
    .text-right__desktop {
        text-align: right;
    }
}

