.no-bold {
    font-weight: normal !important;
}

.caps,
.mobile .caps-xs {
    text-transform: uppercase;
}

.scrollable {
    overflow-y: auto;
    height: 100%;
    padding: 0 15px 15px 0;
    margin-right: -15px;
}

//* columns aligned to the bottom */
//* <div class="row vert-cols-bottom || row vert-cols-top"> */
[class*='vert-align-cols-'] {
    display: table;
    width: 100%;

    > [class*='col-'] {
        float: none;
        position: relative;
        display: table-cell;
        *zoom: 1;
        *display: inline;
    }
}

.row[class*='vert-align-cols-'] {
    width: 103.5%;
}

.vert-align-cols-bottom > [class*='col-'] {
    vertical-align: bottom;
}

.vert-align-cols-top > [class*='col-'] {
    vertical-align: top;
}

.vert-align-cols-middle > [class*='col-'] {
    vertical-align: middle;
}

//* table cells vertical alignment */
.vert-align-top {
    vertical-align: top !important;
}

.vert-align-middle {
    vertical-align: middle !important;
}

.vert-align-bottom {
    vertical-align: bottom !important;
}

//* mobile helper */
.mob--text__center {
    text-align: center; // why? we have text-center-xs
}

//* wrap iframes - iOS fix */
.wrap-iframe {
    width: 100%;
    border: 1px $gray-dark-5 solid;
    margin: .5em 0 1em;
    overflow: hidden; // desktop
    -webkit-overflow-scrolling: touch; // iOS

    iframe {
        border: 0;
        width: 100% !important;
        height: 100% !important;
        margin: 0;
        padding: 0;
        background: transparent;
    }

    // enforcing fix with media query
    @media screen and (max-width: $tablet) {
        overflow: auto;
    }
}

.mobile,
.tablet {
    .wrap-iframe {
        overflow: auto; // mobile (smartphones) and tablet mode
    }
}
//* end  wrap iframes - iOS fix */

// "loading" iframe - DDOL's <iframe src="/ddol/loading.html"></iframe>
.loading-iframe {
    width: 100%;
    height: 100%;
    border: 0;
    margin: 0;
    background: transparent;
    overflow: hidden;
}

// overflow
.overflow-hidden {
    overflow: hidden !important;
}

.overflow-auto {
    overflow: auto !important;
}

.overflow-visible {
    overflow: visible !important;
}

// animations and transitions
// hacking the BS collapsing transition, no animation!
// do NOT remove the vendor-specific
.no-collapsing {
    -webkit-transition-timing-function: linear !important;
    -o-transition-timing-function: linear !important;
    transition-timing-function: linear !important;

    -webkit-transition-duration: .01s !important;
    -o-transition-duration: .01s !important;
    transition-duration: .01s !important;

    -webkit-transition-property: none !important;
    -o-transition-property: none !important;
    transition-property: none !important;

    height: auto !important;
}

// no outlines
.no-outline {
    outline: 0 !important;
}

// no underlines
.no-underline {
    text-decoration: none;

    &:focus,
    &:hover,
    &:active {
        text-decoration: none;
    }
}

// line height overrides
.line-height-1 {
    line-height: 1;
}

// main logo - no border or outline
.logo-link {
    border: 0;
    outline: 0;

    * {
        border: 0;
        outline: 0;
    }
}

// pull helper
@media screen and (max-width: $mobile) {
    .pull-left-xs {
        float: left !important;
    }

    .pull-right-xs {
        float: right !important;
    }

    .pull-none-xs {
        float: none !important;
    }
}
