.accordion {
	margin-bottom: $space;
}

.acc-handle {
	background: $gray-dark-2;
	color: $white;
	font-family: $font-secondary;
	font-weight: bold;
	display: block;
	position: relative;
	padding: $pad-half;
	border-bottom: 1px solid $gray-light-3;

	&::after {
		content:"+";
		float: right;
	}

	&:hover {
		color: $white;
		background: $gray-dark;

	}

	&.active {
		background: $gray-dark;

		&::after {
			content:"-";
		}
	}
}

.trs-accordion {
	-moz-border-radius-topleft: 4px;
	-moz-border-radius-topright: 4px;
	-webkit-border-top-left-radius: 4px;
	-webkit-border-top-right-radius: 4px;
	border-top:$prim-heading-border solid 1px !important;
	border-right:$prim-heading-border solid 1px !important;
	border-left:$prim-heading-border solid 1px !important;

	table, td, tr {
		border:none;
	}
	.glyphicon {
		cursor:pointer;
	}
	.glyphicon-minus-sign, .glyphicon-plus-sign {
		color:$btn-primary;
		font-size:22px;
	}
	.panel-heading {
		border-bottom:$gray-med-2 solid 2px;

	}
	.panel-body{
		padding:0;
	}
	.panel{
			border-radius: 0px !important;
			-moz-border-radius: 0px !important;
			-webkit-border-radius: 0px !important;

	}
	tr:first-child {
		border-top:hidden;
	}
	tr {
		border-bottom:$prim-heading-border solid 1px;
	}
	tr:last-child {
		border-bottom:hidden;
	}
	td {
		padding:10px 10px;
		color:$gray-med-2;
	}
	td.text-right {
		padding-right:45px;
	}
	.panel-collapse {
		border-top:hidden;
	}
	h3 {
		.text-right {
			margin-right:10px;
			float:right;
		}
	}
}

.trs-accordion-table {
	-moz-border-radius-topleft: 4px;
	-moz-border-radius-topright: 4px;
	-webkit-border-top-left-radius: 4px;
	-webkit-border-top-right-radius: 4px;
	border:$prim-heading-border solid 1px !important;

	.glyphicon {
		cursor:pointer;
	}
	.glyphicon-minus-sign, .glyphicon-plus-sign {
		color:$accordion-expander;
		font-size:22px;
		position:absolute;
		right:30px;
		top:5px;
	}
	.panel-heading {
		border-bottom:$gray-med-2 solid 2px;
		border-top:$prim-heading-border solid 1px;
		padding:10px;
		line-height:33px;
		font-weight:bold;

		.row {
			margin-left:-10px;
			margin-right:-10px;
		}

	}
	.first .panel-heading {
		border-top:none;
	}
	.panel-body {
		padding:0 10px;

		.row {
			border-bottom:$prim-heading-border solid 1px;
			line-height:40px;
			margin-left:-10px;
			margin-right:-10px;
		}
		.col-bold {
			font-weight:600;
		}
		.last.row {
			border-bottom:none;
		}
	}
	.panel{
			border-radius: 0px !important;
			-moz-border-radius: 0px !important;
			-webkit-border-radius: 0px !important;
			box-shadow:none;
			margin:0 !important;
			border:none;

	}
}

/* Accordion with Panels */
.panel-accordion .panel, .panel-group.panel-accordion, .panel-collapse>.panel-body, .accordion-toggle, pla-panel
{
    border-top: none;
    border-left: none;
    border-right: none;
    -webkit-border-radius: 0px;
    -moz-border-radius: 0px;
    border-radius: 0px;
}

.panel-accordion .panel-heading .glyphicon, .panel-heading > .glyphicon
{
    float: right;
    color: $accordion-expander;
    font-size: 20px;
}

.panel-accordion .panel-collapse
{
    border-top: 2px solid #000;
}

.panel-accordion .panel + .panel
{
    margin-top: 0px;
}

.trs-accordion h3.panel-title
{
    font-size: $font-base;
    font-weight: bold;
}

.panel-heading.accordion-toggle
{
    background-color: $gray-light;
    background-image: none;
    background-repeat: no-repeat;
    filter: none;
}
