//*------------------------------------*\
//    $BUTTONS
//\*------------------------------------*/
.btn {
    display: inline-block;
    background-color: $gray-dark;
    background-image: none;
    color: $white;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    font-size: $font-base;
    line-height: 1.4;
    letter-spacing: 1.4px;
    margin: 0;
    padding: 9px 17px;
    border: 1px solid transparent;
    border-radius: 40px;
    min-width: 65px; // so buttons don't look 'round' - check variants below

    &:hover,
    &:focus {
        background-color: $gray;
        color: $white;
    }

    &:active,
    &.active.focus,
    &.active:focus,
    &.focus,
    &:active.focus,
    &:active:focus,
    &:focus {
        //outline: 0;
    }

    &:disabled:focus,
    &.disabled:focus,
    &:disabled,
    &.disabled {
        background-color: $gray-dark-5;
        color: $white;
        cursor: not-allowed;
        box-shadow: none;
        pointer-events: none;
    }

    sup {
        font-weight: normal;
        font-size: 100%;
        top: -.25em;
    }

    + .btn__cancel-action {
        margin-left: 60px;
    }

    // smartphone buttons need smaller font size so big buttons can fit
    @media screen and (max-width: $mobile) {
        font-size: $font-xsmall;
    }
}

// Colored buttons
.btn-primary {
    background-color: $btn-primary;
    color: $white;
    border: 1px solid $btn-primary;
    box-shadow: inset 0 -2px 0 rgba(0, 0, 0, .64);
    padding-bottom: 10px;

    &:hover,
    &:focus {
        background-color: $btn-primary-hover;
        color: $white;
        border-color: $btn-primary-hover;
    }

    &:active:focus,
    &:active {
        background-color: $btn-primary-active;
        color: $white;
        box-shadow: inset 0 2px 0 rgba(0, 0, 0, .64);
    }

    &:disabled:focus,
    &.disabled:focus,
    &:disabled,
    &.disabled {
        background-color: $btn-primary-disabled;
        color: $white;
        border-color: $btn-primary-disabled;
        box-shadow: none;

        &:hover {
            background-color: $btn-primary-disabled;
            color: $white;
            border-color: $btn-primary-disabled;
            box-shadow: none;
        }
    }

    &.solid {
        background: $btn-primary;
    }
}

.btn-secondary {
    background-color: $white;
    color: $btn-secondary-font;
    border: 1px solid $btn-secondary-border;
    box-shadow: none;

    &:hover,
    &:focus {
        background-color: $white;
        color: $btn-secondary-font;
        border-color: $btn-secondary-border-hover;
    }

    &:active:focus,
    &:active {
        background-color: $btn-secondary-active;
        color: $btn-secondary-font;
        box-shadow: none;
    }

    &:disabled:focus,
    &.disabled:focus,
    &:disabled,
    &.disabled {
        background-color: $white;
        color: $btn-secondary-font-disabled;
        border-color: $btn-secondary-border-disabled;
        box-shadow: none;

        &:hover {
            background-color: $white;
            box-shadow: none;
            border-color: $btn-secondary-border-disabled;
        }
    }

    &.solid {
        background: $white;
    }
}

.btn-logout,
.btn-login {
    background-color: $emphasis;
    color: $btn-logout-font;
    border: 0;
    box-shadow: inset 0 -2px 0 rgba(0, 0, 0, .64);

    &:hover,
    &:focus {
        background-color: $btn-logout-hover;
        //outline: 0;
    }

    &:active:focus,
    &:active {
        background-color: $btn-logout-active;
        box-shadow: inset 0 2px 0 rgba(0, 0, 0, .64);
    }

    &:disabled:focus,
    &.disabled:focus,
    &:disabled,
    &.disabled {
        background-color: $btn-logout-disabled;
        box-shadow: none;

        &:hover {
            background-color: $btn-logout-disabled;
            box-shadow: none;
        }
    }

    span {
        margin-right: 6px;
    }
}

// Default margin for buttons + buttons combo; hidden buttons + buttons combo exception
.btn.btn-primary,
.btn.btn-secondary {
    &:not([style*="display: none"]):not([style*="display:none"]):not(.trs-hidden):not(.hidden) {
        + .btn {
            &:not(.btn__vertical-stack) {
                margin-left: 30px;
            }

            // smartphone buttons
            @media screen and (max-width: $tablet - 1) {
                margin-left: 5px;
            }

            @media screen and (max-width: $mobile) {
                &.btn__mobile-stack {
                    margin-top: 30px;
                }
            }

            &.btn__vertical-stack {
                margin-top: 15px;
            }
        }
    }
}

// Margin exception for buttons inside tables (e.g. Retirement Accounts)
th,
td {
    .btn.btn-primary,
    .btn.btn-secondary {
        + .btn { margin-left: 5px !important; } // it needs to be important to overwrite the specificity of the above default margin
    }
}

// Button variants
.btn-short {
    padding: 5px 20px;
}

.btn-small {
    padding: 4px 10px;
    min-width: 50px;
}

.btn-large {
    text-transform: uppercase;
    background-color: $gray;
    font-size: 19px;
    font-weight: normal;
    min-width: 75px;
}

.btn-wide {
    padding-left: 40px;
    padding-right: 40px;
}

.text-btn {
    background: none;
    border: 0;
    box-shadow: none;
    font-style: italic;
    min-width: auto;
}

.btn__go {
    background: none;
    border: 0;
    box-shadow: none;
    line-height: 1;
    padding: 13px 8px;
    color: $link-text;
    font-weight: 700;
    display: inline-block;
    vertical-align: middle;
    max-height: 40px;
    max-width: 40px;
    text-align: center;
    min-width: auto;
}

//****** CHAT BUTTON CONSTRUCT ******/
.chat-button {
    display: inline-block;

    // vertical spacing: stacking on smartphones;
    @media screen and (max-width: $mobile) {
        margin-top: 15px;
    }
}

.btn + .chat-button .btn {
    margin-left: 15px;

    @media screen and (max-width: $tablet - 1) {
        margin-left: 5px;
    }
}

.btn--chat:not(.disabled) {
    padding-left: 20px;
    padding-right: 20px;
}

.btn--chat.disabled {
    background-color: rgba(255, 255, 255, 1);
}
//**** END CHAT BUTTON CONSTRUCT ****/

//* Tablet only changes */
@media screen and (max-width: $tablet) {
    .trs-header .btn-logout,
    .trs-header .btn-login {
        font-size: $font-small;
        width: auto;
        margin-right: 0; // 15px margin
    }
}

