//* Mobile only changes - iPhone 6 plus */
@media screen and (max-width: 736px) {
    .trs-header .btn-logout,
    .trs-header .btn-login {
        font-size: $font-small;
        padding: 3px 10px;
        margin: 3px 0 0; // visually, it's ~12px margin right
        letter-spacing: 0;
    }
}

//* Toggle Buttons */
.btn-toggle {
    .btn {
        padding: 2px 10px 3px; // it'll be around 24px tall with one line of text
        background-color: $btn-toggle-btn;
        color: $btn-toggle-font;
        border: $btn-toggle-border 1px solid;
        font-family: $font-pri-bold;
        font-weight: 600;
        font-size: $font-base;
        text-transform: inherit;
        border-radius: 4px;
        //line-height: 21px;
        //height: 24px;
        min-width: auto;
        white-space: nowrap;

        &:disabled:focus,
        &.disabled:focus,
        &:disabled,
        &.disabled {
            background-color: #c9c9c9;
            color: #fff;
        }

        //* Toggle Buttons mobile */
        @media screen and (max-width: $mobile) {
            font-size: $font-xsmall;
        }

        &.large {
            padding: 11px 12px; // it'll be around 40px tall with one line of text
            font-size: $font-small;
            //line-height: 36px; // compensate for radio hidden elements and vcenter text properlly
            //height: 40px;

            @media screen and (max-width: $mobile) {
                font-size: $font-xsmall;
            }
        }
    }


    .active:focus,
    .active {
        color: $white;
        background: $btn-toggle-active;
    }

    label {
        letter-spacing: 0;
    }
}


.btn-social {
    background-color: transparent;
    color: $btn-social-font;
    text-decoration: none;

    span {
        font-size: 42px;
    }

    &:hover,
    &:focus {
        text-decoration: none;
        background-color: $white;
        color: $btn-social-hover;
        box-shadow: none;
    }

    &:active:focus,
    &:active {
        text-decoration: none;
        background-color: $btn-social-active;
        box-shadow: none;
    }

    &:disabled:focus,
    &.disabled:focus,
    &:disabled,
    &.disabled {
        background-color: transparent;
        color: $btn-social-disabled;
        box-shadow: none;

        &:hover {
            background-color: transparent;
            color: $btn-social-hover;
            box-shadow: none;
        }
    }
}

.mobile {
    .btn.btn--small {
        height: 25px;
        width: auto;
        padding: 2px 8px;
    }
}

//* Used for collapsed main nav menu button */
.btn--menu {
    display: inline-block;
    white-space: nowrap;
    position: relative;
    padding: 9px 10px;
    margin-top: 16px;
    margin-right: 0;
    margin-left: -17px; // visually, it's 15px left margin
    margin-bottom: 8px;
    background-color: transparent;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 4px;
    cursor: pointer;

    @media screen and (max-width: $tablet - 1) {
        padding: 0 10px;
        float: left;
        margin-top: 4px;
    }

    button.btn--menu__btn {
        background-color: transparent;
        border: 0;
        cursor: pointer;
        float: none;
        //outline: 0;
        display: inline-block;
        vertical-align: middle;
        width: auto;
        height: auto;
        line-height: normal;
        padding: 1px 6px;
    }

    .icon-bar {
        display: block;
        width: 20px;
        height: 2px;
        border: 1px solid #888;

        + .icon-bar {
            margin-top: 4px;
        }
    }

    .btn--menu__text {
        font-size: $font-base;
        font-weight: bold;
        margin-top: 0;
        float: none;
        display: inline-block;
        vertical-align: middle;
    }
}

@media screen and (max-width: $mobile) {
    .btn--menu {
        .btn--menu__btn {
            padding-right: 0;
        }

        .btn--menu__text {
            font-size: $font-small !important;
        }

        .icon-bar {
            width: 11px;

            + .icon-bar {
                margin-top: 2px;
            }
        }
    }
}

//* Button Groups - left-right on tab & desk, stacked on mobi */
@media screen and (min-width: $tablet){
    .btn-unit-primary-action {
        float: right;

        + .btn-unit-primary-action {
            margin-right: 15px;
        }
    }

    .btn-unit-secondary-action {
        float: left;

        + .btn-unit-secondary-action {
            margin-left: 15px;
        }
    }
}

@media screen and (max-width: $tablet - 1){
    .btn-unit {
        float: none;
        text-align: center;
        margin-top: 30px;

        &:first-child {
            margin-top: 0;
        }
    }
}

//*------------------------------------*\
//$ PLA TOP OF PAGE NAV BUTTONS
//\*------------------------------------*/
.btnnavgroup {
    border-radius: 0 !important;
    text-align: left;
    height: 60px  !important;
    max-height: 60px;
    white-space: inherit !important;
    border: 1px solid $btn-nav-hover;
    font-family: $font-pri-semi-bold;
    font-size: $font-med;
    text-transform: none;
    background-color: $white;
    color: $black;

    &.active:focus,
    &.active {
        background-color: $emphasis !important;
        color: $white !important;
    }

    a:active:focus,
    a:active {
        background-color: $emphasis;
        color: $white;
    }

    &:hover {
        background-color: $btn-nav-hover !important;
        color: $black !important;
        border: 1px solid $btn-nav-hover !important;
    }
}

.btnnavgroup-vertical {
    margin-left: 0 !important;
    border-radius: 0 !important;
    padding-left: 0;
    padding-right: 0;
}

.pla-top-button {
    float: left;
    position: relative;
    width: 33.33%;
    overflow: hidden;
}

.pla-vert-btn {
    text-transform: capitalize !important;
    background-color: $white;
    border: 1px solid $box-borders;
    color: $black;
    font-size: $font-xsmall !important;
    font-weight: 700 !important;
    text-align: left;
    box-shadow: none;

    &:hover {
        background-color: $box-borders !important;
        color: $black;
        border: 1px solid $box-borders !important;
    }

    &:active,
    &:active:focus,
    &.active,
    &.active:focus,
    &:focus {
        background-color: $emphasis !important;
        color: $white;
        border: 1px solid $emphasis !important;
    }
}

.btn-group-vertical > .pla-vert-btn:first-child:not(:last-child) {
    border-top-right-radius: 0 !important;
    border-top-left-radius: 0 !important;
    margin-left: 0 !important;
}

.btntitle {
    position: relative;
    top: 50%;
    margin-top: -10px;
    min-width: 80px;
    line-height: 18px;
    font-size: 13px;
    text-shadow: none;
}

#btnnavdiv .btn-group:first-child {
    border-top: 1px solid $btn-nav-border;
}

.btnnavgroupicon {
    background-color: $btn-nav-arrow;
    padding: 5px;
    border-radius: 100%;
    color: $white;
    border: 0;
}

.btn.btn-default {
    border-bottom: 1px solid $btn-nav-border;
}

.btn-group.btn-group-justified:last-child {
    border-right: 1px solid $btn-nav-border;
}

//*Icons modifier for buttons*/
.btn [class^="trs-icons-"] {
    top: 1px;
    display: inline-block;
    margin-top: -2px;
     -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.btn .glyphicon {
    margin-top: -2px;
}

// SEARCH BUTTON (ICON)
.btn__search {
    display: inline-block;
    background: none;
    color: $gray-dark;
    border-radius: 0;
    margin: 0 8px;
    padding: 0;

    &:active:focus,
    &:active,
    &:focus,
    &:hover {
        background: none;
        color: $gray-dark;
    }
}

//* SSO ONLY BUTTONS */
.sso--btn__sm {
    height: 30px;
    line-height: 0;
    margin: 0;
}

@media screen and (min-width: $mobile) {
    .link--style1 {
        line-height: 40px;
    }
}
