//*------------------------------------*\
//    $ONTRACK
//\*------------------------------------*/
.layout-responsive,
[data-ui-compat=responsive] {

    // iframes - print version
    .print-version {
        height: 225px;
    }

    // Aligner for labels/etc on form rows
    .aligner {
        line-height: 40px;
        padding: 0;
        margin: 0;
        vertical-align: middle;
    }

    @media all and (max-width: $mobile) {
        .aligner--mobile {
            display: inline-block;
            line-height: normal;
            margin-bottom: 5px;
            padding-bottom: 0;
        }

        label.aligner--mobile {
            display: block; // Make sure labels are stacked on top of fields
        }

        .dataTables_scrollHead {
            width: 100%;
        }
    }

    // Income Gap
    .income-table {
        // minus sign before gap
        .signaled.danger::before,
        .signaled.negative::before {
            content: '-';
            margin-right: 2px;
        }
    }

    // Modeling page - Brighten Your Outlook
    .modeling-area {
        // Using Bootstrap tooltip HTML, minor tweaks
        .tooltip {
            @include transition(left, .5s, ease-out);
            left: 0;
            white-space: nowrap;
            opacity: 1;
            margin-top: -20px;
            line-height: 24px;
            font-weight: 600;
            font-family: $font;
            font-size: $font-base;
            z-index: 1040;

            &.top {
                margin: 0 0 17px;
                top: auto;
                bottom: 100%;
            }

            &.bottom {
                margin: 14px 0 0;
                top: 100%;
                bottom: auto;
            }
        }

        table {
            border: 0;

            td {
                border: 0;
            }
        }

        .range-label {
            padding: 0;
        }

        .pre-forecast {
            .forecast-image {
                font-size: $font-small;

                .img {
                    width: 50%;
                }
            }
        }

        &.forecast {
            p {
                margin-bottom: 5px;
            }

            .panel-do-it {
                .forecast-text {
                    font-size: $font-title;
                    font-weight: 600;
                }

                .forecast-image {
                    display: inline-block;
                    max-width: 70%;
                }
            }

            .income-chart {
                width: 100%;
                margin-top: 41px;

                span {
                    position: absolute;
                    display: block;
                    width: 100%;
                    height: 25px;
                    text-indent: -9999px;
                }

                .range {
                    position: relative;
                    // height: 5px;

                    .tooltip {
                        top: -17px;
                        bottom: auto;
                        margin-bottom: 0;
                    }

                    span {
                        // top: -20px;
                        text-indent: 0;
                        font-size: $font-base;
                        font-weight: 400;
                        width: auto;
                        height: auto;
                        padding: 0;
                        margin: 0;
                        min-height: 0;

                        &.left {
                            left: 0;
                            font-family: $font-sec-reg;
                        }

                        &.right {
                            right: 0;
                            font-family: $font-sec-reg;
                        }

                        &.estimate {
                            opacity: 0;
                            top: -65px;
                        }
                    }
                }

                .graph {
                    position: relative;
                    height: 40px;

                    span {
                        @include transition(width, .5s, ease-out);

                        border: 2px $white solid;

                        &.estimate {
                            bottom: 23px;
                            left: 0;
                            width: 0;
                            min-width: 5px; // 1px visually
                        }

                        &.goal {
                            bottom: 0;
                            left: 0;
                        }

                        &.gap {
                            bottom: 0;
                            left: 0;
                            height: 40px;
                        }
                    }
                }
            }

            .view-by {
                text-align: right;
                font-size: $font-small;

                span {
                    vertical-align: middle;
                }
            }

            .income-table {
                margin: 10px 0 0;

                td {
                    font-size: $font-small;
                    white-space: nowrap;
                }
            }
        }

        .spacer {
            display: block;
            clear: both;
            width: 100%;
            height: 15px;
        }

        .panel-do-it {
            background-color: $gray-light;
            border-radius: 4px;

            .panel-body {
                border-color: $gray-light;
                border-radius: 4px;
            }

            .title {
                display: block;
                padding: 0 0 5px;
            }

            input {
                display: inline-block;
                max-width: 99%;
                text-align: center;
                font-weight: 600;
                font-size: $font-h2;
            }

            span {
                display: block;
                padding: 5px 0;
                font-size: $font-title;
            }

            .strat-chosen {
                td {
                    border-bottom: 1px dotted $btn-nav-border;
                    padding: 10px 5px 0;
                    font-size: $font-base;
                }
            }
        }

        &.choose-to-save-more {
            .range-label {
                white-space: nowrap;
                line-height: 43px;
            }

            .form-group {
                margin: 10px 0 30px;

                &.try {
                    margin: -20px 0 5px;

                    label span {
                        float: left;
                        margin-top: 0;
                        margin-bottom: 20px;
                    }
                }
            }

            .panel-do-it {
                .form-group {
                    margin: 25px 0 20px;

                    &:last-child {
                        margin-bottom: 0;
                    }
                }

                &.pre-rate {
                    .form-group {
                        margin-top: 28px;
                    }
                }
            }
        }

        &.choose-new-strategy {
            .form-group {
                label {
                    padding-bottom: 15px;

                    &:last-child { padding-bottom: 0; }
                }
            }

            .panel-do-it {
                .form-group {
                    margin: 0;
                }
            }
        }

        &.adjust-goals {
            .form-group {
                margin: 10px 0 30px;

                .text-small {
                    color: $gray-warm;
                    font-style: italic;
                }
            }

            .panel-do-it {
                .form-group {
                    margin: -10px 0 0;
                }

                fieldset {
                    .form-group {
                        margin: 30px 0 35px;
                    }
                }
            }
        }
    }

    // General forms inside panels
    .ontrack__form {
        // what is this for?!
        @media screen and (min-width: $mobile + 1) {
            overflow-x: hidden;
        }

        .aligner {
            margin-left: 5px;
            margin-right: 5px;

            &:first-child {
                margin-left: 0;
            }
        }

        .frequency {
            font-size: $font-xsmall;
            margin-left: 5px;
        }

        input,
        .form-control {
            &.currency {
                width: 108px; //accomodates digits like 5,555,555

                &.currency-big {
                    width: 130px; //accomodates digites like 555,555,555
                }
            }

            &.percentage,
            &.age {
                width: 70px; //accomodates digits like 555.55
            }
        }

        .symbol {
            display: inline-block;
            vertical-align: middle;
            width: 15px;
            padding: 0;
            text-align: center;
        }

        textarea {
            vertical-align: top;
        }

        fieldset {
            .row {
                margin-bottom: 15px;
                padding-bottom: 15px;
                border-bottom: 1px solid $gray-light-10;

                .form-group {
                    margin-bottom: 0;

                    input[type=radio],
                    input[type=checkbox] {
                        + .aligner {
                            margin-left: 0;
                        }
                    }
                }

                &:last-child {
                    margin-bottom: 0;
                    border: 0;
                }

                @media all and (max-width: $mobile) {
                    &.row__mobile {
                        margin-bottom: 0;
                        padding: 15px 0;
                        border-bottom: 0;

                        &.row__mobile--bg {
                            background: $gray-light;

                            // tabs background color
                            .nav-tabs>li.active>a,
                            .nav-tabs>li.active>a:focus,
                            .nav-tabs>li.active>a:hover {
                                background-color: $gray-light;
                            }

                            .ontrack-spin {
                                background-color: $gray-light;
                            }
                        }
                    }

                    + .dotted--mobile {
                        clear: both;
                        width: auto;
                        height: 0;
                        margin: 0 -15px;
                        border: 0;
                        border-top: 1px dotted $gray-dark-6;
                    }
                }

                @media all and (min-width: $mobile + 1) {
                    + .dotted--mobile {
                        display: none;
                    }
                }
            }

            .panel-body {
                .row {
                    &:last-child {
                        border: 0;
                        padding-bottom: 0;
                    }
                }
            }
        }

        .panel-body fieldset .row:last-child {
            padding-bottom: 0;
        }

        // Profile and Goals + New User
        &.profile-and-goals,
        &.new-user {
            input {
                text-align: right;
            }

            // Specific rows
            .name input {
                width: 100%;
            }

            .dob {
                input {
                    text-align: center;
                    max-width: 136px;
                }

                // we're not using the icons for feedback anymore
                // .error input {
                //     text-align: right;
                // }
            }

            .gender label {
                margin-right: 30px;
            }

            .state select {
                @media screen and (max-width: $tablet - 1) {
                    width: 100%;
                }
            }

            .salary {
                span.aligner { // "$" label
                    width: 5%;

                    @media screen and (max-width: $tablet - 1) {
                        float: left;
                        margin: 0;
                    }
                }

                input {
                    width: 120px;

                    @media screen and (max-width: $tablet - 1) {
                        width: 95%;
                    }
                }
            }

            .desired-retirement-age,
            .retirement-income-goal {
                input,
                .percentage {
                    width: 75px;
                }
            }

            .pre-tax-income-goal,
            .after-tax-income-goal {
                .amount {
                    display: inline-block;
                    min-width: 100px;
                    text-align: right;
                }
            }
        }

        // Profile and Goals
        &.profile-and-goals {
            .nav-tabs-wrapper + .nav-tabs-mobile-border {
                background: $gray-light;
            }

            @media screen and (max-width: $tablet - 1) {
                .nav-tabs>li.active>a,
                .nav-tabs>li.active>a:focus,
                .nav-tabs>li.active>a:hover {
                    background-color: $gray-light;
                }
            }
        }

        // New User
        &.new-user {
            .how-much-saving {
                label {
                    width: 60px;
                }

                .plan-name {
                    float: none;
                    width: auto;
                    padding: 15px 0 0;
                    margin: 20px 15px 0;
                    border-top: 1.5px dotted $gray-light-3;

                    &:first-child {
                        padding-top: 0;
                        margin-top: 0;
                        border-top: 0;
                    }
                }
            }
        }

        // Expenses modal
        &.ontrack__form--expenses {
            @media screen and (min-width: $tablet) {
                .expense-period {
                    margin-right: 0;
                    width: 10%;
                }

                .expense-amount {
                    display: inline-block;
                    width: 90%;
                    text-align: right;
                    margin: 0;
                    padding-right: 12px;
                }
            }

            @media screen and (max-width: $tablet - 1) {
                .expense-period {
                    width: 50%;
                    margin: 0 0 5px;
                    padding-right: 5px;

                    .dollar-sign {
                        float: right;
                    }
                }

                .expense-amount {
                    display: inline-block;
                    margin: 0 0 5px;
                    text-align: right;
                    padding-right: 12px;
                    width: 50%;
                }

                .total-expense {
                    .expense-period,
                    .expense-amount {
                        display: inline-block;
                        line-height: 1.8;
                    }
                }
            }
        }
    }

    // Choose To Save More - Forecast Table
    .forecast-table {
        margin-bottom: 0;

        input {
            text-align: right;
        }

        tbody {
            border-width: 1px;
        }

        td {
            text-align: right;

            &:first-child {
                text-align: left;
            }

            &.tbl_subHead_bg {
                vertical-align: middle;
            }
        }

        tfoot {
            td,
            th {
                background: $white;

                &.col2,
                &.col3 {
                    width: auto;
                    min-width: 200px;
                }

                .forecast-image {
                    text-align: center;

                    .img-responsive {
                        display: inline-block;
                        min-width: 60px;
                        max-width: 60px;
                        width: 60px;
                    }
                }

                strong {
                    font-size: $font-base;
                }

                td {
                    padding: 3px 5px;
                    font-size: 13px;
                    font-weight: 400;
                    white-space: nowrap;
                    background-color: transparent;
                }
            }
        }

        table,
        table td,
        table th {
            border: 0;
        }

        .plan-name {
            display: block;
            margin-top: 10px;
        }

        .inner-row {
            line-height: 40px;
            margin: 0 0 15px;
            white-space: nowrap;

            &:last-child {
                margin: 0;
            }
        }

        .dark-bg {
            background-color: $gray-light;

            .inner-row {
                line-height: 38px;
            }
        }

        // Choose a Strategy - Do It Myself tab
        &.do-it-myself {
            .new-mix-feedback {
                span {
                    vertical-align: middle;
                }
            }

            .total {
                line-height: 1;

                small {
                    font-weight: 400;
                }
            }

            tfoot {
                td,
                th {
                    &.col2,
                    &.col3 { min-width: 0; }
                }
            }
        }

        // max account name width: delimiter for big strings
        .account-name-delimiter {
            display: block;
            max-width: 175px;
            overflow: hidden;
            text-overflow: ellipsis;
        }

    }

    //Ontrack Modals
    .modal {
        .lists {
            ul {
                float: left;
                overflow: hidden;
                width: 45%;
                margin: 0 0 15px 10%;

                &:nth-child(odd) {
                    margin-left: 0;
                }

                @media screen and (max-width: $tablet - 1) {
                    float: none;
                    width: 100%;
                    margin-left: 0;
                    margin-bottom: 0;

                    li {
                        line-height: 1;
                        margin: 0 0 15px;
                    }
                }
            }

            @media screen and (max-width: $tablet - 1) {
                max-height: 305px;
                overflow-y: scroll;
                -webkit-overflow-scrolling: touch; // makes scrolling more fluid, especially on iOS
            }
        }

        .sub-title {
            font-size: $font-title;
            font-weight: 600;
            display: inline-block;
        }

        .name input {
            width: 100%;
        }

        // .account-number.balance is currently on DDOL and it'll be removed in the future once we fix DDOL html
        .account-number.balance {
            input[type=text].employer-contribution {
                width: 110px;
                text-align: right;
            }
        }

        .ontrack__form {
            .balance .currency {
                width: 116px;
            }

            // contributions - inside [ .modal .ontrack__form ]
            .contributions {
                &.how-are-currently-invested {
                    padding-bottom: 0;
                }

                &.how-much-are-you-contributing .disclosure {
                    margin: 0;
                }

                // don't let it stack and stick on smart phones
                .employer-match-tiers {
                    input,
                    .custom-select {
                        display: inline-block;
                        margin-top: 5px;
                        margin-bottom: 5px;
                    }
                }
            }
        }

        // contributions - chart
        .contributions.chart {
            @media screen and (max-width: $tablet - 1) {
                box-shadow: none;
            }

            .panel-body {
                border-color: $gray-light;
                background: $gray-light;
                border-radius: 4px;

                .row {
                    padding-bottom: 0;
                    margin-bottom: 0;

                    label,
                    span {
                        font-size: $font-base;
                    }

                    label {
                        max-width: 66%;
                        padding-bottom: 0;
                    }

                    input {
                        width: 57px;
                    }

                    .aligner {
                        margin-left: 5px;
                    }

                    .row {
                        margin-bottom: 10px;
                        padding-bottom: 10px;

                        &:last-child {
                            margin-bottom: 0;
                            padding-bottom: 0;
                        }
                    }

                    .risk-total {
                        margin-right: 15px;
                    }

                    .allocation-graph {
                        .allocation-graph-container {
                            width: 100%;
                        }

                        .highcharts-container {
                            margin: 0 auto;
                        }

                        @media screen and (min-width: $tablet) {
                            float: right;
                        }

                        @media screen and (max-width: $tablet - 1) {
                            .allocation-graph-container {
                                padding: 0 10%;
                                margin-top: -15px;
                            }
                        }

                        // iPhone 6S and similar
                        @media screen and (min-width: 450px) and (max-width: $tablet - 1) {
                            .allocation-graph-container {
                                padding: 0 30%;
                                margin-top: -15px;
                            }
                        }
                    }
                }
            }
        }

        .search {
            input,
            button {
                vertical-align: middle;
            }

            input {
                width: 525px;

                @media screen and (max-width: $tablet - 1) {
                    float: left;
                    width: 90%;
                }
            }

            button {
                width: 35px;
                min-width: auto;
                margin: 0;
                padding: 12px 10px;
                max-height: 40px;
                outline: 0;
                box-shadow: none;

                @media screen and (max-width: $tablet - 1) {
                    width: 10%;
                }
            }
        }

        .popular-institutions {
            select {
                max-width: 525px !important; // override custom select

                @media screen and (max-width: $tablet - 1) {
                    width: 85%;
                    float: left;
                }
            }

            .btn__go {
                width: 15%;
            }
        }

        .institution-name span {
            font-weight: 600;
            margin-right: 30px;
        }

        .year {
            input { width: 60px; }
        }

        .account-login {
            .institution-name {
                margin: 15px 0;
                padding: 15px 0;
                border: 1px solid $gray-light-10;
                border-width: 1px 0;

                a {
                    font-weight: normal;
                }

                @media screen and (max-width: $tablet - 1) {
                    margin-left: -15px;
                    margin-right: -15px;
                    margin-bottom: 0;
                    padding-left: 15px;
                    padding-right: 15px;
                }
            }

            // primary button color on Login button
            .btn-login {
                background-color: $btn-primary;
            }

            // smartphone view for yodle sign in
            @media screen and (min-width: $tablet) {
                .user-login {
                    float: right;
                }

                .safety-info {
                    float: left;
                }
            }
        }

        .add-existing-accounts {
            .institution-name {
                margin: 15px 0;
                padding: 15px 0;
                border: 1px solid $gray-light-10;
                border-width: 1px 0;

                a {
                    font-weight: normal;
                }

                @media screen and (max-width: $tablet - 1) {
                    margin-left: -15px;
                    margin-right: -15px;
                    margin-bottom: 0;
                    padding-left: 15px;
                    padding-right: 15px;
                }
            }

            .account-name {
                display: inline-block;
                vertical-align: top;
                line-height: 1.4;
                max-width: 530px;

                @media screen and (max-width: $tablet - 1) {
                    margin-top: -3px;
                    max-width: 85%;
                }
            }

            label {
                display: block;
                margin-bottom: 30px;
                padding-bottom: 0;
                clear: both;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }

    // OnTrack Spinner
    .ontrack-graph-loading {
        cursor: progress;
    }

    .ontrack-spin {
        position: absolute;
        display: none;
        background: $white url('../images/ajax-loader-big.gif') no-repeat 50% 52%;
        background-size: 35px;
        width: 100%;
        height: 100%;
        z-index: 1041; // one more than tooltip
        border: 0;
        border-radius: 5px;

        &.ontrack-spin__relative {
            position: relative;
            min-height: 150px;
        }

        .ontrack-spin-inner {
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            margin: auto;
            height: 100.1%;
            text-align: center;
            border: 0;
            border-radius: 5px;
            background: transparent; // background already set on parent

            .title {
                position: absolute;
                bottom: 75%;
                left: 0;
                width: 100%;
                font-size: $font-title;
                font-weight: 600;
                line-height: 1.2;
                padding: 0 15px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }

            .text-small {
                position: absolute;
                top: 75%;
                left: 0;
                display: block;
                width: 100%;
                line-height: 1.3;
                font-weight: 400;
                padding: 0 15px;
            }
        }
    }

    // OnTrack Toaster
    .toast-container {
        position: absolute;
        top: 135px;
        right: 15px;
        display: none;
        width: 330px;
        z-index: 1041;

        .toast-inner {
            position: fixed;
            display: block;
            color: $white;
            background: $toast-bg-success;
            border: 1px $toast-border solid;
            padding: 15px;
            width: 330px;
            border-radius: 4px;
            cursor: pointer;
            box-shadow: 0 2px 3px rgba(0, 0, 0, .5);
            z-index: 1040;

            span {
                display: inline-block;
                vertical-align: middle;
                margin: 0;
                padding: 0;
                width: 265px;

                &.glyphicon {
                    width: 15px;
                    margin-right: 15px;
                    margin-top: 3px;
                    vertical-align: top;
                }
            }
        }

        &.alert-error .toast-inner {
            color: $error;
            background-color: $toast-bg-error;
            border-color: $error;
        }
    }

    // DCMA HOW ARE YOU INVESTED section
    .ma-monitoring {
        cursor: pointer;

        .blue-arrow {
            background-image: url('../images/arrowblue.png');
            background-repeat: no-repeat;
            background-position: 98% 50%;

            @media screen and (max-width: $tablet - 1) {
                background: none;
            }

            @media screen and (max-width: 320px) { // iphone 5
                > .row > .col-xs-8 {
                    padding-left: 0;
                }
            }
        }

        @media screen and (max-width: 320px) { // iphone 5
            h3 {
                font-size: 15px;
            }
        }
    }

    .ma-monitoring-chart .highcharts-title,
    .ma-monitoring-chart .highcharts-subtitle {
        left: 0 !important;
        width: 100% !important;
        text-align: center;
    }

    .table__ma-monitoring {
        border: 0;
        width: auto;

        td {
            padding: 9px 10px 0;
            vertical-align: top;
        }

        tr:last-child {
            border: 0;

            td {
                padding-bottom: 0;
            }
        }

        @media screen and (max-width: 375px) { // iphone 6
            td {
                padding: 7px 7px 0;
            }
        }

        @media screen and (max-width: 320px) { // iphone 5
            margin-right: 15px;

            td {
                padding: 3px 10px 0;
            }
        }
    }
    // /DCMA HOW ARE YOU INVESTED section

    // SUB, NO ONTRACK, WITH LINK
    .try-ontrack__sub.try-ontrack__sub--with-link {
        cursor: pointer;

        .blue-arrow {
            cursor: pointer;
            background-image: url('../images/arrowblue.png');
            background-repeat: no-repeat;
            background-position: 98% 50%;

            @media screen and (max-width: $tablet - 1) {
                background: none;
            }
        }

        .info-text {
            margin: 5px auto 0;
        }
    }
}
