// Fixes for 100% col heights only tablet +
@media screen and (min-width: $tablet) {
    .row-height {
        display: table;
        height: 100%;
    }

    .col-height {
        display: table-cell;
        height: 100%;
    }

    // Fix pixel-off right-border position (if the element's border does not match with another above or below)
    // Only occurs in webkit. DO NOT REMOVE IT.
    .col-sm-5.col-sm-5--webkit-fix {
        width: -webkit-calc(41.66666667% + 0.02px);
    }
}

//* Block */
.block {
    overflow: hidden;
    margin-bottom: 15px;

    p:last-child {
        margin-bottom: 0;
    }

    .block-row {
        border-bottom: $gray-light-4 1px solid;
        margin-left: 0;
        margin-right: 0;
    }

    .block-row__last {
        border-bottom: 0;
    }

    .row:last-child {
        border-bottom: 0; // Bootstrap override
    }
}

.block-basic {
    padding: 15px;
    border: $gray-light-4 1px solid;
    border-radius: 4px;
}

.block-well {
    padding: 0;
    border: $gray-light-4 1px solid;
    border-radius: 4px;

    .well-title {
        background-color: $gray-light;
        font-family: $font-pri-bold; // looks like a panel title H3
        font-size: 16px;
        font-weight: 700;
        line-height: 1.444em;
        padding: 10px 15px;
        border-bottom: 1px solid $gray-light-4;

        .btn-toggle {
            margin-top: -5px;
        }

        // different colors and fonts in smart phone
        @media screen and (max-width: $tablet - 1) {
            background-color: transparent;
            font-weight: 600;
            text-transform: uppercase;
        }
    }

    .well-body {
        padding: 15px; // default Bootstrap margin/padding is 15px. Was 10px.

        // different colors in smart phone
        @media screen and (max-width: $tablet - 1) {
            background-color: $gray-light;
            border: 1px solid $gray-light-3;
        }
    }

    @media screen and (max-width: $mobile) {
        border: 0;
        overflow: visible;
    }

    &.block-well__one-title {
        .well-title {
            width: 100%;
            clear: both;
        }

        .well-body {
            float: left;
            border-left: 1px solid $gray-light-3;
            margin-left: -1px; // hide the first border
            padding-left: 14px; // compensate for the hidden border

            // reset for smart phones
            @media screen and (max-width: $tablet - 1) {
                float: none;
                border: 0;
                margin-left: 0;
                padding-left: 15px;
                width: 100% !important;
            }
        }

        &.block-well__one-title--2col .well-body {
            width: 50%;
        }

        &.block-well__one-title--3col .well-body {
            width: 33%;
        }

        &.block-well__one-title--4col .well-body {
            width: 25%;
        }
    }
}

.block-left {
    border-radius: 4px 0 0 4px;
}

.block-middle {
    border-radius: 0;
    border-left: 0;
    border-right: 0;
}

.block-right {
    border-radius: 0 4px 4px 0;
}

.border-right-collapse {
    margin-right: -1px;
}

.border-left-collapse {
    margin-left: -1px;
}

@media all and (max-width: $tablet - 1) {
    .block-left,
    .block-middle,
    .block-right {
        border: 0;
    }

    .block-left + .block-right {
        margin-top: -16px;
    }
}

.headline {
    line-height: 1.2;
}

//* Hero Block */
.block-hero {
    margin-bottom: $space-half;

    .b-thumb {
        img {
            display: block;
        }
    }

    @media all and (min-width: $bp-large) {
        position: relative;

        .b-text {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            background: $dim;
            color: $white;
            padding: $pad-and-half;
        }
    }
}

//* Block Thumbnail with Headline */
.block-thumb {
    display: table;
    width: 100%;
    border-collapse: collapse;

    .b-inner {
        display: table-row;
        vertical-align: top;
        overflow: hidden;
    }

    .b-thumb {
        @media all and (min-width: $bp-small-2) {
            display: table-cell;
            vertical-align: top;
            width: 30%;
            max-width: 10em;

            img {
                display: block;
                width: 100%;
                height: auto;
            }
        }
    }

    .b-text {
        @media all and (min-width: $bp-small-2) {
            display: table-cell;
            width: 70%;
            padding: 0 $pad;
        }
    }
}

//* Block Headline Summary */
.block-headline-summary {
    a {
        display: block;
        padding: $pad-half;
    }
}

//* Block Inset */
.block-inset {
    position: relative;
    margin-bottom: $space-half;

    .b-thumb {
        position: relative;
        z-index: 0;

        img {
            display: block;
        }
    }

    .b-text {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        background: $dim;
        color: $white;
        padding: $pad-half;
    }

    .headline {
        font-size: $font-input;
    }
}

//* Block Thumb with Summary */
.block-thumb-summary {
    .b-thumb {
        float: left;
        width: 50%;
    }

    .b-text {
        margin-left: 50%;
        padding: $pad-half;
    }
}

//* Inline block (account header) */
.block-header-account {
    background: $gray-light;
    height: 65px;
    line-height: 65px;
    padding: 0;

    .divider {
        position: relative;
        height: 65px;
        border-style: solid;
        border-color: $box-borders;
        border-width: 0 1px 0 0;

        &:empty {
            padding: 0;
        }
    }

    div {
        float: left;
        padding: 0 20px;
    }

    .block-text-bold {
        font-size: $font-header;
        font-weight: bold;
    }

    .block-text-serif {
        font-family: $font-secondary;
        font-size: 23px;
        line-height: 65px;
    }

    .account-number {
        overflow: hidden;
        white-space: nowrap;

        @media screen and (max-width: 480px) {
            font-size: 14px;
        }
    }

    .btn-account {
        float: right;
    }
}

//* Login page column blocks */
.mobile {

     // Trs Activity page css
    .trs-activity-select_right {
        position: relative !important;
        top: 0;
        bottom: 0;
        right: 1px;
    }

    .trs-login__heading {
        text-align: center;
        letter-spacing: 2.2px;
        padding: 0 15px;
    }

    .trs-login__block {
        background-color: $header-bg;
        border-width: 1px 0;
        border-style: solid;
        border-color: $prim-heading-border;


        &.trs-login__block--thick {
            text-align: center;
            padding: 15px 15px 44px;
            border-width: 1px 0; //added this because of legacy trs. not cool.
            margin: 0 -15px 27px; //added this because of legacy trs. not cool.
        }

        &.trs-login__block--thin {
            padding: 15px 15px 30px;
            border-width: 1px 0; //added this because of legacy trs. not cool.
            border-style: solid; //added this because of legacy trs. not cool.
            border-color: $prim-heading-border; //added this because of legacy trs. not cool.
            margin: 0 -15px 14px; //added this because of legacy trs. not cool.
        }
    }

    .trs-login__link-spacer {
        padding: 10px 0 11px;
    }

}

.desktop,
.tablet {
    .trs-login {
        margin-bottom: 40px;
    }

    .trs-login__column {
        padding-right: 50px;
    }
}

//* Loan Payoff Summary */
.desktop,
.tablet {
    .loans-summary-tbl {
        .block-well {
            border-color: $prim-heading-border $box-borders $box-borders;
            margin-bottom: 0;
        }

        .well-title {
            border-bottom: 1px solid $prim-heading-border;
        }
    }
}

.select-loan {
    float: right;

    @media screen and (max-width: $tablet - 1) {
        display: block;
        float: none;
        margin-top: 10px;
    }
}

.loans-payoff__title {
    font-size: $font-section-title;
    letter-spacing: 1px; // it was 2.2px
    font-weight: 600;
    text-transform: uppercase;
    padding-bottom: 10px;
    margin-top: 30px;
}

.tablet .loans-payoff__title {
    margin-top: 38px;
}

.mobile {
    .loans-summary-tbl {
        margin-left: -15px;
        margin-right: -15px;

        > .block:first-of-type {
            margin-bottom: -10px;
        }
    }

    .loans-summary-tbl--mbl__heading {
        font-size: $font-section-title-mobile;
        letter-spacing: 2.3px;
        text-transform: uppercase;
        text-align: center;
        background-color: transparent;
        padding-bottom: 0;
    }

    .loans-summary-tbl--mbl__body {
        background-color: $header-bg;
        padding: 20px 10px 0;
        border-top: 1px solid $prim-heading-border;
        margin: 25px -10px 0;
        border-radius: 0;

        hr {
            margin-bottom: 0;
        }
    }

    .loans-summary-tbl--mbl__bottom {
        background-color: $header-bg;
        padding: 20px 10px 29px;
        border-bottom: 1px solid $prim-heading-border;
        margin-bottom: 40px;
        border-radius: 0;
    }
}

// Loan Refinancing
.layout-responsive .loan-calculator,
[data-ui-compat=responsive] .loan-calculator {
    .currency {
        max-width: 150px;
    }

    .row {
        padding-bottom: 15px;

        &:last-child {
            padding-bottom: 0;
        }
    }
}

//* Mobile Details Slider */
.desktop,
.tablet {
    .mobile__account-details {
        display: none;
    }
}

.mobile {
    .contained-width {
        overflow-x: hidden;
    }

    .mobile__view-wrapper {
        position: relative;
        left: 0;
        width: 100%;
        margin: 0 -15px;
        transition: left .25s ease-in-out;
        overflow: hidden; // added 2016-10-10

        .table--style2 {
            margin: 0;
        }
    }

    .mobile__account-summary,
    .mobile__account-details {
        float: left;
        //overflow-y: hidden; // removed 2016-10-10
        width: 100%;
    }

    .mobile__account-summary {
        margin-bottom: 100px;
    }
}

//* SSO SPECIFIC STYLES */
.sso-terms-block {
    height: 210px;
    overflow-x: hidden;
    overflow-y: scroll;
    border: $box-borders solid 1px;
    padding: 13px;
}

.sso--box__grey {
    padding: 20px;
    background-color: $gray-light;
}

.sso--box__grey2 {
    padding: 20px 0;
    background-color: $gray-light;
}

//* PLA ADDITION FROM ZEPLIN */
.mobile {
    .mobile-phone {
        background-color: #4a4a4a;
        overflow: visible;
        min-height: 36px;
        height: auto;
        padding: 0;
        text-align: center;
        color: #fff;


        a {
            color: #fff;
            font-size: 13px;
            text-decoration: underline;
            font-weight: 600;
            line-height: 36px;
        }

        .icon {
            font-size: 12px;
            margin-right: 3px;
        }
    }
}

.tablet .mobile-phone,
.desktop .mobile-phone {
    display: none;
}

// Page Meter
.page-meter {
    display: inline-block;
    text-align: center;
    border-bottom: 2px solid $gray-light-4;
    margin-bottom: 43px;


    ol {
        margin-bottom: -13px;
    }
}

.page-meter__step {
    display: inline-block;
    background-color: $gray-light-1;
    min-width: 24px;
    border: 2px solid $gray-light-4;
    border-radius: 12px;
    margin: 0 10px;


    &:first-of-type {
        margin-left: 0;
    }

    &:last-of-type {
        margin-right: 0;
    }
}

.page-meter__step--complete,
.page-meter__step--active {
    background-color: $white;

    .glyphicon,
    .page-meter__icon {
        color: $valid;
        font-size: 21px;
        line-height: 12px;
        top: 5px;
    }
}

// ====================================== DCMA SPECIFIC STYLES
.choose-to-save,
.adjust-goals,
.how-invested {
    .block-well {
        background: transparent;

        .well-body {
            background: transparent;
        }
    }

    .new-rate {
        .block-well,
        .well-body {
            background: $gray-light;
        }
    }

    @media screen and (max-width: $tablet - 1) {
        .block-well {
            .well-body {
                height: 100%;

                input {
                    margin-top: 35px;
                }
            }
        }

        .ui-slider.ui-slider-horizontal .ui-slider-handle + .handle-tooltip {
            bottom: -13px;
            z-index: 1;
        }

        .tooltip-inner {
            padding: 5px;
            font-size: 11px;
        }
    }
}

.affect-retirement {
    @media screen and (min-width: $tablet) { //desktop
        .tab-content>.tab-pane {
            display: block;
        }
    }

    @media screen and (max-width: $tablet - 1) { //mobile
        .img-responsive {
            max-height: 63px;
        }

        .forecast-text {
            font-size: $font-small;
        }
    }
}

// DCMA - Managed Advice Landing Page
.layout-non-responsive .row.ma-icon-block, // so it works on non-responsive
[data-ui-compat='non-responsive'] .row.ma-icon-block, // so it works on non-responsive

.ma-icon-block {
    background-color: $gray-light-12;
    padding: 10px;
    position: relative;
    margin: 50px -15px;

    .arrow-down {
        border-left: 20px solid transparent;
        border-right: 20px solid transparent;
        border-top: 20px solid $white;
        position: absolute;
        margin-top: -10px;
        left: 50%;
        margin-left: -20px;
        width: 20px;
        height: 20px;

        &.arrow__gray {
            border-top: 20px solid $gray-light-12;
        }

        &.arrow__bottom {
            bottom: -20px !important;
        }
    }

    .left-border {
        background-image: linear-gradient(to bottom, #536270 5%, rgba(255, 255, 255, 0) 0%);
        background-position: left;
        background-size: 1px 7px;
        background-repeat: repeat-y;
        width: 1px;
        position: absolute;
        height: 50%;
        top: 50%;
        margin-left: -20px;
        transform: translateY(-50%);
    }

    [class*=col] {
        padding: 20px;
    }
}

.ma-h2 {
    color: #4a4a4a;
    margin: 10px 0;
}

.ma-h3 {
    color: #404f5c;
    margin: 10px 0;
}

.ma-horz-dotted {
    background-image: linear-gradient(to right, #536270 40%, rgba(255, 255, 255, 0) 20%);
    background-position: top;
    background-size: 6px 1px;
    background-repeat: repeat-x;
    width: 100%;
    height: 1px;
    margin-top: 20px;
}

.ma-p__gray {
    color: #41505c;
}

.ma-trial-box {
    background: $gray-light-12;
    margin: 15px 20px;
    padding: 10px 15px;
    font-size: $font-xxxsmall;
}

// DCMA confirmation
.row__pointer {
    display: inline-block;
    width: 5em;
    height: 5em;
    border-width: 2.5em;
    border-style: solid;
    border-color: $white $gray-light $gray-light;
    margin-top: -1px;
}

.panel--shaded {
    background-color: $gray-light;
    padding-bottom: 45px;
    margin: 0 -15px;
}

.margin-container {
    margin: 30px 0;

    .margin-container p {
        margin: 0;
    }
}

// About Probability Illustrations
.about-probability {
    font-size: $font-small; // if the font-size here changes, the following 'max-width's inside @media will probably need changing as well
    line-height: 1.1;
    margin-bottom: 15px;
    margin-top: 15px;

    .head-text {
        display: inline-block;
        margin-left: 5px;
        vertical-align: top;

        @media screen and (min-width: 320px) { // iPhone 5
            max-width: 91%;
        }

        @media screen and (min-width: 375px) { // iPhone 6
            max-width: 92.5%;
        }

        @media screen and (min-width: 414px) { // iPhone 6+
            max-width: 94%;
        }

        @media screen and (min-width: 415px) { // Anything else above that
            max-width: 96%;
        }
    }

    .main-text {
        margin-bottom: 15px;

        .title {
            font-size: $font-base;
            font-weight: bold;
            margin-bottom: 10px;
        }

        p {
            font-size: $font-small;
            margin-bottom: 10px;
        }
    }
}
