//*-------------------------------------------------------*\
//    Accordion-like structures meant to hold table data
//    See https://app.zeplin.io/project.html#pid=55bfd25a48bc827e1a6bd3a5&sid=5605fd4d739725210b4b8a14
//\*------------------------------------------------------*/

.mobile-table {
	@mixin mobile-table__row {
		min-height: 50px;
		width: 100%;
		padding: 14px 15px;
		border: solid 1px $gray-light-1;
		vertical-align: center;

		h3 {
			text-align: left;
			font-weight: 700;
			font-size: 14px;
		}
	}

	.mobile-table__header {
		@include mobile-table__row;
		background-color: $gray-light;

		h3 span:first-child { // first-child in the html sequence, but it's the last, right-floated column
			margin-right: 30px;
		}
	}
	.mobile-table__footer {
		@include mobile-table__row;
		background-color: $gray-light;
		margin-top: -1px; // Undo double borders on stacked rows

		dd {
			margin-right: 30px;
		}
	}
	.mobile-table__row {
		@include mobile-table__row;
		margin-top: -1px; // Undo double borders on stacked rows

		.glyphicon {
			color: $link-text;
			font-size: 20px;
			margin-left: 10px;
		}
	}

	// Custom Definition List font and margins
	dl.mobile-table__detail-view {
		margin: 20px 14px;

		dd, dt {
			font-size: 14px;
			margin: 5px 0;
		}
		dt {
			font-weight: 600;
		}
	}

	// Accordion-like animation uses Angular Animate
	.animate-if.ng-enter, .animate-if.ng-leave{
		transition: max-height ease-in-out .5s;
		overflow-y: hidden;
	}
	.animate-if.ng-enter, .animate-if.ng-leave.ng-leave-active {
		max-height: 0;
	}
	.animate-if.ng-leave, .animate-if.ng-enter.ng-enter-active {
		max-height: 200px;
	}
}

.mobile-table--light-rows .mobile-table__row h3 {
	font-weight: 600;
}
