//Intro text
.intro {
    font-size: $font-base;
    font-weight: 700;
}

//Pullquote
.pullquote {
    font-family: $font-secondary;
    font-size: $font-h4;
}

//Caption
.caption {
    font-style: italic;
}

//Passages of text
.text {
    //font-size: $font-p;

    a {
        text-decoration: underline;
    }

    ul {
        list-style: disc;
        margin: 0 0 $space 1.2em;

        ul {
            margin-bottom: 0;
        }
    }

    ol {
        list-style: decimal;
        margin: 0 0 $space 1.5em;

        ol {
            margin-bottom: 0;
        }
    }
}

// TRS - components
.status-note {
    color: $gray-med-2;
    font-size: $font-small;
    font-style: italic;
    font-weight: 300;
    font-family: $font-pri-light-ital;
    padding-right: 10px;
    margin-top: -10px;

    .hidden-mobile {
        display: none;
    }

    .visible-mobile {
        display: block;
    }
}

// smartphones (portrait mode)
@media all and (min-width: 0) and (max-width: $mobile) {
    .status-note {
        text-align: center;
    }
}

// smartphones (landscape mode), tablets and up
@media all and (min-width: $mobile + 1) {
    .status-note__separator.hidden-mobile {
        display: inline;
    }

    .status-note .visible-mobile {
        display: none;
    }
}

.status-note__separator {
    font-family: Arial, Helvetica, sans-serif; // narrower pipe
    color: $gray-dark-6; // lighter color
    font-style: normal;
    font-weight: 400;
    margin: 0 2px;
}

.status-note__name {
    font-weight: 600;
}

.small-text {
    font-size: $font-small;
    font-weight: 100;
}

.text-sm-xb {
    font-size: $font-xxxsmall !important;
    font-weight: 800  !important;
}

.text-nm {
    font-weight: 400  !important;
}

.text-sb {
    font-weight: 600  !important;
}

.text-bd {
    font-weight: 700  !important;
}

.text-xb {
    font-weight: 800 !important;
}

.section-title {
    font-size: $font-section-title;
    letter-spacing: 1px; // it was 2.2px
    font-weight: 600;
    text-transform: uppercase;
}

.section-title-date {
    font-size: $font-notation;
    font-weight: 300;
    font-style: italic;
    float: right;
    line-height: 25px !important;
}

.hgroup_icon {
    margin-bottom: 10px;

    .icon {
        font-size: 20px;
        top: 5px;
    }
}

// mobile only text styles
@media only screen and (max-width: $mobile) {
    .mobile-center {
        text-align: center;
    }

    .section-title {
        font-size: $font-section-title-mobile;
        text-align: center;
        width: 100%;
    }

    .section-title-date {
        text-align: center;
        float: none;
        width: 100%;
    }
}

// https://app.zeplin.io/project.html#pid=55bfd25a48bc827e1a6bd3a5&sid=5605fdaefd44a60f4bfb4136
.footnote,
.trs-table-footnote,
.disclosure,
.disclaimer {
    font-size: $font-xxsmall; // Using 11px: the font looks better, less "oval" than the 10px rendering
    line-height: 1.2;
    color: $gray-warm;
    margin: 1em .25em;
    padding: 0;

    strong,
    b {
        font-weight: 600;
    }

    sup {
        top: -.25em;
    }
}

//------------------------------------
//    PAGE SPECIFIC STYLES - TEXT
//------------------------------------

// Balances
.balance {
    .currency-balance-title {
        font-size: $font-title;
        font-weight: 700;
    }

    .currency-balance {
        font-family: $font-secondary;
        font-size: 40px;
        color: $black;
    }

    hr.dotted {
        width: 60%;
        margin: 15px 0;
        margin-left: 20%;
    }
}

// Modal titles
.modal-title {
    letter-spacing: initial !important;
    overflow: hidden;
    text-overflow: ellipsis;
}

//* SSO ONLY */
.sso--title {
    letter-spacing: 2px !important;
}

.sso--modal__title {
    letter-spacing: 2px !important;
}

.sso--modal__list {
    border-bottom: #ebebeb solid 1px;
    padding: 10px 0;
}

.sso--modal__footer {
    padding-bottom: 25px;
    text-align: center;
}

hr.sso--hr {
    height: 2px;
    border: 0;
    margin: 5px 0 0;
    padding: 0;

    &.sso__orange {
        border-top: 2px solid #e87e04;
    }

    &.sso__black {
        border-top: 2px solid #000;
        margin-bottom: 30px;
    }

    &.sso__blue {
        border-top: 2px solid #147fc1;
    }

    &.sso__green {
        border-top: 2px solid #00a185;
    }

    &.sso__purple {
        border-top: 2px solid #8f3faf;
    }

    &.sso__red {
        border-top: 2px solid #d52027;
    }

    &.sso__margin-bottom {
        margin-bottom: 15px;
    }
}

.sso--font1 {
    color: #000;
    font-size: 16px;
}

.sso--font2 {
    color: #4a4a4a;
    font-size: 16px;
    font-weight: 100;
}

.sso--font3 {
    color: #4a4a4a;
    font-size: 13px;
    font-style: italic;

    &.sso__margin {
        margin: 0 15px 10px 0;
    }
}

.sso--section-title {
    font-size: 18px;
    font-weight: bold;
}

.sso--section-title__date {
    font-size: 13px;
    font-style: italic;
    float: right;
    line-height: 25px !important;
}

.sso--question {
    width: 18px;
    height: 18px;
    display: inline-block;
    border-radius: 60px;
    box-shadow: 0 0 2px #888;
    background: url('../images/help-nav.png') no-repeat 5px 3px;
}

.sso--vertical__padding {
    padding: 15px 0;
}

.sso-timeout {
    font-family: $font-secondary;
    font-size: 20px;
    font-weight: normal !important;
    margin-bottom: 10px;
}

.sso--header_16 {
    font-size: 16px !important;
}

label.sso--header_16 {
    font-size: 16px;
}

.sso--header_20 {
    font-size: 20px;
}

.sso--header_16b {
    font-size: 16px;
    font-weight: bold;
}

.sso--disabled {
    font-size: 16px;
    color: #9b9b9b;
}

.sso--bold_blue {
    color: #3e7cab;
    font-size: 16px;
    font-weight: bold;
    line-height: 40px;
}

//WHITE LABEL PAGES
.white-label__phone {
    float: right;
    font-size: 16px;
    font-family: $font-secondary;
}

// (R) symbol, etc, on titles
h1 sup {
    font-size: .5em;
    vertical-align: super;
    top: -.25em;
}
