//Layout
$max-width: 72em;
$max-width-tablet: 64em;

//Defaults
$space: 1em;
$space-and-half: $space * 1.5;
$space-double: $space * 2;
$space-quad: $space * 4;
$space-half: $space / 2;
$pad: 1em;
$pad-and-half: $pad * 1.5;
$pad-double: $pad * 2;
$pad-half: $pad / 2;
$pad-one-third: $pad / 3;
$pad-two-thirds: $pad-one-third * 2;
$pad-quarter: $pad / 4;
$pad-three-quarters: $pad * .75;

//Borders
$border-med: 3px;
$border-thick: 7px;
$footer-theme-line-height: 14.4px;

