// Ads & On-Page Banners go here. This is our effort to reduce images as readable text.
.ad__bg--ontrack {
    background: #0a4d93; //* Old browsers */
    background: linear-gradient(to right, #00aeef 0%, #0a4d93 100%); //* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    padding: 15px;

    * {
        color: $white;
    }

    a {
        text-decoration: underline;

        &:hover,
        &:focus {
            color: #68cff6;
        }
    }
}

.ad__title--ontrack {
    font-size: 35px;
    font-weight: 600;
    line-height: 1;
    letter-spacing: -1px;
    margin-bottom: 25px;
}

.ad__content--ontrack {
    font-size: 12px;
    font-weight: 300;
    font-family: Arial, Helvetica, sans-serif;
    line-height: 1.5;
}

.desktop,
.tablet {
    .ad__title--ontrack {
        margin-bottom: 60px;
    }

    .ad__content--ontrack:last-child {
        margin-bottom: 0;
    }
}

.mobile {
    .ad__title--ontrack + .ad__content--ontrack {
        padding-bottom: 25px;
    }
}

// RETIREMENT OUTLOOK BANNER with CLOUDS
.ad__bg--outlook {
    padding: 15px;
    position: relative;
    min-width: 290px; //* mobile smartphone @ 320 viewport*/
    max-width: 737px; //* tablet @ 768 viewport */
    font-size: 14px; //* base font size */
    margin: 15px auto;
    background: #00aeef; //* Old browsers */
    background: linear-gradient(to right, #00aeef 0%, #68cff6 100%); //* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

    .ad__forecast--outlook,
    .ad__title--outlook,
    .ad__content--outlook {
        color: $white;
    }

    // dark blue version with sun header
    &.ad__bg--outlook-blue {
        background: linear-gradient(to right, #194487 0%, #00aeef 100%);
        margin-top: 35px; // spacing for the sun on top
        position: relative;

        .btn {
            position: relative;
            z-index: 1;
        }

        // sun on top
        &::before {
            content: '';
            position: absolute;
            display: block;
            width: 100px;
            height: 35px;
            top: -35px;
            left: 8.7%;
            background: url('../images/sunny-notext.png') no-repeat 0 0;
            background-size: cover;

            @media screen and (max-width: $tablet - 1) {
                left: 50%;
                margin-left: -50px;
            }
        }

        // dark blue header
        .ad__header--outlook {
            position: relative;
            color: $white;
            font-size: $font-h2;
            background: radial-gradient(circle at 50% 50%, #014890 0%, #002c62 100%);
            margin: -15px -15px 0;
            padding: 5px 15px;
            margin-bottom: 15px;

            i {
                color: #fcb716;
            }

            // blue triangle
            &::after {
                content: '';
                position: absolute;
                left: 13.4%;
                bottom: -15px;
                display: block;
                width: 0;
                height: 0;
                border-left: 15px solid transparent;
                border-right: 15px solid transparent;
                border-top: 15px solid #003571;

                @media screen and (max-width: $tablet - 1) {
                    left: 50%;
                    margin-left: -15px;
                    border-top-color: #014890;
                }
            }
        }

        // four main icons
        .ad__forecast--outlook {
            margin: 60px auto;

            @media screen and (max-width: $mobile) {
                margin: 40px auto;
            }

            img {
                min-width: 0;
                width: 60px;

                @media screen and (max-width: $mobile) {
                    width: 50px;
                }
            }

            li {
                position: relative;

                // white bg ellipse with opacity
                &::before {
                    display: none;
                    position: absolute;
                    top: -6px;
                    left: 0;
                    content: '';
                    width: 100%;
                    height: 100%;
                    background: radial-gradient(ellipse at 50% 50%, #fff 0%, transparent 60%);
                }
            }
        }

        // main title
        .ad__title--outlook {
            position: relative;

            h3 {
                font-size: $font-rollup-total-2;
                font-weight: 400;
                line-height: 1;
                letter-spacing: normal;
                margin-bottom: 30px;
                text-align: center;
                text-shadow: none;

                @media screen and (max-width: $mobile) {
                    font-size: $font-large;
                    text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
                }

                @media screen and (max-width: 375px) { // up to iPhone 6
                    font-size: $font-title;
                    text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
                }

                span {
                    position: relative;

                    i {
                        color: #fcb716;
                    }
                }
            }

            // white bg with opacity
            &::before {
                position: absolute;
                top: -9px;
                left: -15px;
                content: '';
                width: 100%;
                height: 60px;
                z-index: 0;
                opacity: .3;
                background: linear-gradient(to right, #fff 0%, transparent 50%);

                @media screen and (max-width: $mobile) {
                    height: 40px;
                }

                @media screen and (max-width: 320px) {
                    height: 36px;
                }
            }
        }

        // text below title
        .ad__content--outlook {
            position: absolute;
            width: 100%;
            font-size: $font-h2;
            padding-top: 5px;
            margin-left: -15px;

            @media screen and (max-width: $mobile) {
                position: static;
                font-size: $font-title;
                margin: 0 auto 30px !important; // override class helper
            }
        }
    }
}

.ad__bg2--outlook {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 50%;
    margin-left: -15px;
    margin-right: -15px;
    background: #00aeef;
}

.ad__title--outlook {
    font-size: 34px;
    font-weight: 400;
    line-height: 1;
    letter-spacing: normal;
    margin-bottom: 30px;
    text-align: center;
    text-shadow: 1px 1px 5px rgba(0, 0, 0, .5);
}

.ad__forecast--outlook {
    display: block;
    width: 95%;
    overflow: hidden;
    margin: 0 auto 25px;

    li {
        display: inline-block;
        width: 14.28%;
        padding: 0 10px;
        text-align: center;
        position: relative;
        vertical-align: middle;
    }

    .sep {
        padding: 0;
    }

    img {
        min-width: 65px;
    }

    span {
        display: block;
        margin: 5px 0 0;
        font-size: 11px;
        text-align: center;
        white-space: nowrap;
    }

    hr {
        border: 0;
        margin: -11px 0 0;
        height: 4px;
        background: url('../images/orange-dot.png');
        background-size: auto 100%;
        background-repeat: repeat-x;
    }
}

.ad__content--outlook {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.5;

    br {
        display: none;
    }

    &:last-child {
        margin-bottom: 0;
    }
}


.ad__bg--advice {
    $img-height: 250px;
    //* Old browsers */
    background: #a3eafa;
    //* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    background: url('../images/banner-advice-narrow.png'),
                linear-gradient(to bottom, #a3eafa 0, #fff $img-height);
    background-repeat: no-repeat;
    width: 100%;

    @media all and (max-width: $tablet - 1) {
        background-size: contain;
    }

    .ad__content--advice {
        font-family: 'Open Sans';
        line-height: 150%;

        .ad-advice__title {
            margin-top: 15px;
            font-size: 24px;
            font-weight: normal;
        }

        p {
            margin-top: 40px;
            font-size: 14px;
        }

        // Several breakpoints to best fit every possible viewport size!
        @media all and (min-width: $tablet) {
            height: $img-height;
            width: 50%;
            float: right;
        }

        @media all and (max-width: $tablet - 1) {
            margin-top: 70%;
        }
    }

}

//* mobile - old smartphones */
@media all and (min-width: 0) and (max-width: 320px) {
    .ad__bg2--outlook {
        display: none;
    }

    .ad__title--outlook {
        font-size: 16px;
        margin-bottom: 10px;
    }

    .ad__content--outlook {
        font-size: 9px;

        br {
            display: block;
        }
    }

    .ad__forecast--outlook {
        margin-bottom: 10px;

        li {
            width: 25%;
        }

        img {
            min-width: 45px;
        }

        .sep {
            display: none;
        }
    }
}

//* newer smartphones */
@media all and (min-width: 321px) and (max-width: 567px) {
    .ad__bg2--outlook {
        display: none;
    }

    .ad__title--outlook {
        font-size: 18px;
        margin-bottom: 15px;
    }

    .ad__content--outlook {
        font-size: 11px;

        br {
            display: block;
        }
    }

    .ad__forecast--outlook {
        margin-bottom: 15px;

        li {
            width: 25%;
        }

        .sep {
            display: none;
        }
    }
}

//* small tablets */
@media all and (min-width: 568px) and (max-width: 767px) {
    .ad__title--outlook {
        font-size: 26px;
    }

    .ad__forecast--outlook .sep {
        visibility: hidden;
    }

    .ad__content--outlook {
        font-size: 12px;
    }
}

//* Managed Advice banners. meant to be flexible units with left aligned graphic image */
.primary-banner__wrapper {
    background-size: cover; // 704px x 107px
    background-repeat: no-repeat;
    padding: 23px 10px 23px 155px;
    color: $ma-banner-text;

    p:last-child {
        margin-bottom: 0;
    }
}

#main_content .primary-banner__title {
    font-family: Helvetica, 'Open Sans';
    font-style: italic;
    font-size: 28px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 5px;
}

.primary-banner__subtitle {
    text-transform: uppercase;
    margin: 0;
}

@media screen and (max-width: $tablet) {
    .primary-banner__wrapper {
        padding-bottom: 28px;
    }
}

@media screen and (max-width: $tablet - 1) {
    .primary-banner__wrapper {
        background-image: none !important;
        text-align: center;
        padding: 0 0 15px;
        margin-bottom: 0;
    }

    #main_content .primary-banner__title {
        margin-bottom: 5px;
    }

    .primary-banner__subtitle {
        margin: 0;
    }
}

//* vertical banner created to fill smaller spaces */
.secondary-banner {
    position: relative;
    background-color: $gray-light-11;
    padding: 15px;
    height: 100%;
}

.secondary-banner__title {
    text-align: center;
}

.secondary-banner__hero {
    background-repeat: no-repeat;
    background-size: cover;
    margin: 15px 0;
}

// try OnTrack (dashboard sub/unsub, no OnTrack data)
.try-ontrack {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    height: 310px;
    text-align: center;

    .info-text {
        display: block;
        width: 180px;
        font-size: $font-title; // 16px
        margin: 60px auto;
    }

    @media screen and (max-width: $tablet - 1) {
        position: static;
        height: auto;

        .info-text {
            font-size: $font-base; // 14px
            margin: 30px auto;
        }
    }

    &.try-ontrack__sub {
        position: static;
        height: auto;

        .info-text {
            width: auto;
            text-align: left;
            margin: 18px auto 0;

            @media screen and (max-width: $tablet - 1) {
                width: 200px;
                text-align: center;
                margin: 0 auto 20px;
            }
        }

        @media screen and (max-width: $tablet - 1) {
            border: 0;
        }
    }
}
