//* Text-Related Elements */
p {
    font-family: $font;
    font-size: $font-p;
    line-height: 1.4em;
    margin-bottom: $space;
}

blockquote {
    font-style: italic;
    border-left: 3px solid $gray-light-3;
    color: $gray;
    padding-left: $pad;
    margin-bottom: $space;

    strong,
    b {
        font-weight: 600;
    }
}

hr {
    display: block;
    clear: both;
    height: 0;
    border: 0;
    border-top: 1px solid $gray-light-10;
    margin: 15px 0;
    padding: 0;
    font-size: 0;
    line-height: 0;

    &.dotted {
        border-top-style: dotted;
    }

    &.hr--mobile__separator {
        margin: -15px 0 15px;
    }
}

abbr {
    border-bottom: 1px dotted $gray;
    cursor: help;
}

.text-larger {
    font-size: $font-title;
}

.text-larger__p {
    font-size: $font-title;
    line-height: 24px;
}

// Used in the Dashboard for Retirement Strategy section
.text-14small {
    font-size: 14px !important;
    * { font-size: 14px !important; }
    sup {
        top: -.25em;
    }
}

.text-small {
    font-size: $font-small !important;
    * { font-size: $font-small !important; }
    sup {
        top: -.25em;
    }
}

.text-xsmall {
    font-size: $font-xsmall !important;
    * { font-size: $font-xsmall !important; }
    sup {
        top: -.1em;
    }
}

.text-xxsmall {
    font-size: $font-xxsmall !important;
    * { font-size: $font-xxsmall !important; }
    sup {
        top: -.25em;
    }
}

.text-xxxsmall {
    font-size: $font-xxxsmall !important;
    * { font-size: $font-xxxsmall !important; }
    sup {
        top: -.25em;
    }
}

.text-smaller {
    font-size: smaller !important;
    * { font-size: smaller !important; }
    sup {
        top: -.25em;
    }
}

.text-sm-xb {
    font-size: $font-xxxsmall;
    font-weight: 800;
    * { font-size: $font-xxxsmall; }
}

.text-nm {
    font-weight: 400;
}

.text-sb {
    font-weight: 600;
}

.text-bd {
    font-weight: 700;
}

.text-xb {
    font-weight: 800;
}

.text-border-top {
    border-top: $prim-heading-border solid 1px;
}

//* Specific Text TA Styles */
.georgia-pro-lt-lg {
    font-family: $font-secondary;
    font-size: $font-h1;
}

.open-rg-md {
    font-family: $font;
    font-size: $font-p;
    font-style: normal;
}

.open-it-sm {
    font-family: $font;
    font-size: $font-xsmall;
    font-style: italic;
}

.tablet .icon {
    margin-right: 10px;
}

.desktop .icon {
    margin-right: 10px;
}

.bulleted {
    list-style: disc !important;
}

@media screen and (max-width: $tablet - 1) {
    p {
        font-size: $font-base;
        line-height: 1.42857em;
    }

    .text-small-sm {
        font-size: smaller;
    }

    .text-center-sm {
        text-align: center;
    }
}

@media screen and (min-width: $desktop) {
    .text-small-md,
    .text-small-lg {
        font-size: smaller;
    }

    .text-center-md,
    .text-center-lg {
        text-align: center;
    }
}

@media screen and (max-width: $mobile) {
    .text-small-xs {
        font-size: smaller;
    }

    .text-center-xs {
        text-align: center;
    }

    .align-center-xs {
        text-align: center !important;
    }

    .text-right-xs {
        text-align: right;
    }

    .align-right-xs {
        text-align: right !important;
    }

    .text-left-xs {
        text-align: left;
    }

    .align-left-xs {
        text-align: left !important;
    }
}
