///*------------------------------------*\
//    $TRS_NON-RESPONSIVE_LAYOUT
//\*------------------------------------*/
.trs-legacy,
.iframe {
    .info {
        color: #00529b;
        border: 0;
        background-image: url('../images/icons/info_icn.png');
    }

    .success {
        color: #4f8a10;
        background-color: #dff2bf;
        background-image: url('../images/icons/success_icn.png');
    }

    .warning {
        color: #9f6000;
        background-color: #feefb3;
        background-image: url('../images/icons/warning_icn.png');
    }

    .error {
        color: #d8000c;
        background-color: #ffbaba;
        background-image: url('../images/icons/error_icn.png');

        h4 {
            padding: 0;
            margin: 0;
        }

        h5 {
            margin: 2px 0;
        }
    }

    .sponsor {
        color: #666666;
        background-color: #d9d9d9;
        border-color: $box-borders;
        //background-image: url('../images/icons/sponsor_icn.png');
        padding: 15px !important;
    }

    .bluebox {
        color: $black;
        background-color: #99ccff;
        border-color: $box-borders;
    }

    .whitebox {
        color: $black;
        background-color: $white;
        border-color: $box-borders;
    }

    .lightbluebox {
        color: $black;
        background-color: #e8f8fe;
        border-color: #bababa;
        padding-left: 10px;
        padding-right: 45px;
    }

    .info,
    .success,
    .warning,
    .error,
    .sponsor,
    .bluebox,
    .lightbluebox,
    .whitebox {
        font-size: 13px;
        border-width: 1px;
        border-style: solid;
        background-repeat: no-repeat;
        background-position: 10px 8px;
        margin: 1em auto;
        padding: 15px 15px 15px 45px;
        border-radius: 4px;

        // Re-reset the lists
        ul,
        ol {
            margin: initial;
            padding: initial;
            list-style-type: disc;
            margin-left: 1.2em;
        }

        &.mini {
            font-size: 12px;
            margin: .4em;
            padding: 4px 16px 4px 40px;
            background-position: 10px 0;
            border-radius: 2px;
        }

        * {
            font-size: 13px;
            line-height: 1.3;
        }
    }

    // Re-reset the lists
    ul,
    ol {
        margin: initial;
        padding: initial;
        list-style-type: disc;
        margin-left: 1.2em;
    }

    .no-list,
    .bullet-list,
    .text-list,
    .number-list,
    .alpha-list {
        list-style-type: none;
        margin: 0 0 1em;

        li {
            margin-bottom: .25em;
            margin-left: 35px;
        }
    }

    .number-list {
        list-style: decimal outside;

        ul:not(.bullet-list) {
            list-style-type: upper-roman;

            ul:not(.bullet-list) {
                list-style-type: lower-roman;
            }
        }
    }

    .alpha-list {
        list-style-type: upper-alpha;

        ul:not(.bullet-list) {
            list-style-type: lower-alpha;
        }
    }

    .info-text ul {
        margin: initial;
        padding: initial;
        list-style-type: disc;
        margin-left: 1.2em;
    }

    .warning_mini2 {
        width: 15%;
        text-align: center;
        font-size: 11px;
        border: 0 solid;
        background-repeat: no-repeat;
        background-position: 5px 0;
        margin: 0;
        padding: 4px 2px 5px 33px;
        border-radius: 7px;
        color: #9F6000;
        background-image: url('../images/icons/warning_icn.png');
    }
}
