.tabs {
	overflow: hidden;

	ul {
		display: table;
		width: 100%;
	}
	
	li {
		display: table-cell;
		text-align: center;
		border-right: 1px solid $gray-light-3;

		&:last-child {
			border-right: 0;
		}
	}

	a {
		display: block;
		padding: $pad-half;
		background: $gray;

		&:hover, &:focus {
			background: $gray-light-3;
		}

		&.active {
			background: $gray-dark;
			color: $white;
		}
	}
}

.tabs--red-underline {
	list-style: none;
}
.tabs--red-underline li {
	display: inline;
	margin-right: 15px;
}
.tabs--red-underline li a {
	text-decoration: none;
	color:$secondary-text;
	font-weight: normal;
}

.tabs-red-underline li.active a:hover {
	background:$white;

}
.tabs--red-underline li.active {
	border-bottom: 3px solid $ta-brand-red;
}
.tabs--red-underline li.active a {
centerolor: $primary-text;
}

/*----------------------------------*\
    #WELL TAB MENU - dashboard
\*----------------------------------*/

.well-tabs {
	list-style: none;
	width: 100%;
}
.well-tabs li {
	display: inline;
	padding:14px 0px 14px 0px;
	margin-right: 15px;
}
.well-tabs li a {
	text-decoration: none;
	color: #4a4a4a;
	font-size: 14px;
	font-weight: normal;
}

.well-tabs li.active a:hover {
	background: #f9f9f9;

}
.well-tabs li.active {
	border-bottom: 3px solid #e22739;
	padding: 15px 0px 10px 0px;
}
.well-tabs li.active a {
	color: #000;
	font-weight: 600;
}
.tab-2 {
	display: none;
}
