//* Icon Font */
@font-face {
    font-family: 'icons';
    src: url('fonts/icons.eot');
    src: url('fonts/icons.eot?#iefix') format('embedded-opentype'), url('fonts/icons.woff') format('woff'), url('fonts/icons.ttf') format('truetype'), url('fonts/icons.svg#icons') format('svg');
    font-weight: normal;
    font-style: normal;
}

//* Use the following CSS code if you want to use data attributes for inserting your icons */
[data-icon]::before {
    font-family: 'icons';
    content: attr(data-icon);
    speak: none;
    font-weight: normal;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
}

.icon-twitter::before,
.icon-stumbleupon::before,
.icon-pinterest::before,
.icon-linkedin::before,
.icon-google-plus::before,
.icon-search::before,
.icon-play::before,
.icon-menu::before,
.icon-arrow-left::before,
.icon-arrow-right::before,
.icon-bubble::before,
.icon-facebook::before,
.icon-feed::before,
.icon-youtube::before,
.icon-tag::before,
.icon-tumblr::before,
.icon-instagram,
.icon-podcast,
.icon-apple,
.icon-android,
.icon-arrow::after,
.icon-envelope::before {
    font-family: 'icons';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
}

.icon-twitter::before {
    content: '\74';
}

.icon-stumbleupon::before {
    content: '\75';
}

.icon-pinterest::before {
    content: '\70';
}

.icon-linkedin::before {
    content: '\69';
}

.icon-google-plus::before {
    content: '\67';
}

.icon-search::before {
    content: '\73';
}

.icon-play::before {
    content: '\61';
    font-size: .7rem;
    padding-left: .2em;
}

.icon-menu::before {
    content: '\21';
}

.icon-arrow-left::before {
    content: '\23';
}

.icon-arrow-right::before {
    content: '\24';
}

.icon-bubble::before {
    content: '\25';
}

.icon-facebook::before {
    content: '\66';
}

.icon-feed::before {
    content: '\27';
}

.icon-youtube::before {
    content: '\79';
}

.icon-tag::before {
    content: '\28';
}

.icon-tumblr::before {
    content: '\6d';
}

.icon-instagram::before {
    content: '\22';
}

.icon-podcast::before {
    content: '\26';
}

.icon-android::before {
    content: '\29';
}

.icon-apple::before {
    content: '\2a';
}

.icon-envelope::before {
    content: '\2b';
}

.icon-arrow::after {
    content: '\61';
    display: inline-block;
    transform: rotate(90deg);
}

.icon-play-box {
    display: block;
    margin-left: $pad-double;
}

.icon-play-box::before {
    padding: $pad-quarter;
    background: $gray;
    color: $white;
    margin-left: -1.7rem;
    margin-right: $pad-half;
}

//* temp place for now jcottone ---  graph legend icons */
.box-sm {
    width: 12px;
    height: 12px;
    margin-right: 10px;
    display: inline-block;

    @media screen and (max-width: $mobile) {
        + .label--block {
            margin-left: 20px;
            margin-top: -20px;
        }
    }
}

.box-md {
    width: 15px;
    height: 15px;
    margin-right: 10px;
    display: inline-block;

    @media screen and (max-width: $mobile) {
        + .label--block {
            margin-left: 21px;
            margin-top: -21px;
        }
    }
}

.box-lg {
    width: 18px;
    height: 18px;
    margin-right: 10px;
    display: inline-block;

    @media screen and (max-width: $mobile) {
        + .label--block {
            margin-left: 23px;
            margin-top: -23px;
        }
    }
}

//* alphabetic icons (1), (2), (a), (b), w/e */
.icon-alphabetic {
    display: inline-block;
    color: $white;
    background-color: $black;
    border-radius: 50%;
    width: 1.25em;
    height: 1.25em;
    text-align: center;
    font-size: .9em;
    font-style: normal;
    line-height: 1.25em;
    vertical-align: baseline;
}

//* circles sized relative to font for link use */
.trs-icon-circle {
    display: inline-block;
    position: relative;
    color: #a0a0a0;
    font-size: 1em;
    line-height: 100%;
    width: 2em;
    height: 2em;
    border: 1px solid #cbcbcb;
    border-radius: 1.75em;
}

.trs-icon-circle [class^='trs-icons-'],
.trs-icon-circle [class*=' trs-icons-'] {
    position: absolute;
    left: .45em;
    top: .45em;
}

.trs-icon-circle:hover,
.trs-icon-circle:focus {
    background-color: #ececec;
    border-color: #979797;
}

.glyphicon--smooth {
    -webkit-font-smoothing: subpixel-antialiasing;
    -webkit-text-stroke: .5px;
}

.glyphicon--super-circle {
    font-size: 60px;
    padding: 20px;
    border: 1px solid;
    border-radius: 50%;

    &.success {
        border-color: $valid;
    }
}

