//*------------------------------------*\
//    $PRINT
//      - this is being imported by style.scss inside a @media print
//      - we also have a separate print.scss that compiles to print.css with *only* print styles for use with a 'link rel=stylesheet'
//\*-----------------------------------*/
body {
    font-family: Arial, Helvetica, sans-serif;
}

.print-page-header {
    display: block;
    margin: 0 auto;
    padding: 25px;
    width: 980px;
}

.trs-header {
    border-bottom: 0;
    height: 100px;
}

.top-bar {
    display: none;
}

// Remove links from printed pages
a[href]::after {
    content: '';
}

a,
a:active,
a:hover {
    border: 0;
    outline: 0;
}

// Hide generic elements
#message-notif-unread-panel,
#sidebar,
.hidden, // yes, this one too
.no-print,
.noprint,
.chat-button,
.breadcrumbs,
.btn--menu,
.btn-logout,
.trs-footer__social,
.trs-footer__policies,
.breadcrumb__switch,
.subnavbar {
    display: none !important; // needs to be !important to override global classes
}

// Fixes margins/paddings
.trs-footer__footnotes {
    margin-top: 30px;
}

.breadcrumbs {
    padding-left: 0;
    padding-right: 0;
}

.breadcrumb__acct-name {
    margin-left: 0;
}

.footer-rule {
    border: 0;
    padding: 0;
}

// Fixes backgrounds
.btn,
.btn-primary,
.btn-secondary {
    background: $white !important;
    border: 1px solid $btn-secondary-border;
}

// Fixes borders
.left-content-border.right_panel {
    border: 0;
}

// Try to avoid page breaks
table table,
p {
    page-break-inside: avoid;
}

//------------------------------------------
// Specific - Responsive pages
//------------------------------------------
.layout-responsive,
[data-ui-compat=responsive] {
    //
}

//------------------------------------------
// Specific - Non-responsive pages
//------------------------------------------
.layout-non-responsive,
[data-ui-compat=non-responsive] {
    //
}

.trs-footer__phone .btn-secondary {
    display: block;
    width: 190px;
    position: relative;
    margin: 0 auto;
}

