//* other stuff */
.visible-xs {
    display: none !important;
}

.hidden-xs {
    display: block !important;

    &.hidden-print {
        display: none !important;
    }
}

table.hidden-xs {
    display: table;
}

tr.hidden-xs {
    display: table-row !important;
}

th.hidden-xs,
td.hidden-xs {
    display: table-cell !important;
}

.hidden-sm {
    display: none !important;
}

.visible-sm {
    display: block !important;
}

table.visible-sm {
    display: table;
}

tr.visible-sm {
    display: table-row !important;
}

th.visible-sm,
td.visible-sm {
    display: table-cell !important;
}
