/*----------------------------------*\
    #NON-RESPONSIVE NAV
\*----------------------------------*/

@media only screen
    and (min-width: 768px)
    and (max-width: 1024px ) {

    .sidebar-offcanvas {
        position: absolute;
        top: 120px;
        left: -245px;
        width: 245px;
        border-right: 1px solid $gray-light-3;
    }

    .sidebar-offcanvas.slid {
        top: 120px;
        left: 0;
    }

    .desktop {
        &.menu-is-open {
            position: fixed;
            width: 100%;
        }

        #mobile-menu {
            display: block !important;
            width: 15%;
        }

        #cobrand-logo {
            display: block;
            width: 29%;
        }

        #header-trs-links {
            display: block !important;
            float: left;
            width: 39%;

            .link.link--style1__larrow { display: none; }
        }

        #logoutContainer {
            display: block;
            width: auto;
        }
    }
}

//* marquee animation */
@keyframes marquee {
    0% { transform: translateX(100%); }
    100% { transform: translateX(-100%); }
}

