//* Tablet mode */
@media only screen
    and (min-width: 768px)
    and (max-width: 975px) {

    .trs {
        .trs-header,
        #message-notif-unread-panel {
            max-width: 100%;
            margin: 0;
        }

        #mobile-menu {
            display: block !important;
            width: 33.33%;
        }

        #cobrand-logo {
            display: block;
            width: 33.33%;
        }

        #header-trs-links {
            display: none !important;
        }

        #logoutContainer {
            display: block;
            width: 33.33%;
        }
    }

