/* swipable container */
@media screen and (max-width: 1023px) {
    .layout-non-responsive,
    [data-ui-compat='non-responsive'] {
        .non-responsive-swipe {
            min-width: 320px;
            margin: 0;
            overflow-y: auto;
            overflow-x: auto;
            -webkit-overflow-scrolling: touch;

            > .right_panel {
                min-width: 975px;
            }
        }
    }
}
