//* links */
a {
    color: $link-text;
    text-decoration: none;
    outline: 0;

    &:hover,
    &:focus {
        color: $link-text;
    }

    &.link--style1,
    &.trs-text-link,
    &.trs-text-link span {
        color: $link-text;
    }

    &.link--style1__larrow,
    &.link--style1__rarrow {
        position: relative;
        display: inline-block;
        color: $link-text;
        font: normal $font-base $font-pri-bold;
        line-height: 22px;
        text-decoration: none !important;
        outline: 0;

        &:hover span {
            text-decoration: underline;
        }
    }

    &.link--style1__rarrow {
        padding-right: 25px;

        &::after {
            content: '\45';
            position: absolute;
            top: 0;
            right: 3px;
            display: inline-block;
            font-family: 'trs-icons';
            font-style: normal;
            //font-weight: 600;
            line-height: 24px;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }
    }

    &.link--style1__larrow {
        padding-left: 25px;

        &::before {
            content: '\46';
            position: absolute;
            top: 0;
            left: 3px;
            display: inline-block;
            font-family: 'trs-icons';
            font-style: normal;
            //font-weight: 600;
            line-height: 24px;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }
    }

    &.dropdown-toggle {
        text-decoration: none;
    }

    &.link--inline-link {
        color: $link-text;
        font: 1em / 1.2em $font-pri-reg;

        &:hover,
        &:focus {
            color: $link-text !important;
        }
    }

    &.link--style2 {
        color: $link-text;
        font-size: $font-base;
        font-weight: bold;
    }

    &.trs-text-link:hover {
        text-decoration: none;

        span {
            text-decoration: underline;

            &.glyphicon {
                text-decoration: none;
            }
        }
    }

    &.link--disclaimer span {
        color: #3e7cab;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
    }

    &.textonlybutton {
        cursor: pointer;
    }
}

.btn__cancel-action {
    display: inline-block;
    line-height: 1;
    font-weight: bold;
    text-transform: capitalize;
    padding: 10px 0;
}

//* Mobile Links */
.mobile .link--style1__rarrow {
    font-size: $font-base;
}

// TRS modal specific
.modal .list-dotted {
    // links with blue arrows
    .link--style1__rarrow,
    .link--style1__larrow {
        overflow: hidden;
        text-overflow: ellipsis;
    }
}

// color overrides
.link--blue {
    color: $link-text;
}

.link--black {
    color: $black;

    &:hover,
    &:focus,
    &:active {
        color: $black;
        text-decoration: none;
    }
}
