.inline-list {
    li {
        display: inline-block;
    }
}

//* Social List */
.social-list {
    li {
        margin: 0 .4rem $space 0;
    }

    a {
        font-size: 22px;
    }
}


//* Headline List */
.headline-list {
    margin-bottom: $space;

    &.flush {
        margin: 0;
    }

    h4 {
        font-weight: normal;
    }

    li {
        padding: $pad-quarter 0;
        border-top: 1px solid $gray-light-3;
    }
}

//* Post List */
.post-list {
    li {
        margin-bottom: $space;
    }
}

//* Bullet List */
.bullet-list {
    list-style-type: none;
    margin: 0 0 1em;
    line-height: 1.3;

    li {
        margin-bottom: .25em;
        margin-left: 35px; // visually, it looks like ~20px

        &::before {
            position: relative;
            bottom: .25em;
            display: inline-block;
            content: '';
            background-color: $link-text;
            width: 5px;
            height: 5px;
            margin: 0 10px 0 -15px;
            border-radius: 2px;
        }
    }

    &.bullet-list--collapse li {
        margin-bottom: 0;
    }

    &.bullet-list--regular-margin li {
        margin-bottom: 15px;
    }

    &.bullet-list--red li::before,
    li.bullet-list--red::before {
        background-color: $ta-brand-red;
    }

    &.bullet-list--ta-blue li::before,
    li.bullet-list--ta-blue::before {
        background-color: $ta-brand-blue;
    }

    &.bullet-list--caution li::before,
    li.bullet-list--caution::before {
        background-color: $caution;
    }

    &.bullet-list--graph-6 li::before,
    li.bullet-list--graph-6::before {
        background-color: $graph-color6;
    }

    &.bullet-list--graph-9 li::before,
    li.bullet-list--graph-9::before {
        background-color: $graph-color9;
    }

    &.bullet-list--graph-13 li::before,
    li.bullet-list--graph-13::before {
        background-color: $graph-color13;
    }

    &.bullet-list--black li::before,
    li.bullet-list--black::before {
        background-color: $black;
    }
}

//* Text List */
.text-list {
    margin: 0 0 1em;
    line-height: 1.3;

    li {
        margin-bottom: .25em;
        margin-left: 35px; // visually, it looks like ~ 20px
    }
}

//* Number List */
.number-list {
    list-style: decimal outside;
    margin: 0 0 1em;
    line-height: 1.3;

    li {
        // margin-bottom: $space;
        margin-bottom: .25em;
        margin-left: 35px; // visually, it looks like ~ 20px
    }

    ul:not(.bullet-list) {
        list-style-type: upper-roman;

        ul:not(.bullet-list) {
            list-style-type: lower-roman;
        }
    }
}

//* Alpha List */
.alpha-list {
    list-style-type: upper-alpha;
    margin: 0 0 1em;
    line-height: 1.3;

    li {
        margin-bottom: .25em;
        margin-left: 35px; // visually, it looks like ~ 20px
    }

    ul:not(.bullet-list) {
        list-style-type: lower-alpha;
    }
}

//* dotted lists, used mainly to separate links */
@mixin list-custom($border-top-down, $border-left-right: none) {
    $offset: 7.5px;
    border-top: $border-top-down;
    border-bottom: $border-top-down;
    border-left: $border-left-right;
    border-right: $border-left-right;

    li {
        border-bottom: $border-top-down;
        margin: $offset 0;
        padding-bottom: $offset;

        &:last-child {
            border: 0;
            padding-bottom: 0;
        }

        @if $border-left-right != none {
            padding-left: $offset;
            padding-right: $offset;
        }
    }
}

.list-dotted {
    @include list-custom(1px dotted $gray-light-1);
}

.list-box {
    @include list-custom(1px solid $gray-light-1, 1px solid $gray-light-1);
}

// no bullets!
.no-bullet {
    list-style: none;

    li {
        list-style: none;
    }
}

.list--glyphed-bullets {
    border: 1px $gray-light-3 solid;
    border-width: 0 1px;
    margin-bottom: -1px;

    li {
        border: 1px $gray-light-3 solid;
        border-width: 1px 0;
        padding: 15px;
        margin-bottom: -1px;
    }

    .glyphicon,
    .icon {
        margin-right: 8px;
    }
}

.list--circles {
    text-align: justify;
    min-width: 100%;

    &::after {
        content: '';
        display: inline-block;
        width: 100%;
    }

    li {
        list-style: none;
        display: inline-block;
        background-color: $aqua-bright;
        width: 150px;
        height: 150px;
        padding: 15px;
        border-radius: 50%;
        text-align: center;
        overflow-y: hidden;

        div {
            margin: 15%;
        }

        p {
            min-height: 66px;
        }

        .list--circles__icon {
            font-size: 35px;
            display: block;
            position: relative;
            top: -10px;
        }
    }

    @media all and (max-width: 360px) {
        li {
            width: 130px;
            height: 130px;

            .list--circles__icon {
                font-size: 30px;
            }

            p {
                font-size: 12px;
            }
        }
    }

    @media all and (max-width: $tablet - 1) {
        text-align: center;
    }
}

// ordered list, vertical circles (variations)
.list__vertical-circles {
    counter-reset: listNumbering;

    // variations
    &.list__vertical-circles--decimal {
        li::before {
            content: counter(listNumbering, decimal);
        }
    }

    &.list__vertical-circles--alpha {
        li::before {
            content: counter(listNumbering, lower-alpha);
        }
    }

    &.list__vertical-circles--roman {
        li::before {
            content: counter(listNumbering, upper-roman);
        }
    }

    // common
    li {
        counter-increment: listNumbering;
        margin-bottom: 15px;

        &::before {
            display: inline-block;
            width: 25px;
            height: 25px;
            font-weight: bold;
            text-align: center;
            border: 2px $black solid;
            border-radius: 50%;
            margin-right: 5px;
        }
    }

    .list__item {
        display: inline-block;
        vertical-align: top;

        @media screen and (min-width: $tablet) {
            max-width: 665px;
        }

        @media screen and (max-width: $tablet - 1) {
            max-width: 87.5%;
        }
    }
}

// forecast icons
.forecast-icons {
    display: block;
    margin: 0 auto;
    width: 100%;
    text-align: center;
    overflow: hidden;

    li {
        display: inline-block;
        vertical-align: middle;
        min-width: 25%;
        max-width: 25%;
        text-align: center;
        padding: 0 2.5%;
        border-left: 1px $gray-light-3 solid;

        img {
            display: inline-block;
            max-width: 100%;
            height: auto;
        }

        span {
            display: block;
            font-weight: 600;
            margin: 5px -15% 0; // this and the background below 'cuts out' the line separators
            background: $white;

            @media screen and (max-width: $tablet - 1) {
                font-size: $font-xxxsmall; // 10px
                font-weight: 700;
                background: transparent; // it was $gray-light;
            }
        }

        &:first-child {
            border: 0;
        }
    }

    // vertical list, horizontally centered
    &.forecast-icons__vertical {
        li {
            display: block;
            min-width: auto;
            margin: 0 auto 15px;
            border: 0;
        }
    }

    // no text below icons
    &.forecast-icons__notext {
        li span {
            display: none;
        }
    }
}

.enumeration {
    .enum {
        &::after { content: ', '; }
        &:last-child {
            &::after { content: ''; }
            &:not(:first-child)::before { content: ' and '; }
        }

        &:first-child:nth-last-child(2)::after { content: ''; }
    }
}

// Ja'Red's step progress
.step-progress {
    display: block;
    list-style: none;
    height: 12px;
    line-height: 1;
    background: $gray-light-7;
    border-radius: 6px;
    margin-top: 25px; // so the component won't overlap. adjust this if you move the text further up.

    // default styles
    .step-progress__step {
        position: relative;
        display: inline-block;
        height: 12px;
        text-align: center;

        .text {
            display: inline-block;
            position: relative;
            top: -25px; // text position above the grey bar
            font-weight: 700;
            white-space: nowrap;
        }

        // our bullet
        &::after {
            content: '';
            display: block;
            position: absolute;
            top: -4px;
            left: 50%;
            margin-left: -10.5px;
            width: 21px;
            height: 21px;
            border-radius: 50%;
            background: $ta-brand-blue;
            border: 3px $gray-light-11 solid;
        }

        // first step
        &:first-child {
            text-align: left;

            &::after {
                left: 0;
                margin: 0;
            }
        }

        // last step
        &:last-child {
            text-align: right;

            &::after {
                left: auto;
                right: 0;
                margin: 0;
            }
        }
    }

    // active step
    .step-progress__step--active::after {
        background: $ta-brand-blue;
    }

    // inactive step
    .step-progress__step--inactive::after {
        background: $gray-dark-8;
    }

    // 4 steps
    &.step-progress__4step .step-progress__step {
        width: 25%;

        &:nth-child(2) {
            &::after {
                left: 33%;
            }

            .text {
                left: -23%;
            }
        }

        &:nth-child(3) {
            &::after {
                left: 66%;
            }

            .text {
                left: 15%;
            }
        }
    }
}
