//*------------------------------------*\
//    $MESSAGING
//\*------------------------------------*/
.alert {
    font-family: $font;
    background-image: none;
    padding: $pad;
    border-width: 1px;
    border-style: solid;
    margin-bottom: $space-half;
}

.alert-error {
    color: $error;
    background-color: #fbdfe2;
    border-color: $error;
}

.alert-success {
    color: #2b8456; // more contrast. it was $graph-color5
    background-color: #def7ea;
    border-color: $graph-color5;
}

.alert-caution {
    color: $caution;
    background-color: #fefcdd;
    border-color: $caution;
}

.alert-info {
    color: $graph-color2;
    background-color: #dcf4fa;
    border-color: $graph-color2;
}

.alert,
.alert p {
    font-size: $font-small;

    &:last-child {
        margin-bottom: 0;
    }
}

.alert-dismissable .close,
.alert-dismissible .close {
    right: 0;
    font-size: $font-large;
}

.alert-icon {
    float: left;
    width: 30px;
    text-align: center;
    margin: 0 auto;
}

.alert-icon-size {
    font-size: 18px; //24px;
}

.glyph-white {
    background: $white;
    border-radius: 24px;
}

.alert-message {
    margin-left: 36px;
}

.alert ul,
form .alert ul {
    list-style-type: disc;
    margin-left: 20px;
}

//*------------------------------------*\
//    $POPOVERS
//\*------------------------------------*/
//* overrides */
a[data-toggle="tooltip"],
a[data-toggle="popover"] {
    white-space: nowrap; //* prevents nasty tooltip behaviors */
}

//*undoing damage by another global style */
.popover.right,
.popover.left,
.popover.top,
.popover.bottom {
    float: none;
    padding: 0;
}
