.row-margins {
    margin-left: 15px;
    margin-right: 15px;
}

.breadcrumb > li+li::before {
    content: none;
}

.breadcrumb-icon {
    font-size: $font-breadcrumb-icon;
    margin-right: 5px;
    line-height: 1em;
    text-decoration: none;
    overflow: hidden;
    display: inline-block;
    vertical-align: middle;
}

//* Modals */
.modal-backdrop.in {
    filter: alpha(opacity = 60);
    opacity: .6;
}

.modal-content {
    border-radius: 0;
}

.modal-header {
    border: 0;

    .close {
        font-size: $font-base;
        filter: alpha(opacity = 80);
        opacity: .8;
    }
}

.modal-footer {
    border-top: 0;

    .btn {
        margin: 0;
    }
}

.modal-xmd {
    @media all and (max-width: $mobile) {
        width: auto;
    }

    @media all and (min-width: $mobile + 1) and (max-width: $tablet) {
        width: 95.06%;
    }

    @media all and (min-width: $tablet + 1) {
        width: 730px;
    }
}

.modal-md {
    @media all and (max-width: $mobile) {
        width: auto;
    }

    @media all and (min-width: $mobile + 1) and (max-width: $tablet) {
        width: 450px;
    }

    @media all and (min-width: $tablet + 1) {
        width: 450px;
    }
}

.tablet {
    .modal-lg.agent-find {
        min-width: 705px;
    }
}

@media screen and (min-width: $desktop) {
    .modal-lg.agent-find {
        width: 983px !important;
    }

    .sm-gutter {
        padding-right: 15px;
    }
}

.sm-gutter {
    padding-right: 15px;
}

// modal fix for iOS - there's some JS as well so the page stays in its original position
.modal-open {
    width: 100%;
}

// navbar icon
.navbar-toggle .icon-bar {
    display: block;
    width: 20px;
    height: 2px;
    border: 1px solid #888;
    border-radius: 1px;
}

///*-----------------------------------*\
///  --Begin Post Login specific --
//\*-----------------------------------*/
.table > thead > tr > th {
    border-bottom: 0;
}

///*-----------------------------------*\
//  --Begin TRS specific Media-Query Footer section--
//\*-----------------------------------*/
@media screen and (max-width: $max-width) {
    .footer-theme--col-border + .footer-theme--col-border {
        border-right: 0;
    }

    .footer-theme__footnotes {
        margin-bottom: 55px;
    }
}

@media screen and (max-width: $max-width-tablet) {
    .footer-theme__subhead {
        margin-top: 35px;
    }

    .footer-theme--col-border {
        border-right: 0;
        margin-bottom: 0;
    }

    .footer-theme__footnotes {
        border-top: 1px solid $gray-dark-65;
        padding: 30px 0;
        margin: 30px 0 0;
    }
}

///*-----------------------------------*\
//  --Begin DataTable specific styles--
//\*-----------------------------------*/
.dataTable {
    > tbody > .child ul li {
        display: block !important;
    }

    &.dtr-inline.collapsed > tbody > tr > td:first-child::before,
    &.dtr-inline.collapsed > tbody > tr > th:first-child::before {
        left: 347px !important;
        height: 21.9px !important;
        width: 22px;
        border: 0 !important;
        background-color: #407399 !important;
        line-height: 19px !important;
    }

    &.dtr-inline.collapsed > tbody > .parent > td:first-child::before,
    &.dtr-inline.collapsed > tbody > .parent > th:first-child::before {
        background-color: #407399 !important;
    }

    thead {
        th {
            border-bottom-width: 2px;
            border-bottom: 2px $link-text solid !important;
        }

        .sorting::after,
        .sorting_asc::after,
        .sorting_desc::after,
        .sorting_asc_disabled::after,
        .sorting_desc_disabled::after {
            display: none !important;
        }
    }

    &.no-footer {
        border-bottom: 0 solid !important;
    }
}

.dtr-data {
    padding-left: 151px;
    float: right;
}

.table-bordered > thead > tr > th {
    border: 0 solid #ddd;
}

.dataTables_wrapper {
    .dataTables_paginate {
        color: $link-text !important;
        border: 0 !important;
        background: none !important;

        .paginate_button {
            padding: 0 !important;
            color: $link-text !important;

            &.current,
            &.current:hover {
                color: $black !important;
                font-weight: 700;
                border: 0 !important;
                background: none !important;
            }

            :hover {
                color: #c0c0c0 !important;
                border: 0 !important;
                background: none !important;
                font-weight: 700;
            }
        }
    }
}

///* ----------------------------------*\
///  -- Inputs & Input Addons
//\* ----------------------------------*/
.input-group .form-control:first-child,
.input-group-addon:first-child,
.input-group-btn:first-child > .btn,
.input-group-btn:first-child > .btn-group > .btn,
.input-group-btn:first-child > .dropdown-toggle,
.input-group-btn:last-child > .btn-group:not(:last-child) > .btn,
.input-group-btn:last-child > .btn:not(:last-child):not(.dropdown-toggle) {
    border-right: 0;
}

.input-group-btn:last-child > .btn,
.input-group-btn:last-child > .btn-group {
    margin-left: 1px;
}

.input-group-addon {
    @include transition(border-color, .3s, linear);
    font-size: $font-large;
    color: $prim-heading-icon;
    background-color: transparent;
    border-color: $gray-light-3;
    display: table-cell !important;

    input {
        background-color: $white;
    }

    &:last-child {
        background-color: $white;
    }
}

///* ----------------------------------*\
///  -- Responsive Images
//\* ----------------------------------*/
.img-responsive {
    font-size: 0;

    > img {
        display: block;
        max-width: 100%;
        height: auto;
        font-size: 0;
    }
}

///* ----------------------------------*\
///  -- Tooltip
//\* ----------------------------------*/
///* overrides */
[data-toggle="tooltip"] {
    white-space: nowrap;

    > .glyphicon-question-sign {
        top: 0;
        font-size: 1.2em; //16px;
        color: $black;
        background: $white;
        border-radius: 50%;
        vertical-align: middle;
    }
}

.tooltip.top .tooltip-arrow {
    border-top-color: $gray-med-2;
}

.tooltip.bottom .tooltip-arrow {
    border-bottom-color: $gray-med-2;
}

.tooltip.left .tooltip-arrow {
    border-left-color: $gray-med-2;
}

.tooltip.right .tooltip-arrow {
    border-right-color: $gray-med-2;
}

.tooltip-inner {
    background-color: $gray-med-2;
    padding: 5px 10px;
    text-align: left;
    font-size: 13px;
    font-weight: 400;
    line-height: 1.2;
    max-width: 250px;
}

.tooltip-link {
    text-decoration: none;
    border-bottom: 1px dotted $ta-brand-blue;
}

.tooltip-link:hover,
.tooltip-link:focus,
.tooltip-link:active {
    text-decoration: none;
    border-bottom-style: solid;
}

///* ----------------------------------*\
///  -- Panels
//\* ----------------------------------*/
.panel {
    margin-bottom: 15px;

    // panel with nav-tabs
    &.with-nav-tabs {
        .panel-heading {
            padding: 5px 5px 0;
        }

        .nav-tabs {
            border-bottom: 0;
        }

        .nav-justified {
            margin-bottom: -1px;
        }
    }

    // panel well
    &.panel-well {
        background-color: $gray-light;
        border: 1px solid $gray-light-3;
        margin-left: 0;
        margin-right: 0;
    }

    // smart phone
    @media screen and (max-width: $tablet - 1) {
        margin-left: -15px;
        margin-right: -15px;
        border-width: 0;
        border-radius: 0;
        background-color: $gray-light;

        .panel-heading {
            border: 0;
            padding-bottom: 0;
        }

        fieldset .row {
            border: 0;
            margin-bottom: 0;
        }

        &.panel-well {
            margin-left: 0;
            margin-right: 0;
            background-color: $gray-light;
            border: 1px solid $gray-light-3;
        }
    }
}

.panel-default > .panel-heading {
    background: $gray-light;
}

///* ----------------------------------*\
///  -- Alerts
//\* ----------------------------------*/
.alert {
    margin-bottom: 15px;
}

///* ----------------------------------*\
///  - bootstrap-datepicker plugin
//\* ----------------------------------*/
.desktop .input-group.date input[type=text].form-control,
.desktop .input-group.date input[type=date].form-control {
    width: 125px; // because of feedback icon on the left
}

.input-group.date {
    min-width: 160px;

    input[type=text].form-control,
    input[type=date].form-control {
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
        text-align: center;
    }

    input:focus,
    input:active {
        + .input-group-addon {
            border-color: $black;
        }
    }

    .extra-label {
        display: none;
    }

    .input-group-addon {
        background-color: $white;
        font-weight: normal;
        border: 1px solid $gray-light-3;
        border-left: 0;
        text-shadow: none;
        border-radius: 4px;
        width: 35px;

        &:last-child {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
    }

    input[disabled] + .input-group-addon {
        background: $gray-light-1;
    }

    // we're not using icons for feedback anymore.
    // &.has-feedback {
    //     input[type=text].form-control,
    //     input[type=date].form-control {
    //         padding-right: 0;
    //     }
    // }
}

// date range
.input-daterange {
    width: auto;
    margin: 30px 0 15px; // because the extra labels
    display: inline-block;

    .input-group.date {
        .extra-label {
            display: block;
            position: absolute;
            top: -20px;
            left: 0;
        }
    }
}

@media screen and (max-width: $tablet) {
    .input-daterange {
        .input-group.date {
            display: inline-table;

            + .input-group-addon {
                display: inline-block !important;
                margin-top: 8px;
                vertical-align: top;
            }
        }
    }
}

.datepicker {
    background: $gray-light-3;
    color: $gray;
    border-radius: 0;
    margin-top: 5px;
    box-shadow: none;
    border: 0;

    &.datepicker-dropdown {
        padding: 10px;

        &::after,
        &::before {
            display: none;
        }
    }

    .prev,
    .next,
    .dow,
    .datepicker-switch {
        height: 30px;
        padding: 0;

        &:hover {
            background: $gray-light-3;
        }
    }

    .prev,
    .next {
        font-size: 0;

        &::before {
            font-family: 'Glyphicons Halflings';
            font-size: 16px;
            font-weight: 400;
        }
    }

    .prev::before {
        content: '\e079';
    }

    .next::before {
        content: '\e080';
    }

    .datepicker-switch {
        font-weight: 600;
        font-size: $font-h4;
    }

    .dow {
        font-size: $font-xsmall;
    }

    table {
        border: 0;

        tfoot {
            display: none;
        }

        tr {
            th {
                font-weight: 400;
                background: $gray-light-3;
            }

            th,
            td {
                border-radius: 0;
                width: 44px;
                height: 44px;
            }

            td {
                &.day {
                    background: $white;
                    border: 1px solid $gray-light-3;
                }

                &.old,
                &.new {
                    background: $gray-light-3;
                }

                &.today {
                    color: $gray !important;
                    background: $gray-light-5 !important;
                    border-color: $gray-light-3 !important;

                    &:hover {
                        background: $gray-light-2 !important;
                    }
                }

                &:hover,
                &.focus,
                &:focus,
                &.active,
                &:active {
                    background: $gray-light-2 !important;
                    border-color: $gray-light-3 !important;
                    color: $gray !important;
                }

                &.disabled {
                    background: $gray-light-3 !important;
                }

                &.focused {
                    background: $yellow-tooltip !important;
                }
            }
        }
    }

    .datepicker-months,
    .datepicker-years {
        table {
            tr {
                td {
                    &:hover,
                    &.focus,
                    &:focus,
                    &.active,
                    &:active {
                        background: $gray-light-3 !important;
                    }

                    span {
                        &:hover,
                        &.focus,
                        &:focus,
                        &.active,
                        &:active {
                            background: $gray-light-2 !important;
                            color: $gray !important;
                        }
                    }
                }
            }
        }
    }

    .datepicker-title {
        display: none !important;
    }
}

///* ----------------------------------*\
///  -- form-group
//\* ----------------------------------*/
.form-group {
    display: block;
    position: relative;
}

.form-inline .form-group {
    display: inline-block;
}

///* ----------------------------------*\
///  -- typeahead.js
//\* ----------------------------------*/
.twitter-typeahead {
    vertical-align: middle;

    .tt-query,
    .tt-hint {
        outline: none;
    }

    .tt-hint {
        color: #999;
    }

    .tt-menu {
        width: 100%;
        margin: 5px 0;
        padding: 0;
        background-color: #f5f5f5;
        border: 1px solid rgba(0, 0, 0, .2);
        border-radius: 4px;
        box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
    }

    .tt-suggestion {
        padding: 9px 12px;

        &.tt-cursor {
            color: $white;
            background-color: #0097cf;
        }

        p {
            margin: 0;
        }
    }

    .tt-suggestion:hover {
        cursor: pointer;
        color: $white;
        background-color: #0097cf;
    }
}

// date filters (activity summary/details)
.layout-responsive,
[data-ui-compat='responsive'] {
    .filters-activity select {
        min-width: 290px;
    }
}

// date filters (activity summary/details) on smartphones
@media all and (min-width: 0) and (max-width: $tablet - 1) {
    .layout-responsive,
    [data-ui-compat='responsive'] {

        .filters-activity {
            width: 290px;
            margin: 0 auto;
        }

        .date-ranges {
            width: 100%;
            float: none;
            margin: 0 auto;

            .form-inline .custom-select-inner select {
                width: 290px;
            }

            input[type=text],
            input[type=date] {
                width: 255px; // 255 + icon (35) = 290
            }
        }
    }
}

// Error and success glyphicons
// Same as Bootstrap .glyphicon-ok* and .glyphicon-remove*
.glyphicon {
    outline: 0;

    &.glyphicon-success::before { content: '\e013'; }
    &.glyphicon-error::before { content: '\e014'; }

    &.glyphicon-success-sign::before { content: '\e084'; }
    &.glyphicon-error-sign::before { content: '\e083'; }
}

//*******************************
// Nav Tabs
//*
// this comes from BS CSS
.nav-tabs {
    &.nav-justified > li {
        float: none;

        @media (min-width: $tablet) {
            display: table-cell;
            width: 1%;
            white-space: nowrap;
        }
    }
}

// smarphones - wrapper for scrollable tabs
.nav-tabs-wrapper {
    // desktop/tablet spacing
    padding-bottom: 10px;
    margin-bottom: 5px;

    // remove scrollbars since we can't position them right, the OS always takes over :/
    -ms-overflow-style: none;

    &::-webkit-scrollbar {
        height: 0; width: 0;
        background: transparent;
    }

    &::-webkit-scrollbar-thumb {
        background: transparent;
    }

    // bottom border for smartphones, use along with .visible-xs class
    + .nav-tabs-mobile-border {
        margin: -1px -15px 0;
        border-top: 1px solid $gray-light-3;
        padding-bottom: 15px;
    }

    // no outline
    .nav-tabs > li > a:focus {
        outline: 0;
    }

    // scrolling on smartphones
    @media screen and (max-width: $tablet - 1) {
        position: relative;

        // smartphone spacing
        padding-bottom: 0;
        margin-bottom: 0;

        // scroll
        overflow: hidden;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch; // smoother movement
        margin-left: -15px;
        margin-right: -15px;

        .nav-tabs {
            margin-left: 15px;
            margin-right: 15px;
            width: 2048px; // start big to avoid FOUC - https://en.wikipedia.org/wiki/Flash_of_unstyled_content - JS is taking care of this

        }
    }
}

// Tab content is a dotted list? remove top and bottom border
.tab-content .tab-pane ul.list-dotted {
    border-top: 0;
    border-bottom: 0;
}

// tablets and up
@media screen and (min-width: $tablet) {
    .nav-tabs {
        width: auto !important;
    }
}

//***********
// Smartphone
//*
@media screen and (max-width: $tablet - 1) {
    .layout-responsive,
    [data-ui-compat=responsive] {
        h3,
        .h3 {
            text-align: center;
        }
    }
}

//*******************************
// list-group-item inside ontrack__form
.ontrack__form fieldset .list-group-item .row {
    padding-bottom: 0;
}
