//*------------------------------------*\
//    $TRS_BUTTONS
//*------------------------------------*/
// Increase specificity to override ddolMain.css
.layout-non-responsive,
[data-ui-compat='non-responsive'],
.trs-legacy {

    .btn {
        opacity: 1;

        &:hover,
        &:focus {
            opacity: 1;
        }
    }

    // fixes strikeouts on disabled buttons
    a.disabled,
    a.disabled:hover {
        text-decoration: none !important;
    }

    // button groups with step dots (Managed Advice, etc)
    .three-buttons,
    .two-buttons {
        position: relative;

        .btn.btn-primary,
        .btn.btn-secondary {
            position: relative;
            z-index: 1;
        }

        .dot-steps {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            text-align: center;

            .ma-ball {
                display: inline-block;
                float: none;
            }
        }
    }

    button {
        font-family: inherit;

        span,
        em {
            padding: 0;
            background: none;
            color: inherit;
            height: auto;
            line-height: inherit;
        }

        .breadcrumb__dropdown__ellipse {
            height: 8px;
        }

        &.close {
            width: auto;
        }
    }

    .breadcrumb__dropdown {
        button,
        .breadcrumb__button {
            font-family: $font;
            font-size: $font-base;
            height: auto;
        }
    }
}

#tabDiv {
    table td span {
        display: block;
        max-height: 24px;
        width: 165px;

        table td:first-child {
            max-width: 5px;
        }
    }
}
