/* Headings */
//*Further Reading: http:/csswizardry.com/2012/02/pragmatic-practical-font-sizing-in-css/ */
h1,
.h1 {
    font-family: $font-sec-light;
    font-size: $font-h1;
    line-height: 1.125em; //line-heights are relative to the font-size attached, not to the base size.
}

h2,
.h2 {
    font-family: $font-pri-semi-bold;
    font-weight: 600;
    font-size: $font-h2;
    line-height: 1.444em;
}

h3,
.h3 {
    font-family: $font;
    font-weight: 700;
    font-size: $font-h3;
    line-height: 1.444em;
}

h4,
.h4 {
    font-family: $font-pri-light;
    font-size: $font-h4;
    line-height: 1.4em;
}

.stand-out {
    margin-top: 2em;
    margin-bottom: 1em;
}

.heading-section {
    margin-top: 25px;
    margin-bottom: 35px;
}

.heading-section__top {
    margin-top: 25px;
}

.main-layout,
#main_content {
    h1 {
        margin-bottom: 30px; // VISUALLY, on Chrome, this looks like 40px on most pages. The Web isn't paper
    }
}

/* Subheadings */
.subheading {
    font-family: $font-secondary;
    font-weight: normal;
}

@media screen and (max-width: $tablet - 1) {
    h1,
    .h1 {
        font-size: $font-h1-mobile;
        line-height: 1.125em;
    }

    h2,
    .h2 {
        font-size: $font-h2-mobile;
        line-height: 1.333em;
    }

    h3,
    .h3 {
        font-size: $font-h3-mobile;
        line-height: 1.5em;
    }

    h4,
    .h4 {
        font-size: $font-h3-mobile;
        line-height: 1.444em;
    }
}

@media screen and (max-width:$mobile - 1) {
    h1,
    .h1 {
        text-align: center;
    }
}

.heading--top-of-content {
    margin: 30px 0;
}

.mobile .heading--top-of-content {
    margin: 15px 0;
}

/* SSO ONLY HEADINGS */

.sso--h2 {
	font-size:18px;
}
@media screen and (max-width:$mobile - 1) {
   .sso-title {
	   text-align:center;
	}
}
