html,
body {
    min-height: 100%;
}

body {
    background-color: $box-borders;
}

// body .sg-main,
// body .sg-pattern {
//     background: $white;
// }

header {
    background-color: $header-bg;
}

/*------------------------------------*\
    $LAYOUT
\*------------------------------------*/
#main_content {
    padding-bottom: 20px;
    padding-top: 20px;
    //overflow-x: hidden;
}

.page-container {
    max-width: 980px;
    background-color: $white;
    margin: 0 auto;
    position: relative;
}

@media screen and (min-width: $tablet) {
    #main_content {
        min-height: 460px;
    }

    .page-container {
        min-height: 960px;
    }
}

//* Layout Container */
.lc {
    max-width: $max-width;
    margin: 0 auto;
    padding: $pad-half;
}

//* LOADER */
.loadingOverlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 10000; // [Leandro]: this is bad for mobile, why so high?
    //*opacity: .9;*/
    background: url('../images/loading-icon.gif') center no-repeat $white;
}
