//*------------------------------------*\
//    $TRS_NON-RESPONSIVE_LAYOUT
//\*------------------------------------*/
.trs-legacy {
    // Legacy pages rely heavily on layout tables (ugh)
    table,
    td {
        border: 0;
    }

    [background*='bk-dashLine.gif'] {
        max-height: 1px;
        background: none;
        border-bottom: dotted 1px $gray-dark-4;
        padding: 0;
        font-size: 0 !important;
    }

    img[src*='box_shad_t.gif'],
    img[src*='box_shad_r.gif'],
    img[src*='box_shad_b.gif'],
    img[src*='box_shad_l.gif'] {
        display: none;
    }

    ///*------------------------------------*\
    //    $DROPSHADOWTABLE_FIX
    //\*------------------------------------*/
    table[bgColor='f3f0f0'],
    table[data-ui-component='dropShadowTable'] {
        background-color: #f3f0f0; // don't touch the hard-coded color
        box-shadow: 3px 3px 3px #ccc;
        margin-bottom: 15px;
        padding: 0;
        border: $gray-light-4 1px solid;
        border-radius: 4px;

        + [data-ui-component='dropShadowTable'] {
            margin-top: 1em;
        }

        [data-ui-component='dropShadowTableContent'] {
            padding: 10px;

            [width='100%'] {
                padding: 2px;
            }
        }

        [data-ui-role='container'] {
            padding: 10px;

            td {
                width: auto;
            }
        }

        [data-ui-role='layout'] {
            display: none;
        }

        hr {
            background-color: #808080;
            color: #808080;
            margin: 15px 0;
            box-shadow: 1px 1px 1px #ccc;
            height: 0;
            border: 0;
            border-top: 1px #f3f0f0 solid;
        }

        [align=right] {
            text-align: right;
        }

        img[src*='bk-dashLine.gif'],
        img[src*='spacer.gif'] {
            display: none;
        }
    }

    td[background*='b-bg_dots_bot.gif'] {
        padding: 0;
    }

    table.calendartbl {
        width: auto;
    }

    table.minMaxTable {
        td.mixMaxcell1 {
            min-width: 1px;
        }

        td.mixMaxcell5 {
            padding: 0 5px;
        }
    }

    table.table-standard {
        border: 1px solid #e0e0e0;
        margin-bottom: 1.4em;
        width:100%;
        border-spacing:0px;
        border-collapse:collapse;
    }
}
