//------------------------------------
//  $FORMS
//------------------------------------

// Namespaced element-based selectors
.layout-responsive,
[data-ui-compat='responsive'] {
    form ol,
    form ul {
        list-style: none;
        margin-left: 0;
    }

    fieldset {
        border: 0;
        padding: 0;
        margin: 0;
    }

    label {
        display: block;
        color: $black; // it was $gray-dark-3
        font-size: $font-base;
        line-height: 125%;
        font-weight: normal;
        padding-bottom: 7px;

        &.FromDatepicker,
        &.ToDatepicker {
            padding-top: 17px;
            padding-left: 0;
            padding-right: 0;
            width: 45px;
            color: $black;
        }

        &.disabled,
        &[disabled] {
            color: $disabled;
            border-color: $gray-light-3 !important;
            transition: none;
            cursor: not-allowed !important;
            opacity: 1;
            -webkit-text-fill-color: $disabled; // Override iOS / Android font color change
            -webkit-opacity: 1; // Override iOS opacity change affecting text & background color
        }
    }

    button,
    input,
    select,
    textarea {
        font-family: inherit;
        margin: 0 1px;
        outline: 0;
    }

    input,
    textarea,
    select,
    .form-control {
        @include transition(border-color, .3s, linear);
        width: 100%;
        height: auto;
        min-height: 40px;
        padding: 7px 12px 8px;
        margin: 0;
        display: block;
        font-size: $font-input;
        color: $input;
        background-color: $white;
        background-image: none;
        border: 1px solid $gray-light-3;
        border-radius: 4px;
        box-shadow: none;
        outline: 0;

        &.age,
        &.currency,
        &.percentage {
            text-align: right;
        }

        &:focus {
            border-color: $black;

            + .input-group-addon {
                border-color: $black;
            }
        }
    }

    input,
    textarea,
    select {
        &[disabled] {
            background: $gray-light-1;
            color: $disabled;
            border-color: $gray-light-3 !important;
            opacity: 1;
            -webkit-text-fill-color: $disabled; // Override iOS / Android font color change
            -webkit-opacity: 1; // Override iOS opacity change affecting text & background color

            + label {
                color: $disabled;
                border-color: $gray-light-3 !important;
                transition: none;
                cursor: not-allowed !important;
                opacity: 1;
                -webkit-text-fill-color: $disabled; // Override iOS / Android font color change
                -webkit-opacity: 1; // Override iOS opacity change affecting text & background color
            }
        }
    }

    input[type=text],
    input[type=search],
    input[type=url],
    input[type=number],
    input[type=date],
    input[type=email],
    input[type=password] {
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;
        appearance: none;

        &::-ms-clear {
            display: none;
        }
    }

    input[type=number] {
        &::-webkit-inner-spin-button,
        &::-webkit-outer-spin-button {
            -webkit-appearance: none;
        }
    }

    input[type=date] {
        text-transform: uppercase;
        max-height: 40px;
        line-height: normal;

        &::-webkit-clear-button { // this is OK
            display: none;
        }

        &::-webkit-inner-spin-button {
            display: none;
        }

        &::-webkit-calendar-picker-indicator {
            display: none;
        }

        &::-ms-clear {
            display: none;
        }
    }

    select {
        min-width: 75px; // needs a min-width because of the customized select
        cursor: pointer;
    }

    option,
    optgroup {
        &:disabled {
            color: $disabled;
        }
    }

    // Inline forms
    .form-inline {
        label,
        input,
        textarea,
        select,
        .form-control {
            display: inline-block;
            vertical-align: middle;
            width: auto;
        }

        .input-group > .form-control {
            width: 100%;
        }

        .custom-select {
            display: inline;
            vertical-align: middle;

            // &.has-feedback {
            //     display: inline-block;
            // }

            .custom-select-inner {
                display: inline;
                vertical-align: top;

                select {
                    //width: auto;
                    max-width: 100%;
                    vertical-align: top;
                }
            }
        }
    }

    .form-control:focus {
        box-shadow: none;
    }

    // CSS-only custom select (graceful degradation IE9 and below)
    // Check more rules on ie.scss file
    // Check the properties above for inline forms
    .custom-select {
        display: block;

        .custom-select-inner {
            position: relative;
            display: block;
            vertical-align: middle;

            select {
                min-height: 41px;
                padding-right: 35px;
                -webkit-appearance: none;
                -moz-appearance: none;
                -ms-appearance: none;
                appearance: none;
                display: inline-block;
                //width: 100%;

                &::-ms-expand {
                    display: none;
                }
            }

            &::after {
                position: absolute;
                top: 1px;
                right: 1px;
                width: 30px;
                height: 38px;
                text-align: center;
                cursor: pointer;
                border-left: 1px solid $gray-light-1;
                padding-top: 14px;
                border-top-right-radius: 4px;
                border-bottom-right-radius: 4px;
                color: $custom-select-arrow;
                content: '\e259';
                background-color: $custom-select-bg;
                font-family: 'Glyphicons Halflings';
                font-style: normal;
                font-weight: 400;
                line-height: 1;
                pointer-events: none;
                -ms-pointer-events: none;
                -webkit-font-smoothing: antialiased;
            }
        }

        &.multiple {
            .custom-select-inner::after {
                display: none;
            }

            select {
                padding-right: 15px;
                border-radius: 0;
            }
        }
    }

    // Radio/Checkbox
    input[type=checkbox],
    input[type=radio] {
        width: auto;
        height: auto;
        min-height: 0;
        margin: 0 8px;
        padding: 0;
        line-height: 1;
        background: none;
    }

    input[type=search] {
        -webkit-appearance: none;
        border-radius: 0;
    }

    input[type=search]::-webkit-search-cancel-button, // this is OK
    input[type=search]::-webkit-search-decoration {
        -webkit-appearance: none;
    }

    //------------------------------------
    //  $CHECKBOXES & $RADIO BUTTONS
    //------------------------------------
    input {
        &[type=checkbox],
        &[type=radio] {
            position: absolute;
            width: 1px !important;
            height: 1px !important;
            padding: 0;
            border: 0;
            outline: 0;
            overflow: hidden;
            clip: rect(1px, 1px, 1px, 1px);

            + label span[role] {
                display: inline-block;
                width: 25px;
                height: 25px;
                vertical-align: middle;
                margin: -1px 8px 0 0;
                border: 0;
                outline: 0;
                cursor: pointer;
                background: url('../images/radio-checkbox-22x22-16x16.png') no-repeat 0 0;
            }

            + label[right] span[role],
            + label[rightie] span[role] {
                margin: -2px 0 0 8px;
            }
        }

        // check box
        &[type=checkbox] {
            + label span[role] {
                background-position: 0 0;
            }

            &:focus,
            &:active {
                + label span[role] {
                    box-shadow: -1px -1px 2px $input-outline;
                    border-radius: 4px;
                    background-color: $input-outline;
                }
            }
        }

        &[type=checkbox]:checked {
            + label span[role] {
                background-position: 0 -25px;
            }
        }

        &[type=checkbox]:disabled {
            + label span[role] {
                background-position: 0 -50px;
            }
        }

        // radio
        &[type=radio] {
            + label span[role] {
                background-position: -25px 0;
            }

            &:focus,
            &:active {
                + label span[role] {
                    box-shadow: -1px 0 2px $input-outline;
                    border-radius: 50%;
                    background-color: $input-outline;
                }
            }

            &:checked {
                + label span[role] {
                    background-position: -25px -25px;
                }
            }

            &:disabled {
                + label span[role] {
                    background-position: -25px -50px;
                }
            }
        }

        // 16x16
        &.secondary-input[type=radio] + label span[role],
        &.secondary-input[type=checkbox] + label span[role] {
            width: 18px;
            height: 18px;
            margin-top: -2px;
        }

        // check box
        &.secondary-input[type=checkbox] {
            + label span[role] {
                background-position: -50px 0;
            }
        }

        &.secondary-input[type=checkbox]:checked {
            + label span[role] {
                background-position: -50px -25px;
            }
        }

        &.secondary-input[type=checkbox]:disabled {
            + label span[role] {
                background-position: -50px -50px;
            }
        }

        // radio
        &.secondary-input[type=radio] {
            + label span[role] {
                background-position: -70px 0;
            }
        }

        &.secondary-input[type=radio]:checked {
            + label span[role] {
                background-position: -70px -25px;
            }
        }

        &.secondary-input[type=radio]:disabled {
            + label span[role] {
                background-position: -70px -50px;
            }
        }

        &[type=checkbox]:disabled,
        &[type=radio]:disabled {
            + label {
                cursor: not-allowed;

                span[role] {
                    cursor: not-allowed;
                }
            }
        }
    }
}

// hanging indent for multiple line labels
.radio--indent {
    text-indent: -34px;
    margin-left: 34px;
}

// INACTIVE STATE - ALSO NOT CLICKABLE
.inactive-state {
    opacity: .6;
    pointer-events: none;
}

//Form Field Container
.field-container {
    margin-bottom: 15px; // stick with Bootstrap defaults. also, increments of 5. check the 'margin-bottom-XX' CSS classes for references.
}

.inline-form {
    fieldset,
    .inline-container {
        position: relative;
    }

    input[type=submit],
    button,
    .btn {
        font-size: $font-xsmall;
        padding: .65rem 1.3rem;
        background: $gray-dark;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;
        width: auto;

        &:hover,
        &:focus {
            background: $gray;
            color: $white;
        }
    }

    // PLA form
    .search-submit {
        background: none;
        padding: .78em 1em;
        border: 0;
        border-left: 1px solid $gray;
        color: $gray;
    }
}

// Validation
.success {
    .form-control {
        border-color: $success-form !important;
        color: $gray-dark-3 !important;
    }

    .glyphicon {
        color: $success-form !important;
    }

    &.input-group label {
        border-color: $success-form !important;
    }
}

.error {
    .form-control {
        border-color: $error-form !important;
        color: $gray-dark-3 !important;
    }

    .glyphicon {
        color: $error-form !important;
    }

    &.input-group label {
        border-color: $error-form !important;
    }
}

.required {
    color: #d0021b;
}

// https://css-tricks.com/css3-gradients
// no need for prefixes anymore, see chart on the above page for browser support
// really old browsers will get the background-color
.progress-bar-short {
    background-color: #d9534f;
    background-image: linear-gradient(to bottom, #d9534f 0, #c9302c 100%);
    background-repeat: repeat-x;
}

.progress-bar-weak {
    background-color: #f0ad4e;
    background-image: linear-gradient(to bottom, #f0ad4e 0, #ec971f 100%);
    background-repeat: repeat-x;
}

.progress-bar-medium {
    background-color: #f0e34e;
    background-image: linear-gradient(to bottom, #f0e34e 0, #dbcd32 100%);
    background-repeat: repeat-x;
}

.progress-bar-strong {
    background-color: #449d44;
    background-image: linear-gradient(to bottom, #5cb85c 0, #449d44 100%);
    background-repeat: repeat-x;
}

//------------------------------------
//  $SPECIFIC FORMS
//------------------------------------

//PLA Form
.pla-form {
    margin-left: -15px;
    margin-right: -15px;
}

// Search Form
.search-field {
    padding-right: 3em;
}

// Large datepicker
.date-large {
    position: relative;

    input {
        font-size: $font-input;
        color: $gray-dark-3;
        padding: 10px;
    }

    .glyphicon {
        position: absolute;
        padding: 8px;
        pointer-events: none;
        right: 8px;
        font-size: 20px;
        color: $prim-heading-icon;
        background: $white;
    }
}

@media screen and (max-width: $mobile) {
    .datepicker {
        display: none !important;
    }

    .mobile-check label {
        font-size: $font-xsmall !important;
    }

    .label--block {
        display: block;
        margin-left: 34px;
        margin-top: -22px;

        &.label--block--secondary {
            margin-left: 26px;
            margin-top: -17px;
        }
    }
}

//--------------------------------------------
// $CUSTOMIZED INPUT RANGE (jQuery UI Slider)
//--------------------------------------------
.ui-slider {
    background: $range-input-track-color;
    border: 0;

    .ui-slider-range {
        background: $range-input-track-focus-color;
    }

    // Handle
    .ui-slider-handle {
        position: absolute;
        border: 2px solid $range-input-knob-border;
        height: 28px;
        width: 28px;
        border-radius: 50%;
        background: $range-input-knob-color;
        cursor: pointer;
        transition: border-color .3s linear;

        // Microsoft specific, do not remove
        -ms-touch-action: none;
        touch-action: none;

        // Handle states
        &.ui-state-active,
        &.ui-state-hover,
        &.ui-state-focus {
            border-color: $range-input-knob-focus-color;
        }

        &:focus {
            outline: 0;
        }

        // Tooltip (Using Bootstrap's tooltip HTML + CSS, with minor tweaks here)
        + .handle-tooltip {
            opacity: 1;
            white-space: nowrap;
            bottom: 100%;
            z-index: 3;
        }
    }

    // Horizontal slider
    &.ui-slider-horizontal {
        margin: 22px auto;
        height: 3px;

        .ui-slider-range {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }

        .ui-slider-handle {
            margin-top: -8px;

            + .handle-tooltip {
                margin: 0 0 15px;
            }
        }
    }

    // Horizontal Slider - DCMA Tweaks
    .tooltip_nomargin {
        .handle-tooltip {
            margin: 0;
        }
    }

    // Vertical slider
    &.ui-slider-vertical {
        margin: auto 22px;
        width: 3px;
        min-height: 0;
        max-height: 100%;

        .ui-slider-range {
            border-top-right-radius: 0;
            border-top-left-radius: 0;
        }

        .ui-slider-handle {
            margin-left: -8px;

            + .handle-tooltip {
                margin: 0 0 -5px 25px;

                &.left {
                    margin: 0 25px -5px 0;
                }
            }
        }
    }

    &.ui-state-disabled {
        .ui-slider-handle,
        .ui-slider-range {
            cursor: default;
        }
    }

    &.ui-slider-pips {
        .ui-slider-pip {
            top: -2px;
        }

        .ui-slider-pip-label .ui-slider-label {
            //display: none;
        }

        .ui-slider-line {
            background: transparent;
        }

        [class*=ui-slider-pip-selected] .ui-slider-line,
        .ui-slider-pip-inrange .ui-slider-line {
            width: 7px;
            height: 7px;
            background: $range-input-track-focus-color;
            border-radius: 50%;
        }
    }
}

