    font-style: italic;
    font-size: 16px;
}

.passwordBorder {
    border: 1px #ebebeb solid;
    width: 100%;
    height: 20px;

}

.passwordDescription {
    font-weight: bold;
    font-size: 16px;
}

.passwordDescriptionWrap {
    height: 20px;
    margin-bottom: 10px;
}

#passwordStrength {
    display: block;
    height: 20px;
    margin-bottom: 10px;
    transition: all .4s ease;
}

.pwdStrength0 {
    background: none repeat scroll 0 0 #d0021b; //* too short */
    width: 10%;
}

.pwdStrength1 {
    background: none repeat scroll 0 0 #f5633a; //* weak */
    width: 35%;
}

.pwdStrength2 {
    background: none repeat scroll 0 0 #ebf229; //* medium */
    width: 70%;
}

.pwdStrength3 {
    background: none repeat scroll 0 0 #10bf00; //* strong */
    width: 100%;
}

.pwdStrength4 {
    background: none repeat scroll 0 0 #10bf00; //* strong */
