/*------------------------------------*\
    $TRS_NON-RESPONSIVE_LAYOUT
\*------------------------------------*/

$trs-layout-width: 960px; // main content
$trs-layout-offset: 245px; // menu
$trs-layout-space-between-columns: 15px; // default margins
$trs-layout-min-height: 618px; // not sure where this number came from

// ---- for reference
// $mobile: 568px;
// $tablet: 768px;
// $desktop: 992px;

.layout-non-responsive,
[data-ui-compat='non-responsive'] {
    font: 400 $font-base $font; // need to override inline styles on some non-responsive pages

    //* Full-width */
    .page-container {
        min-width: $trs-layout-width + $trs-layout-offset + $trs-layout-space-between-columns;
        max-width: $trs-layout-width + $trs-layout-offset + $trs-layout-space-between-columns;
        margin: 0 auto;
        overflow: hidden;
    }

    .container-fluid {
        padding-left: 0;
        padding-right: 0;
    }

    //* Landscape mode */
    @media screen and (max-width: 1024px) {
    //@media screen and (max-width: $desktop - 1) {
        .page-container {
            min-width: $trs-layout-width + $trs-layout-space-between-columns;
            max-width: $trs-layout-width + $trs-layout-space-between-columns;
        }
    }

    //* Tablet mode */
    @media screen and (max-width: 768px) {
        .page-container {
            min-width: 320px; // 768px
            max-width: 768px;
        }
    }

    .sidebar-offcanvas {
        max-width: $trs-layout-offset;
    }

    #layout-container {
        // min-height: $trs-layout-min-height;
        margin: 0;
        overflow: hidden;
        text-align: left;
        border-left: 1px $gray-light-2 solid;
    }

    .layout-full {
        max-width: 100%;
    }

     // Prevent rogue page-specific styles from defacing the menu
    #main-menu .menu a {
        font-size: $font-base;
        line-height: 1.4;
    }

    // General
    .trs-legacy {
        // normalize regular layout
        [role="main"] {
            margin: 0 15px;
            padding: 20px 0;
        }

        // horizontal rules
        hr {
            height: 0;
            border: 0;
            border-top: 1px solid $gray-light-10;
            margin: 15px 0 !important;
            padding: 0;

            &.dotted {
                border-top-style: dotted;
            }
        }

        // iframes - print version
        .print-version {
            height: 225px;
        }

        // reset inputs
        input[type=text], input[type=search], input[type=url], input[type=number], input[type=date], input[type=email], input[type=password] {
            -webkit-appearance: none;
            -moz-appearance: none;
            -ms-appearance: none;
            appearance: none;

            &::-ms-clear {
                display: none;
            }
        }

        input[type=number] {
            &::-webkit-inner-spin-button,
            &::-webkit-outer-spin-button {
                -webkit-appearance: none;
            }
        }

        input[type=date] {
            text-transform: uppercase;

            &::-webkit-clear-button {
                display: none;
            }

            &::-webkit-inner-spin-button {
                display: none;
            }

            &::-webkit-calendar-picker-indicator {
                display: none;
            }

            &::-ms-clear {
                display: none;
            }
        }

        // normalize inputs
        input:not([type=radio]):not([type=checkbox]):not([type=hidden]),
        textarea,
        select {
            padding: 3px 5px;
            height: auto !important;
            line-height: normal !important;
            border: 1px $gray-light-3 solid;
            box-shadow: none;
            transition: none;

            &[disabled] {
                background: $gray-light-1;
                color: $disabled;
                border-color: $gray-light-3 !important;
                opacity: 1;
                -webkit-text-fill-color: $disabled; // Override iOS / Android font color change
                -webkit-opacity: 1; // Override iOS opacity change affecting text & background color

                + label {
                    background: $gray-light-1;
                    color: $disabled;
                    border-color: $gray-light-3 !important;
                    cursor: not-allowed !important;
                    transition: none;
                    pointer-events: none;
                    opacity: 1;
                    -webkit-text-fill-color: $disabled; // Override iOS / Android font color change
                    -webkit-opacity: 1; // Override iOS opacity change affecting text & background color
                }
            }

            &:focus {
                outline: 0;
                box-shadow: 0;
                border: 1px $black solid !important;

                + .input-group-addon {
                    border-color: $black;
                }
            }
        }

        input[type=radio],
        input[type=checkbox] {
            width: 15px;
            height: 15px;
            outline: 0;
            border: 0;
            background: none;
            padding: 0;
            margin: 0 1px;
            vertical-align: middle;
        }

        // Fix datepicker icon
        .input-group.date .input-group-addon {
            padding-top: 3px;
            padding-bottom: 3px;
            font-size: 100%;
            transition: none;
        }

        // General fixes
        .form-box {
            .bg-gradient {
                margin: 0;
                background: #e7e7e7;
            }

            h2 {
                padding: 5px 15px;
                font-size: 16px;
            }

            fieldset {
                border-color: #e7e7e7;

                input[type=text],
                input[type=password],
                select {
                    background-color: #f5f5f5;
                }
            }
        }

        .popup-title {
            h2 {
                //padding: 5px 15px;
                font-size: 16px;
            }

            &.bg-gradient {
                margin: 0;
                background: #e7e7e7;
            }
        }

        .popup-button {
            outline: 0;
            text-decoration: none;
        }

        .instructText,
        .instructtext,
        .bodytextsmall,
        .bodytextSmall,
        .bodyTextSmall,
        .formtitle,
        .formTitle,
        .bodytext,
        .bodyText,
        .bodytext td,
        .bodyText td,
        .bodytext p,
        .bodyText p {
            font: normal 12px Arial, Helvetica, sans-serif;
        }

        .bodytextBold,
        .bodytextbold {
            font: 700 12px Arial, Helvetica, sans-serif;
        }

        .instructText,
        .instructtext {
            padding-top: 5px;
        }

        // Fixes the old, diagonal tabs
        .tabContainer {
            box-sizing: content-box;
            padding: 0;

            * {
                box-sizing: content-box;
            }

            .tabName {
                font: normal 11px/1 Arial, Helvetica, sans-serif;
            }
        }

        // Fixes non-responsive "windows" (modals) on pages like Home > Beneficiaries
        #boxes {
            .window {
                .dialog {
                    width: auto !important;
                }

                h3.modal {
                    display: block;
                    position: static;
                    margin: 0 0 0 5px;
                    padding: 5px;
                    font-size: 14px;
                }
            }
        }

        // fix modal "mask" overlay dark opacity position/dimensions
        #mask {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }

        // step dots on non-responsive
        .ball_fill {
            background: $btn-primary;
        }

        .ma-ball {
            border-color: $btn-primary-active;
        }

        .wizard-progress {
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            margin: auto;
            list-style: none;
            float: none;
            width: 112px;
            height: 16px;

            .ball_fill {
                background: $btn-primary;
            }

            .ma-ball {
                border-color: $btn-primary-active;
            }
        }

        table.table-plain td {
            padding: 0 15px 5px 0;
            vertical-align: top;

            &:last-child {
                padding-right: 0;
            }
        }

        // fix for MA advertising
        .retirement-outlook-legacy.retirement-outlook-legacy__responsive-fix {
            border: #dfdfdf 1px solid;
            border-radius: 4px;
            box-shadow: none;

            .block.block-basic {
                float: none;
            }
        }
    } // end .trs-legacy

    .cursor-link {
        cursor: pointer;
    }

    // fix the clearfix on some non-responsive pages
    .clearfix {
        &::before,
        &::after {
            display: none;
        }
    }

    // "grid" system
    .row {
        display: block;
        width: 100%; // was auto
        margin: 0;
    }

    header .row {
        overflow: visible;
    }

    // .row .row,
    // table .row {
    //     display: block; // was inline-block
    //     margin: 0;
    //     width: 100%; // was auto
    // }

    .trs-header.row .row {
        display: block;
    }

    .alpha { margin-left: 10px !important; }
    .omega { margin-right: 10px !important; }

    .column { margin: 0; overflow: hidden; float: left; display: inline; box-sizing: content-box; }

    .span-1 { width: 39px; }
    .span-2 { width: 78px; }
    .span-3 { width: 117px; }
    .span-4 { width: 156px; }
    .span-5 { width: 195px; }
    .span-6 { width: 234px; }
    .span-7 { width: 273px; }
    .span-8 { width: 313px; }
    .span-9 { width: 352px; }
    .span-10 { width: 391px; }
    .span-11 { width: 430px; }
    .span-12 { width: 469px; }
    .span-13 { width: 508px; }
    .span-14 { width: 547px; }
    .span-15 { width: 586px; }
    .span-16 { width: 626px; margin-bottom: 15px; }
    .span-17 { width: 660px; margin-bottom: 15px; }
    .span-18 { width: 704px; margin-bottom: 15px; }
    .span-20 { width: 782px; margin-bottom: 15px; }
    .span-21 { width: 795px; margin-bottom: 15px; }
    .span-23 { width: 899px; margin-bottom: 15px; }
    //.span-24 { width: 939px; margin-bottom: 15px; }
    .span-24 { width: 100%; margin-bottom: 15px; }

    .offset-1 { margin-left: 39px; }
    .offset-2 { margin-left: 78px; }
    .offset-3 { margin-left: 117px; }
    .offset-4 { margin-left: 156px; }
    .offset-5 { margin-left: 195px; }
    .offset-6 { margin-left: 234px; }
    .offset-7 { margin-left: 273px; }
    .offset-8 { margin-left: 313px; }
    .offset-9 { margin-left: 352px; }
    .offset-10 { margin-left: 390px; }
    .offset-12 { margin-left: 469px; }

    .iframe {
        background-color: $white;
    }

    .text-right {
        text-align: right !important;
    }

    .text-left {
        text-align: left !important;
    }

    .dottedLineTop {
        clear: both;
        border-bottom: 1px dotted $black;
        margin: 0 0 5px;
        box-sizing: border-box;
        overflow: hidden;

        * {
            box-sizing: border-box;
        }
    }

    .popover {
        font-size: 13px;
    }
}

.mobile--non-responsive {
    .hidden-mobile {
        display: none;
    }
}

.trs-hidden { display: none; }

.simplemodal-overlay {
    opacity: .5;
    height: 100% !important;
    width: 100% !important;
    position: fixed !important;
    left: 0 !important;
    top: 0 !important;
    z-index: 1001;
    background-color: $black;
}

.simplemodal-container {
    position: fixed !important;
    text-align: left;
    border: 0;
    border-radius: 5px;
    background-color: $white;
    z-index: 1002;
    width: auto !important;
    height: auto !important;
    max-height: none !important;
    top: 50% !important;
    left: 50% !important;
    transform: translate(-50%, -50%) !important;
    padding: 15px !important;
    margin: 0 !important;

    .popup-close {
        top: 8px !important;
        right: 8px !important;
        left: auto !important;
        bottom: auto !important;
    }

    .simplemodal-wrap {
        h2.content-med {
            margin-bottom: 15px;
        }

        .popup-title {
            border-top-right-radius: 5px;
            border-top-left-radius: 5px;
            filter: none;
            background: none;
            border-bottom: 0;
            margin: 0 0 15px;
            padding: 0;
        }

        .popup-body {
            margin: 0;
            border: 0;

            .alpha,
            .omega {
                margin: auto !important;
            }
        }

        .row {
            width: auto;
            margin: 0 auto;
        }

        .column {
            overflow: initial;
        }

        .modal {
            display: block;
            position: static;
            line-height: initial;
        }

        .content-text.modal {
            display: inline;
        }

        input,
        textarea,
        select {
            border: 1px $gray-dark-6 solid;
            padding: 3px 5px;
            line-height: normal !important;
            height: auto !important;
        }

        input[type=radio],
        input[type=checkbox] {
            border: 0;
            padding: 0 !important;
            line-height: 1;
            vertical-align: text-bottom;
        }
    }
}

.resource-container table tr td {
    font-size: 11px;
}
