//*----------------------------------*\
//    #CHARTS AND PLOTTING
//\*----------------------------------*/

// Used in Dashboard
.pie-chart {
    position: relative;
    height: 150px;
}

.graph-text {
    position: absolute;
    width: 100%;
    top: 40px;
    text-align: center;
    font-size: 14px;

    hr {
        margin: 0;
        margin-left: auto;
        margin-right: auto;
        padding: 0;
        width: 100px;
    }
}

.ao-graph {
    position: relative;
    width: 155px;
    height: 150px;
    margin: 0 auto;
}

// custom label prepended in jQuery
.custom-label {
    position: absolute;
    left: 50%;
    margin-top: -20px;
    z-index: 1;
    font-size: 12px;
    font-weight: 600;
    //width: 175px;
    //opacity: .6;

    .custom-label__center,
    .custom-label__right,
    .custom-label__left {
        position: relative;
        background-color: #f2f2f2;
        padding: 5px 8px;
        border: solid 1px $black;
        color: $black;
        text-align: center;
    }

    .custom-label__center {
        left: -50%;
    }

    .custom-label__right {
        right: 0;
    }

    .custom-label__left {
        left: 0;
    }
}

// Highcharts specific CSS
.highcharts-container {
    // Title
    .highcharts-title {
        color: $black !important;
        font-size: $font-h2 !important;
        font-family: $font-sec-reg !important;
        text-align: center;
    }

    // Subtitle
    .highcharts-subtitle {
        color: $black !important;
        font-size: $font-title !important;
        font-family: $font-pri-reg !important;
        text-align: center;
        text-transform: capitalize;
        font-weight: 700;
        line-height: 1.1;
    }

    // Tooltip
    .highcharts-tooltip {
        span {
            padding: 5px 8px;
            background: $white;
            font-size: $font-xsmall;
            font-family: $font-pri-reg;
            border-radius: 4px;
            border: 1px solid $gray-med;
            color: $gray-med;
            line-height: 1.5;
            white-space: nowrap;
            text-align: center;
            z-index: 1;

            .chart-point-name {
                font-size: inherit;
            }

            .chart-point-xy {
                font-size: inherit;
                font-weight: 600;
            }
        }
    }
}

// Household Retirement Budget chart
.householdRetirementBudgetChart {
    .highcharts-container .highcharts-title,
    .highcharts-container .highcharts-subtitle {
        left: 0 !important;
        width: 100% !important;
    }
}

//mini progress bar as used on Acct Overview/Dashboard
.progress--mini {
    &.progress {
        position: relative;
        overflow: visible;
        height: 10px;
        width: 50%;
        border-radius: 0;
        margin: 15px 0 10px;
    }

    .progress-bar {
        border-right: 3px solid $white;

        &[aria-valuenow="100"],
        &[aria-valuenow="0"],
        &[style*="100%"] {
            border-right: 0;
        }
    }
}

.progress--mini__label {
    position: absolute;
    bottom: 0;
    right: 0;
    color: $black !important;
}

.desktop,
.tablet {
    .progress--mini__caption {
        font-size: 10px;
        display: inline-block;
        line-height: 14px;
        width: 48%;
    }
}

.mobile {
    .progress.progress--mini {
        width: 100%;
        margin-bottom: 5px;

        .progress-bar {
            position: relative;
        }
    }

    .progress--mini__label {
        right: -1.25em;
        font-size: $font-xsmall !important;
    }
}

//progress bar as used on OnTrack/DCMA
.progress__unit-btn {
    margin-bottom: 15px;

    label {
        padding-bottom: 0;
    }
}

.progress__income-gap {
    position: relative;
    background-color: $ta-brand-red;
    background-image: none;
    overflow: visible;
    height: 15px;
    margin: 30px 10px 48px;
    border-radius: 0;

    .progress-bar {
        text-align: left;
        height: 22px;
        border-right: 3px solid $white;
        max-width: 100%;
        margin-top: -7px;
        z-index: 2;
        transition: width .5s ease-out;

        &[style*="100%"],
        &[aria-valuenow="0"] {
            border-right: 0;
        }
    }

    .progress__income-gap--current {
        position: absolute;
        color: $gray-med-2;
        top: -27px;
    }

    @media screen and (max-width: $tablet - 1) {
        margin: 30px 0 48px;
    }
}

.progress-bar-complete {
    position: absolute;
    bottom: -25px;
    font-size: 12px;
    width: 100%;
    min-width: 150px;
    max-width: 100%;
    height: 22px;
    margin-top: 3px;
}

.progress__income-gap--alert,
.progress__income-gap--goal {
    position: absolute;
    right: 0;
}

.progress__income-gap--goal {
    display: block;
    text-align: right;
    text-wrap: none;
    color: $gray-med-2;
    bottom: -18px;
}

.progress__income-gap--alert {
    color: $gray-warm;
    top: -27px;

    &.success {
        color: $valid;
    }

    &.danger {
        color: $error;

        // minus sign before gap
        .signaled::before {
            content: '-';
            margin-right: 2px;
        }
    }
}

// DCMA Summary Chart
.dcma-summary-chart {
    width: 100%;
}

.mobile .dcma-summary-chart .assets,
.mobile .dcma-summary-chart.highcharts-data-label-connector {
    display: none;
}

@media screen and (max-width: $tablet - 1) {
    .dcma-summary-chart .assets,
    .dcma-summary-chart .highcharts-data-label-connector {
        display: none;
    }
}
