/*------------------------------------*\
    $FOOTER
\*------------------------------------*/
.footer {
    clear: both;
    overflow: hidden;
    color: $white;
    line-height: 1.2;

    a {
        color: $gray-light;
    }

    .copyright {
        @media all and (min-width: $bp-med) {
            float: right;
        }
    }
}

//Footer Nav
.nav-footer {
    margin: (-$pad) (-$pad) $space;

    li {
        border-bottom: 1px solid $gray-dark;

        @media all and (min-width: $bp-med) {
            border: 0;
            float: left;
        }
    }

    a {
        display: block;
        padding: $pad;
    }
}

/*------------------------------------*\
    $Global Footer - PLA
\*------------------------------------*/
.pla-footer {
    padding-top: 0 !important;

    .pla-footer__body {
        background-color: $gray-light-5 !important;
        color: $gray-warm;
        padding: 20px;
    }
}

.pla-footer-links,
.pla-footer-links a:focus {
    margin-top: 9px !important;
    display: inline;
    text-decoration: none;
    color: $link-text;
}

.pla-footer-links a:hover {
    text-decoration: underline;
    color: $link-text;
}

.pla-footer-links a:visited {
    text-decoration: none;
    color: $link-text;
}

.pla-footer-title-links {
    margin-bottom: 10px;
}

.pla-footer-contactus-container {
    bottom: -20px;
    .btn {
        background-color: $white;
        font-size: 12px;
        padding: 0 20px !important;
        line-height: 40px;

        .glyphicon {
            font-size: 16px !important;
            font-weight: bold !important;
        }

        .phone-number {
            top: -2px;
            position: relative
        }
    }
}

.pla-legal__nav {
    margin-top:40px;
}

.pla-footer-links-container {
    text-align: center;
    margin: 0 auto;
    padding-bottom: 24px;
}

.pla-footer__footnotes {
 font:normal $font-footnotes ArialMT, Arial, sans-serif !important;
    line-height: 120%;
    margin-top: 0px !important;
}

.pla-footer__footnotes p {
    padding-left: 15px !important;
    padding-right: 15px !important;
}

.pla-footer-divider {
    border-top: 1px solid $soc-icon-hovered;
    margin-top: 23px;
    margin-right: auto;
    margin-left: auto;
}

.pla-footer-seperator {
    font-weight: bolder;
    color: black;
}

.pla-footer-logo-vert-center {
    text-align: center;
    height: 95px;
    line-height: 95px;
}

.pla-footer-country-vert-center {
    text-align: center;
    margin: 0 auto;
    height: 95px;
}

.pla-footer-mobile-footnotes {
    text-align: center;
    border-bottom: 1px solid $soc-icon-hovered;
    padding-bottom: 32px;
}

.pla-footer-country-form-vert-center {
    vertical-align: middle;
    display: table-cell;

    .form-group {
        margin-bottom: 0;
    }
}
.pla-footer-country-form {
    height: 110px;
    display: table;
    text-align: center;
    margin: 0 auto;
}

/*------------------------------------*\
    $Global Footer - TRS
\*------------------------------------*/
.trs-footer {
    padding: 0;
    position: relative;
    overflow-x: hidden;
    clear: both;
    background: none;

    .trs-footer__body {
        background-color: $gray-light-5 !important;
        color: $gray-warm;
        padding: 20px;
        overflow: hidden;
		
		a {
			color: #3b75a0;//ada compliant color contrast
		}
    }

    .trs-icon-circle {
        font-size: 22px;
        color: $gray-dark-4;
        border-color: $soc-icon-outline;

        &:hover {
            color: $gray;
        }

        + .trs-icon-circle {
            margin-left: 14px;
        }
    }
}

.trs-footer__subhead {
    font-size: $font-small !important;
    font-weight: bold !important;
    text-transform: capitalize;
    color: $gray-med-2 !important;
    margin: 0 0 15px;
}

.trs-footer__nav-list {
    font-size: $font-xsmall;
    list-style: none outside none;
    padding-left: 0;
    position: relative;

    li {
        display:inline-block;
    }

    .divider {
        color: $gray-dark;
        margin: 0 2px;
    }
}

.mobile {
    .trs-footer__nav-list {
        .divider {
            display:none;
        }

        a {
            display: block;
            margin: 5px 0;
        }

        li {
            display:block;
        }
    }

    .trs-footer {
        padding: 0 0 35px;
    }

    .trs-footer__subhead {
        margin-top: 30px;
    }

    .trs-footer--col-border {
        border-right: none;
        margin-bottom: 0;
        height: auto;
    }

    .trs-footer__footnotes {
        margin-top: 35px;
    }

    #global_footer {
        padding: 0 !important;
    }

    .pla-footer-links {
        margin-top: 9px !important;
        display: block !important;
    }

    .pla-footer-seperator {
        display: none !important;
    }

    .pla-footer-title-links {
        margin-bottom: 0 !important;
        margin-top: 0 !important;
    }

    .trs-footer__subhead.control-label {
        top: -25px;
    }
}

.trs-footer__policies {
    margin-top: 40px;
}

.footer-rule {
    border-top: $soc-icon-hovered solid 1px;
    padding-bottom: 50px;
}

.trs-footer__footnotes {
    margin: 60px 0 0;
    p {
        font-size: $font-xxxsmall;
        color: $gray-warm;
    }
}

.trs-footer--col-border {
    border-right: 1px solid $btn-secondary-border-disabled;
}

.trs-footer__phone {
    bottom: -20px;
    position: relative;
    .btn:not([style*="display: none"]):not([style*="display:none"]):not(.trs-hidden):not(.hidden) {
        //background-color: $white;

        .glyphicon {
            font-size: $font-link;
            font-weight: bold;
        }

        .phone-number {
            top: -1px;
            position: relative
        }

        + .btn {
            margin-left: 20px;
        }
    }
}

.trs-footer__social {
    margin: 15px 0;
}

/*------------------------------------*\
    $SSO FOOTER
\*------------------------------------*/
$page-size: 980px;
$font-size-small-12 : 12px;
$font-size-small-11: 11px;


// Desktop
$footerLinksHeaderFontSizeD: 15px;
$footerLinksHeaderLnHeightD: 20px;
$footerLinksItemFontSizeD: 14px;
$footerLinksItemLnHeightD: 23px;

// Tablet
$footerLinksHeaderFontSizeT: 13px;
$footerLinksHeaderLnHeightT: 20px;
$footerLinksItemFontSizeT: 12px;
$footerLinksItemLnHeightT: 25px;

// Mobile
$footerLinksHeaderFontSizeM: 13px;
$footerLinksHeaderLnHeightM: 20px;
$footerLinksItemFontSizeM: 12px;
$footerLinksItemLnHeightM: 25px;

.sso-footer {
	background-color: $gray-light-5 !important;
    border-color: transparent !important;
    background-image: none !important;
	padding-bottom:20px;


.ta-tracking-number {
	max-width: $page-size;
	margin: 0 auto;

	p {
		padding-left: 25px !important;
		font-family: "ArialMT, sans serif";
		font-size: 14px;
		line-height: 21px;
		color: $gray-warm;
		padding-top: 0;
		padding-bottom: 6px;
		margin-bottom: 40px;
	}

	@media (min-width: $desktop) {
		p {
			padding-left: 15px !important;
		}
	}
}

.tablet {
	.ta-tracking-number {
		p {
			margin-bottom: 25px;
		}
	}
}

.mobile {
	.ta-tracking-number {
		p {
			margin-bottom: 20px;
		}
	}
}

//Footer Nav
footer.trs-footer {
	padding-top:0;
    overflow: visible;
}
.trs-footer--col-border {
	border-right: none;
}
.ta-footer-contactus-container {
	.btn-secondary {
		background-color: $white;
	}
}

.ta-footer-mobile-footnotes {
	text-align: center;
    border-bottom: 1px solid $soc-icon-hovered;
    padding-bottom: 32px;
	margin: 0 30px;
}


.ta-footer__socialmedia {
	padding-top: 70px;
	padding-bottom: 40px;
	background-color: $white;

	.trs-icon-circle {
		margin:4px;
		font-size: 24px;
	}
}
.ta-footer__socialmedia--heading {
	display: none;
}
.ta-footer-link-listing, .ta-footer .ta-footer-divider {
	background-color: $gray-light-5;
}

.ta-footer {

  	max-width: $page-size;
  	margin: 0 auto;
  	padding-bottom: 0;

	.ta-footer-contactus-container {
    	//margin-bottom: 30px;
    	margin-left: 0;
    	margin-right: 0;
    	position: relative;
    	top: -19px;
	}

	.ta-footer-divider {
		border-top: 1px solid $soc-icon-hovered;
	    margin-top: 23px;
	    margin-right: 16px;
	    margin-left: 16px;
	}

	.trs-footer__subhead.control-label {
		padding-right: 0;
		margin-top: 0px !important;
	}

	select.form-control {
		color: #777;
	}

	.trs-footer__footnotes {
		margin-top: 0px  !important;
		padding-left: 30px !important;
		padding-right: 30px !important;

		p.legal {
			line-height: 16px;
		}
	}

	.trs-footer__nav-list {
		margin-bottom: 30px;

      li {
        float: left;
        clear: both;
      }
	}
	
	.trs-footer__subhead {
		font-size: 13px !important;
		font-weight: bold !important;
		text-transform: capitalize;
		color: $gray-med-2 !important;
		margin: 0 0 15px;
	}
}


.legal {
	font-size: 10px;
	color: $gray-warm;
	padding:5px;
}
.trs-footer__nav-list {
	font-size: 12px;
	line-height: 25px;
}
.trs-footer__social {
	margin-bottom: 0px;
}

.pla-footer .pla-footer__body {
	padding-top: 40px;
}

.trs-footer__nav-list li {
	float: left;
	clear: both;
}

.trs-footer__nav-list {
	font-size: 12px;
	line-height: 25px;
}
}




// SSO FOOTER MOBILE ------

.mobile .sso-footer .col-xs-6:nth-child(2n+1) {
	clear: left;
}


.mobile .sso-footer .trs-footer__nav-list {
	font-size: 12px;
	line-height: 15px;
	padding-left: 30%;
}

.mobile .sso-footer .trs-footer__subhead {
	padding-left: 30%;	
	text-align: left;
}

// SSO FOOTER TABLET ------

.tablet .sso-footer .trs-footer__nav-list {
	font-size: 12px;
	line-height: 15px;
	padding-left: 30%;
}

.tablet .sso-footer .trs-footer__subhead {
	padding-left: 30%;
	padding-top: 20px;
	text-align: left;
}

.tablet .sso-footer .trs-footer__nav-list a {
    display: block;
    margin: 5px 0;
}
.tablet .sso-footer .col-xs-6:nth-child(3n+1) {
	clear: left;
}

/*------------------------------------*\
    $END SSO FOOTER
\*------------------------------------*/

