//*------------------------------------*\
//    $Tables
//\*------------------------------------*/
.table {
    margin-bottom: 15px;

    // overwrite Bootstrap
    > tbody + tbody {
        border-top: 0;
    }

    tbody > tr {
        > td {
            padding: 8px 10px 9px; // "40px"
            vertical-align: top;
        }

        &.click-action > td {
            vertical-align: middle; // exception for 'account tables', e.g. Retirement Accounts
        }
    }

    tfoot tr {
        td,
        th {
            font-size: $font-base;
        }

        td {
            text-align: right;
        }
    }

    // Override Bootstrap defaults
    > tfoot > tr > td {
        padding: 8px 10px;
    }

    thead tr {
        td,
        th {
            padding: 8px 10px;
            vertical-align: middle;
            font-size: $font-base;
        }

        .currency,
        .percentage,
        .percent,
        .balance,
        .units,
        .value {
            text-align: right;
        }
    }

    &.table__row--no-border {
        > tbody > tr > td {
            border: 0;
        }
    }

    &.table__row--narrow {
        tbody > tr > td {
            padding: 5px 10px 4px;
        }
    }

    // fix for bizarre border bug on first cell of a tbody
    // &.tbl-responsive .sr-only {
    //     display: none;
    // }

    // Loans
    &.table__loans--selected,
    &.table__loans--additional-amount {
        > tbody {
            border-top: 1px solid $gray-light-2;

            &:first-child {
                border: 0;

                tr:first-child td {
                    padding-top: 0;
                }
            }

            &:last-child {
                tr:last-child td {
                    padding-bottom: 0;
                }
            }

            tr:first-child td {
                padding-top: 15px;
            }

            tr:last-child td {
                padding-bottom: 15px;
            }
        }
    }

    &.table__loans--additional-amount {
        > tbody {
            border: 0;
        }
    }

    // DCMA Summary - Savings Rate
    &.table__savings-rate {
        .auto-increase {
            white-space: nowrap;
            margin-top: 5px;
        }

        .table__savings-rate--footer {
            td {
                padding: 0;
                text-align: center;
            }

            .total {
                margin: 10px auto;
            }
        }

        @media screen and (max-width: $mobile) {
            border-width: 1px 0;
        }
    }
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    border: $tbl-border solid 1px;
    border-radius: 4px;

    // scrollable table, it needs high specificity
    &.table--scrollable {
        width: 100%;
        border-radius: 0;

        thead,
        tbody,
        tfoot {
            th,
            td {
                padding: 10px 10px 9px;
            }
        }
    }
}

.dataTables_scrollBody table.table--scrollable {
    thead th {
        border-bottom: 0 !important;
    }
}

th,
.th,
tfoot td {
    text-align: left;
    vertical-align: middle;
    padding: .2em;
    background-color: $gray-light;
    font-size: $font-tbl-cell;
    font-weight: 700;
}

td,
.td {
    font-size: $font-tbl-cell;
    font-weight: normal;
    padding: .2em;
    text-align: left;

    // fix managed advice fund list tables
    &:not(.cell-with-bg) {
        background-color: transparent;
    }
}

.tbl_subHead,
.tbl_subHead th,
.tbl_subHead td {
    font-size: $font-tbl-header;
    border-right-color: transparent !important;
