.td_bold {
    font-weight: bold;
}

.tbl-noborder {
    border: 0;

    td,
    th {
        border: 0;
    }

    // Reset BS
    > tbody > tr > td,
    > tbody > tr > th,
    > tfoot > tr > td,
    > tfoot > tr > th,
    > thead > tr > td,
    > thead > tr > th {
        border-top-color: transparent;
    }
}

.tbl-nobkg {
    th,
    td {
        background-color: transparent;
    }
}

.tbl-style1 {
    margin: auto;
    border-style: none;
    border-collapse: separate;

    th,
    td {
        font-size: $font-tbl-cell;
        border: 0;
        border-bottom: 1px dotted $gray-light-3;
        padding-top: 10px;
        padding-bottom: 9px;
        vertical-align: middle;
    }
}

.tbl-style1--row-separator tr:not(.tbl-style1--row-separator__th) + tr td,
.tbl-style1--row-separator tr:first-child td {
    border-top: solid 1px $gray-light;
}

.tbl-style1--row-separator tr:last-child td {
    border-bottom: solid 1px $gray-light;
}

