///*------------------------------------*\
//    $Table For Mobile
//\*------------------------------------*/
@media all and (min-width: $mobile + 1) {
    // hide on tablet and up
    .tbl-mobile-show {
        display: none;
    }
}

@media only screen and (max-width: $mobile) {
    .tbl-mobile--div-balance {
        display: block;
    }

    .table--style2 {
        display: block;
        overflow: hidden;
        margin-left: -15px;
        margin-right: -15px;
    }

    .trs-accordion .table--style2 {
        margin-left: 0;
        margin-right: 0;
    }

    //* Force table to not be like tables anymore */
    table.tbl-mobile,
    .tbl-mobile thead,
    .tbl-mobile tbody,
    .tbl-mobile th,
    .tbl-mobile td,
    .tbl-mobile tr {
        display: block;
    }

    table.tbl-mobile {
        border: 0;
        border-collapse: separate;

        //* Hide table headers and footers */
        thead tr,
        tfoot tr {
            display: none;
        }

        // overwrite Bootstrap
        > tbody + tbody {
            border: 0;
            margin-top: -1px;
        }

        tbody > tr {
            border-top: 1px solid $gray-light-10;
            background-color: $gray-light;
            padding: 10px 0;
            cursor: pointer;

            &.embedded-row {
                padding: 0 !important;
                border-top: 0;

                td,
                td:first-child {
                    width: 100%;
                    overflow: hidden;
                    padding-bottom: 0 !important;
                    padding-left: 10px !important;

                    &::before,
                    &::after {
                        content: none;
                    }
                }

                .row {
                    border-top: 1px solid $gray-light-10;
                }
            }

            &.click-action {
                position: relative;

                &::after {
                    content: '\45';
                    position: absolute;
                    top: 50%;
                    right: 10px;
                    display: inline-block;
                    font-family: 'trs-icons';
                    font-style: normal;
                    font-weight: normal;
                    line-height: 24px;
                    margin-top: -12px;
                    color: $link-text;
                    -webkit-font-smoothing: antialiased;
                    -moz-osx-font-smoothing: grayscale;
                }
            }

            &:last-child {
                border-bottom: 1px solid $gray-light-10;
            }

            //* Behave like a "row" */
            > td {
                border-top: 0;
                border-bottom: 0;
                position: relative;
                white-space: normal;
                text-align: left;
                padding-top: 0;
                padding-bottom: 0;
                width: 97%;

                .tbl-td-noborder__indent {
                    width: 100%;
                }

                &:first-child {
                    padding-bottom: 10px;
                }

                //* Now like a table header */
                &::before {
                    position: static;

                    //* Top/left values mimic padding */
                    top: 6px;
                    left: 6px;
                    padding-right: 10px;
                    white-space: nowrap;
                    text-align: left;
                    font-weight: bold;

                    //* Label the data */
                    content: attr(data-title);
                }
            }
        }

        tbody > tr > td[data-title=""]::before,
        tbody > tr > td:not([title]) {
            content: none;
        }

        tbody.total-balance > tr {
            background: #fff;
        }

        // hide on mobile
        .tbl-mobile-hide {
            display: none;
            height: 0 !important;
            padding: 0 !important;
            margin: 0 !important;
            overflow: hidden;
            * { display: none !important; }
        }

        // show on mobile
        .tbl-mobile-show {
            height: auto;
            white-space: normal !important;

            .account-name-delimiter {
                padding-bottom: 10px;
            }

            .account-totals {
                padding-right: 10px;
            }

            &::before {
                display: none;
            }
        }

        .hide-in-mobile {
            display: none;
        }
    }

    .tbl-header {
        text-align: center;
        font-size: $font-base;
    }

    .tbl-header-date {
        font-size: $font-xxsmall;
    }

    .tbl-noborder-right-mobile_tbl {
        border-right: hidden;
    }

    //* mobile table for balance page  */
    .tbl-balance {
        border-top: 0;

        .click-action {
            cursor: pointer;

            th {
                color: $link-text;
                padding-right: 0;
            }

            td {
                background-image: url('../images/sm-table-arrow.png');
                background-repeat: no-repeat;
                background-position: 93%;
                padding-right: 20px;
            }
        }

        tr td:nth-child(2),
        tr td:nth-child(4),
        tr td:nth-child(5) {
            display: none;
        }

        tr th:nth-child(2),
        tr th:nth-child(4),
        tr th:nth-child(5) {
            display: none;
        }

        thead {
            display: none;
        }

        td,
        th {
            font-size: $font-small;
        }

        .tbl_blue_border,
        .tbl_green_border,
        .tbl_purple_border {
            border-bottom: 0;
        }
    }

    .trs-activity-tbl {
        border: 0;

        thead {
            display: none;
        }

        td,
        th {
            font-size: $font-small;
        }

        .click-action {
            cursor: pointer;

            th {
                color: $black;
                background-image: url('../images/sm-table-arrow.png');
                background-repeat: no-repeat;
                background-position: 98%;
                padding: 8px 17px;
            }
        }

        tbody td {
            display: none;
        }

        .tbl_blue_dark_border,
        .tbl_blue_border,
        .tbl_green_border {
            border-bottom: 0;
        }
    }

    .trs-activity-select_right .well-body table.tbl-style1 tbody tr td {
        border-bottom: $gray-light-7 1px;
        border-style: none none solid;
        padding-left: $pad;
        padding-right: $pad;
        font-size: $font-base;
        line-height: 21px;
    }

    .trs-activity-select {
        margin-left: -14px;
        margin-right: -14px;
    }

    //* TBL-CUSTOM-MOBILE */
    .tbl-custom-mobile {
        tr.click-action th {
            color: $black;
        }

        th.group-title-hide {
            display: none;
        }

        th.group-title {
            font-size: 0;
        }

        th::before {
            content: attr(data-title);
            font-size: 14px;
        }

        td {
            white-space: nowrap;
            padding-left: 0;
            vertical-align: middle !important;
        }
    }
}

//*------------------------------------*\
//    PLA TABLES
//\*------------------------------------*/
@media only screen and (max-width: $mobile) {
    //* Force table to not be like tables anymore */
    table.pla-mobile,
    .pla-mobile thead,
    .pla-mobile tbody,
    .pla-mobile th,
    .pla-mobile td,
    .pla-mobile tr {
        display: block;
    }

    table.pla-mobile {
        border: 0;
        border-collapse: separate;

        thead tr,
        tfoot tr {
            display: none;
        }

        tr {
            border-top: 1px solid #ccc;
            background-color: $header-bg;
            padding: 10px 0;
            cursor: pointer;

            &.embedded-row {
                padding: 0 !important;
                border-top: 1px solid #ccc;
            }

            &.embedded-row td,
            &.embedded-row td:first-child {
                width: 100%;
                overflow: hidden;
                padding-bottom: 0 !important;
                padding-left: 10px !important;
            }

            &.embedded-row td::before,
            &.embedded-row td::after,
            &.embedded-row td:first-child::before,
            &.embedded-row td:first-child::after {
                content: none;
            }

            &:last-child {
                border-bottom: 1px solid #ccc;
            }
        }

        td {
            border-top: none !important;
            border-bottom: none !important;
            position: relative;
            white-space: normal;
            text-align: left;
            padding-top: 0 !important;
            padding-bottom: 0 !important;
            width: 97%;
        }

        td:first-child {
            padding-left: 6px !important;
            padding-bottom: 10px !important;
        }

        td:first-child::before {
            display: none;
        }

        td::before {
            content: attr(data-title);
            position: static;

            //* Top/left values mimic padding */
            top: 6px;
            left: 6px;
            padding-right: 10px;
            white-space: nowrap;
            text-align: left;
            font-weight: bold;
        }

        .pla-mobile-hide {
            height: 0;

            button {
                display: none;
            }

            .btn-primary {
                display: none;
            }
        }
    }
}

//* Collapsing Columns Table */
.tbl-collapse-cols {
    margin-bottom: 10px;

    th,
    td {
        vertical-align: top;
        font-size: $font-small;
        line-height: 1.5em;
        padding: 7px 10px;
    }

    tfoot td {
        font-size: $font-base;
    }

    tbody th {
        border-right: 0;
    }

    tbody td {
        border-left: 0;
    }
}

.tbl-collapse-cols__thin-link {
    font-weight: 400;
}

.desktop,
.tablet {
    .tbl-collapse-cols {
        tbody {
            display: table;
            float: left;
            width: 33.33%;
        }

        td,
        th {
            border: 0;
        }

        tbody:first-of-type :not(.tbl-collapse-cols__col-hdr) th {
            text-align: right;
            padding-right: 1px;
        }

        tfoot {
            display: table;
            width: 100%;

            td {
                border-top: 1px solid $tbl-border;
            }

            a + a {
                margin-left: 30px;
            }
        }
    }

    .tbl-collapse-cols__col-hdr th,
    .tbl-collapse-cols__col-hdr td {
        font-size: $font-tbl-header-2;
        background-color: $gray-light;
        padding: 12px 10px 16px;
        border-bottom: 1px solid $tbl-border;
    }

    .tbl-collapse-cols_-bordered th,
    .tbl-collapse-cols_-bordered td,
    .tbl-collapse-cols_-btm-brdr th,
    .tbl-collapse-cols_-btm-brdr td {
        border-bottom: 1px solid $tbl-border;
    }

    .tbl-collapse-cols_-bordered th,
    .tbl-collapse-cols_-bordered--end th,
    th.tbl-collapse-cols_-bordered--lt {
        border-left: 1px solid $tbl-border;
    }

    .tbl-collapse-cols_-bordered td,
    .tbl-collapse-cols_-bordered--end td {
        border-right: 1px solid $tbl-border;
    }

    td.tbl-collapse-cols_-connect {
        padding-left: 0;
    }

    .tbl-collapse-cols_-dashes {
        display: block;
        padding-bottom: 7px;
        border-bottom: 1px dotted $gray-light-3;
    }

    .heavy-sm {
        font-weight: 700;
    }

    .loans-summary-tbl {
        th,
        td {
            font-size: 16px;
            line-height: 22px;
        }
    }
}

.mobile {
    .tbl-collapse-cols {
        border-top: 0;

        th,
        td {
            padding: 6px 9px;
        }

        tfoot td {
            text-align: center;
            background-color: transparent;
            width: 100%;
            padding-top: 20px;
            border-color: transparent;
        }
    }

    .tbl-collapse-cols__col-hdr th,
    .tbl-collapse-cols__col-hdr td,
    .tbl-collapse-cols__mbl-hdr th,
    .tbl-collapse-cols__mbl-hdr td {
        background-color: $gray-light;
        border-top: 1px solid $tbl-border;
    }

    .tbl-collapse-cols__pseudo-hdr {
        font-weight: 700;
    }

    .tbl-collapse-cols__thin-link {
        display: block;
        margin-top: 20px;
    }

    .loans-summary-tbl--mbl__body th,
    .loans-summary-tbl--mbl__body td {
        font-size: $font-base;
    }
}

