// Accordion table
.accordion-table {
    width: 100%;
    margin-bottom: 15px;
    background: $white;

    // .CELL needs to be a DIV / block element
    .cell {
        padding: 8px 10px;
    }

    .head-title {
        display: inline-block;
        font-weight: 700;
        font-size: 15px;
        white-space: nowrap;
    }

    .subhead-title {
        display: inline-block;
        font-weight: 600;
        font-size: 14px;
    }

    .glyphicon {
        position: static;
        vertical-align: middle;
        color: $accordion-expander;
        font-size: 22px;
        margin-left: 15px;
    }

    .table-head,
    .table-subhead {
        td {
            text-align: right;
            background: $gray-light-9;
            padding: 8px 10px;
            border-bottom: 2px solid $gray-med-2;
            line-height: 33px;
            cursor: pointer;

            &:first-child {
                text-align: left;
            }
        }
    }

    .table-subhead td {
        padding: 8px 10px;
        line-height: normal;

        &:last-child {
            white-space: nowrap;
        }
    }

    .table-subhead--regular td {
        border-bottom: $gray-light-8 solid 1px;
    }

    .table-body {
        td {
            text-align: right;
            padding: 0;
            border-bottom: $gray-light-8 solid 1px;

            &:first-child {
                text-align: left;
            }

            &:last-child {
                .cell {
                    padding-right: 47px;
                }
            }
        }
    }

    .table-footer {
        td {
            text-align: right;

            .cell {
                border: 0;
            }

            &:last-child {
                .cell {
                    padding-right: 47px;
                }
            }
        }
    }

    // DCMA specific table
    &.accordion-table__dcma {
        display: none; // initial state
