// Table Responsive (BS Default)
.table-responsive {
    border: 0;
    -webkit-overflow-scrolling: touch; // gives momentum on iOS

    table {
        @media screen and (max-width: $tablet - 1) {
            border: 0;
        }

        tr th,
        tr td {
            width: auto;
        }
    }

    &.table--fix-margin {
        margin-left: 0;
        margin-right: 0;

        @media all and (max-width: $tablet - 1) {
            width: auto;
            margin-left: -15px;
            margin-right: -15px;
        }
    }

    .table--style2 {
        display: block;
        overflow: hidden;
        margin: 0;

        @media all and (max-width: $mobile) {
            min-width: $mobile - 1; // was $tablet
        }

        @media all and (min-width: $mobile + 1) and (max-width: $tablet - 1) {
            min-width: $tablet - 1; // was $desktop
        }
    }
}

// TableSaw modifications
.tablesaw th,
.tablesaw td {
    box-sizing: border-box;
    padding: 8px 10px;
}

.tablesaw thead tr:first-child th {
    padding: 8px 10px;
}

// Stack Mode
.tablesaw-stack td .tablesaw-cell-label,
.tablesaw-stack th .tablesaw-cell-label {
    width: 50%;
    max-width: 50%;
}

.tablesaw-stack td .tablesaw-cell-content,
.tablesaw-stack th .tablesaw-cell-content {
    width: 50%;
    max-width: 50%;
}

.tablesaw-stack tbody tr {
    border-bottom: 0;
    border-top: 1px solid $gray-light-3;
}

// TableSaw Bar
.tablesaw-bar {
    font-family: "Open Sans", Helvetica, sans-serif;
    font-size: 12px;

    * {
        font-family: "Open Sans", Helvetica, sans-serif;
        font-size: 12px;
    }

    // reset custom select
    .custom-select {
        display: inline;

        .custom-select-inner {
            display: inline;
            position: static;

            &::after {
                display: none;
            }
        }
    }

    .tablesaw-toolbar {
        .btn {
            letter-spacing: normal;
            min-width: auto;
        }

        label {
            display: inline-block;
            font-family: inherit;
            font-size: inherit;
            padding: .5em 0;
            line-height: normal;
            font-weight: 600;
        }

        select {
            padding: 0;
            -webkit-appearance: initial;
            -moz-appearance: initial;
            -ms-appearance: initial;
            appearance: initial;
            display: initial;
            width: 100%;
        }
    }

    .tablesaw-advance {
        .btn {
            min-width: auto;
        }

        a.tablesaw-nav-btn {
            letter-spacing: normal;
            clear: none;
        }

        a.tablesaw-nav-btn.up::before,
        a.tablesaw-nav-btn.down::before {
            left: .3em;
        }

        a.tablesaw-nav-btn.left::before,
        a.tablesaw-nav-btn.right::before {
            top: .35em;
        }
    }
}

.tablesaw-enhanced .tablesaw-bar {
    .btn:active {
        background: none;
    }

    .btn.btn-select {
        background: $gray-light-2;
        border: 1px solid $gray-dark-7;
        vertical-align: middle;
        font-weight: normal;
        padding: .2em 1.5em .15em .5em;
        text-shadow: none;
        line-height: normal;

        &::after {
            top: .5em;
            right: 1.5em;
        }
    }

}

// TableSaw - Reset Inputs
.tablesaw-columntoggle-popup .btn-group > label input {
    margin: 3px 3px 3px 4px;
    margin-right: .8em;
    position: static;
    clip: initial;
    width: 13px;
    height: 13px;
    display: inline;
}

