// Simple Responsive Tables
.tbl-responsive {
    th,
    td {
        background: #fff;
    }

    tr.tbl_subHead_bg td {
        padding: 15px 10px 16px;
    }

    // fixed floating headers
    tr.tbl-responsive__rowHolder {
        height: 0;
        margin-bottom: -15px;

        td {
            padding: 0;
            border: 0;
        }
    }

    tr.tbl-responsive__fixedRow {
        td {
            position: relative;
        }

        &.freezeIt {
            display: block;
            position: fixed;
            margin-top: 0;
            z-index: 0;

            td {
                position: static;
                display: block;
            }
        }
    }

    // media queries
    @media screen and (max-width: 736px) { //* iPhone 6+ */
        border: 0;
        border-top: #e0e0e0 solid 1px;
        border-bottom: #e0e0e0 solid 1px;

        thead {
            clip: rect(0 0 0 0);
            height: 1px;
            margin: -1px;
            overflow: hidden;
            padding: 0;
            position: absolute;
            width: 1px;
        }

        tr {
            display: block;
            border-top: 2px #999 solid;

            td[scope=row] {
                font-weight: 700;
            }

            &.tbl_subHead_bg {
                border: 0;
                margin-top: 15px;
                margin-bottom: -2px;
                position: relative;
                z-index: 1;
            }

            &:last-child {
                margin: 0;
            }

            td:first-child {
                border: 0;
                margin: 0;
            }
        }

        tbody tr td,
        tfoot tr td {
            display: block;
            font-size: .93em;
            text-align: right;
        }

        td::before {
            //aria-label has no advantage, it won't be read inside a table
            //content: attr(aria-label);
            content: attr(data-label);
            float: left;
            font-weight: 600;
        }

        td:last-child {
            border-bottom: 0;
        }

        tbody tr:first-child {
            margin-top: 0;
            border: 0;
        }

        //* specific tables */
        .table__summary.tbl-responsive td::before {
            max-width: 40%;
        }

        .table__summary.tbl-responsive td a {
            display: inline-block;
            max-width: 60%;
        }
    }
}

// Financial Engine Retirement Planning table
.table__fe {
    width: auto;
    margin: 0 auto 20px;

    h2,
    h3 {
        line-height: 1.2;
    }

    //* resetting .table
    &,
    th,
    tfoot td {
        background: $white;
        border-color: $white;
        color: #595959;
    }

    > tbody > tr > td,
    > tbody > tr > th,
    > tfoot > tr > td,
    > tfoot > tr > th,
    > thead > tr > td,
    > thead > tr > th {
        border-color: $white;
        vertical-align: middle;
        padding: 15px 20px;
    }

    th,
    td {
        text-align: center;
        font-weight: normal;
    }

    > tfoot > tr > td {
        text-align: center;
    }

    th:first-child,
    td:first-child {
        text-align: left;
    }

    //* colors and layout */
    .light-blue {
        background: $light-blue;
        border-right: 1px $light-blue solid;
        border-top: 1px $light-blue solid !important; // override .table
    }

    .dark-blue {
        background: $dark-blue;
        border-right: 1px $dark-blue solid;
        border-top: 1px $dark-blue solid !important; // override .table
    }

    .light-blue,
    .dark-blue {
        color: $white;
        width: 25%;
        min-width: 200px;
        padding-top: 20px;
        padding-bottom: 20px;
        vertical-align: top;

        h3 {
            font-size: $font-title;
            font-weight: 600;
        }
    }

    th:first-child h2 {
        color: $light-blue;
        font-size: $font-xxlarge;
        font-weight: normal;
    }

    //* vertical separator */
    th:nth-child(3),
    td:nth-child(3) {
        width: 15px;
        padding: 0;
    }

    //* icons */
    .fe-icon-size {
        font-size: 20px;
    }

    td:nth-child(4) .glyphicon {
        color: $light-blue;
    }

    td:nth-child(2) .glyphicon {
        color: $dark-blue;
    }

    //* borders */
    > tbody > tr > th,
    > tbody > tr > td {
        border-bottom: 1px $gray-light-11 solid;
    }

    > tbody > tr:last-child > th,
    > tbody > tr:last-child > td {
        border-bottom-color: $white;
    }

    td:nth-child(2),
    td:nth-child(4) {
        border-left: 1px $gray-light-13 solid;
        border-right: 1px $gray-light-13 solid;
    }

    &.table > tfoot > tr > td:nth-child(2),
    &.table > tfoot > tr > td:nth-child(4) {
        border-bottom: 1px $gray-light-13 solid;
    }

    //************************ hiding columns in mobile smart phone
    @media screen and (max-width: $tablet - 1) {
        // professional management main column (default)
        > thead th:nth-child(1),
        > thead th:nth-child(2),
        > tbody th:nth-child(1),
        td:nth-child(2) {
            min-width: 0;
            padding: 15px 10px;
            border-right-color: $white;
        }

        // "first" column
        > thead th:nth-child(1),
        td:nth-child(1) {
            width: 70%;
        }

        // "second" column
        > thead th:nth-child(2),
        td:nth-child(2) {
            width: 30%;
        }

        // hide other columns
        > thead th:nth-child(3),
        > thead th:nth-child(4),
        td:nth-child(3),
        td:nth-child(4) {
            display: none;
        }

        // "header"
        > thead th:nth-child(1) {
            display: none;
        }

        > thead th:nth-child(2) {
            display: block;
            width: 143%; // hacks!
        }

        // button row
        &.table > tfoot > tr {
            td:first-child {
                display: none;
            }

            > td:nth-child(2) {
                display: block;
                width: 143%; // hacks!
                border: 0;
                border-top: 1px $gray-light-11 solid;
            }
        }

        // online advice main column
        &.table__fe--online-advice {
            > thead th:nth-child(1),
            > thead th:nth-child(4),
            > tbody th:nth-child(1),
            td:nth-child(4) {
                min-width: 0;
                padding: 15px 10px;
                border-right-color: $white;
            }

            // "first" column
            > thead th:nth-child(1),
            td:nth-child(1) {
                width: 70%;
            }

            // "second" column
            > thead th:nth-child(4),
            td:nth-child(4) {
                display: table-cell;
                width: 30%;
            }

            // hide other columns
            > thead th:nth-child(2),
            > thead th:nth-child(3),
            td:nth-child(2),
            td:nth-child(3) {
                display: none;
            }

            // "header"
            > thead th:nth-child(1) {
                display: none;
            }

            > thead th:nth-child(4) {
                display: block;
                width: 143%; // hacks!
            }

            // hide other rows
            tbody {
                tr:nth-child(4),
                tr:nth-child(5),
                tr:nth-child(6) {
                    display: none;
                }
            }

            // button row
            &.table > tfoot > tr {
                td:nth-child(1),
                td:nth-child(2) {
                    display: none;
                }

                > td:nth-child(4) {
                    display: block;
                    width: 143%; // hacks!
                    border: 0;
                }
            }
        }
    }
}
