.section {
    margin: 0 0 $space;
}

//* PLA Panels */
.pla-panel,
.pla-panel-collapse {
    border-radius: 0;
    border: 0;
}

.pla-panel-heading {
    height: 48px;
}

.pla-flip-header {
    position: relative;

    #header-div {
        position: relative;
        top: 0%;
    }
}

#header-div b {
    font-weight: 600;
}

#data-div p,
#header-div p {
    margin-bottom: 0 !important;
}

.pla-panel-btn {
    padding: 6px 12px;
    line-height: auto;
    max-height: 30px;
}

.panel-collapse.noborder {
    border-top: 0;
}

.pla-panel-body-xs,
.pla-panel-body-sm {
    background-color: $gray-light-3 !important;
}

.pla-as-of-date {
    clear: both;
    margin-top: 11px;
    margin-bottom: 11px;
}

.panel-heading.pla-panel-heading.white {
    background-color: $white !important;
}

.pla-accordion-heading {
    padding-left: 0;
    letter-spacing: 2.3px;
}

.esign-logo {
    padding-bottom: 3px;
}

//*begin screen queries */
@media screen and (max-width: $mobile) {
    .pla-panel-heading {
        background-color: $btn-nav-border !important;
    }

    .pla-accordion-heading {
        margin-bottom: 0;
        padding-left: 15px !important;
    }

    .pla-panel-collapse {
        border-top: 0;
    }

    .pla-panel-body {
        background-color: $btn-nav-border !important;
    }

    .pla-accordian-icon {
        display: block;
    }

    .hide-body-btn {
        display: inline-block;
    }

    .hide-panel-btn {
        display: none;
    }

    .pla-corners {
        border-top: $pla-panel-border !important;
        border-left: $pla-panel-border !important;
        border-right: $pla-panel-border !important;
        border-radius: 0 !important;
    }

    .pla-title-align {
        clear: both;
        text-align: center;
    }

    .pla-div-align {
        text-align: right;
    }

    .pla-flip-header #header-div {
        position: absolute;
        top: 100%;
        width: 100%;
        padding-right: 30px;
    }

    .pla-flip-header {
        margin-bottom: $space;
    }

    .no-clicks {
        pointer-events: auto;
    }

    .pla-title-align {
        font-size: $font-small;
    }
}

