//* panels for accordion structure support */
.panel.panel--accordion-placeholder {
    box-shadow: none;
}

//* accordion 'panels' */
.panel--accordion-placeholder .accordion-toggle .glyphicon {
    color: #31708e;
    vertical-align: text-top;
}

.accordion-toggle {
    cursor: pointer;
}

@keyframes plus-to-minus {
    0% {
        content: '\e081';
    }

    100% {
        content: '\e082';
    }
}

@keyframes minus-to-plus {
    0% {
        content: '\e082';
    }

    100% {
        content: '\e081';
    }
}

.accordion-toggle[aria-expanded="true"] .glyphicon-plus-sign::before {
    animation-name: plus-to-minus;
    animation-duration: 1s;
    content: "\e082";
}

.accordion-toggle[aria-expanded="false"] .glyphicon-plus-sign::before {
    animation-name: minus-to-plus;
    animation-duration: 1s;
}

.mobile {
    .not-on-track {
        display: none;
    }

    .mobile-content-panel {
        background-color: #fafafa;
        padding-top: 30px;
        padding-bottom: 30px;
        border-width: 1px 0;
        border-style: solid;
        border-color: #ebebeb;
        margin-bottom: 20px;
    }

    .input-group-addon {
        background-color: #fff;
    }

    .panel--accordion-placeholder .mobile-content-panel {
        border-width: 0 0 1px;
    }

    .panel--accordion-placeholder .accordion-toggle {
        line-height: 24px;
        padding: 10px 15px;
        border: 1px solid #ebebeb;
        margin-bottom: 0;

        .glyphicon {
            float: right;
        }
    }

    .panel.panel-2-accordion__mbl {
        border-collapse: collapse;
        border-width: 1px 0 0;
        border-radius: 0;
        box-shadow: none;
        margin-bottom: 0;

        > .panel-heading {
            background-color: transparent;
        }

        .glyphicon {
            color: #31708e;
        }
    }

}

.tablet,
.desktop {
    .user-pw__guidelines,
    .user-pw__guidelines p {
        font: italic normal 12px / 1.25 "Open Sans";
        color: #717171;
    }
}

// Compare Me Section - table styles are in tables.scss
.not_ontrack__overlay {
    display: none;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, .85);
    z-index: 6;

    p {
        // stroke
        text-shadow: 5px 5px 0 $white,
                     4px 4px 0 $white,
                     3px 3px 0 $white,
                     2px 2px 0 $white,
                     1px 1px 0 $white, // bottom right

                     -5px -5px 0 $white,
                     -4px -4px 0 $white,
                     -3px -3px 0 $white,
                     -2px -2px 0 $white,
                     -1px -1px 0 $white, // top left

                     -5px 5px 0 $white,
                     -4px 4px 0 $white,
                     -3px 3px 0 $white,
                     -2px 2px 0 $white,
                     -1px 1px 0 $white, // bottom left

                     5px -5px 0 $white,
                     4px -4px 0 $white,
                     3px -3px 0 $white,
                     2px -2px 0 $white,
                     1px -1px 0 $white, // top right

                     0 -5px 0 $white,
                     0 -4px 0 $white,
                     0 -3px 0 $white,
                     0 -2px 0 $white,
                     0 -1px 0 $white, // left

                     0 5px 0 $white,
                     0 4px 0 $white,
                     0 3px 0 $white,
                     0 2px 0 $white,
                     0 1px 0 $white; // right

        // link
        a {
            position: relative;
            display: inline-block;
            text-decoration: none !important;

            &::after {
                content: '';
                position: absolute;
                display: none;
                width: 100%;
                height: 1px;
                background: $link-text;
            }

            &:hover::after {
                display: block;
            }

            sup {
                text-shadow: none; // (R)
            }
        }
    }
}

.not-on-track {
    .not_ontrack__overlay {
        display: block;
    }

    .compare-table__footnote {
        opacity: .15;
    }
}

.well--plain {
    border: none;
