//*------------------------------------*\
//    $MOBILE RULES ONLY
//\*------------------------------------*/
@media only screen and (max-width: $tablet - 1) {
    //* ------------- utility class ----------------*/
    .mob__panel {
        background-color: $header-bg;
        text-align: center;
        padding: 15px;
        border-color: $prim-heading-border;
        border-style: solid;
        border-width: 1px 0;
        margin: 10px -15px;
    }

    //*-------------- balance page -----------------*/
    .balance .block,
    .activitysummary .block {
        background-color: $gray-light;
    }

    .balance .block-right,
    .activitysummary .block-right {
        position: relative;
    }

    .balance .block-right::before {
        content: '';
        background: $box-borders;
        left: 50%;
        margin-left: -150px;
        position: absolute;
        height: 1px;
        width: 300px;
    }

    .balance .well-title {
        text-align: center;
        padding-bottom: 0 !important;
    }

    //*-------------- Activity summary page -----------------*/
    .activitysummary .well-title {
        text-align: center;
        text-transform: uppercase;
        font-size: $font-small;
        letter-spacing: 3px;
    }

    .activitysummary .well-body p {
        text-align: center;
    }

    .trs-activity-select_right .well-title {
        background-color: $white;
        padding-top: $pad-double;
    }

    //*-------------- DASHBOARD -----------------*/
    .dashboard {
        //* ----- nesting mobile dashboard rules ----- */
        .dashboard--image {
            display: none;
        }

        .section-title {
            text-align: center;
            padding-top: 15px;
        }

        .block {
            background-color: $gray-light;
            margin-bottom: 0;
        }

        .row-margin-fix {
            margin-left: -15px;
            margin-right: -15px;
        }

        .highcharts-container {
            width: auto !important;
            text-align: center !important;
        }

        .dash-balances {
            .block {
                margin-bottom: 0;
            }

            .well-title {
                padding-bottom: 0;
                margin: 0;
            }

        }

        .mobile-legend {
            margin-left: -15px;
            margin-right: -15px;

            ul li {
                display: inline-block;
                text-align: center;
                width: 32%;
                float: left;

                .box-sm {
                    margin: 0;
                }
            }

            ul.w-sbl li {
                width: 24%;
            }
        }

        .mobile-footnote {
            background-color: $gray-light;
            //margin-top: -20px;
        }

        .contributions {
            margin-top: -20px;
        }

        .padding-bottom-lg {
            padding-bottom: 0 !important;
        }

        .compare {
            .block {
                border-bottom: $gray-light-4 solid 1px;
            }

            .icon {
                display: block;
            }
        }
    }

    //*-------------- Block css for activity summary page -----------------*/
    .trs-activity-select_right .well-body {
        padding: 0 !important;
    }

    .activitydetails {
        .trs-activity-details {
            margin-left: -14px;
            margin-right: -14px;
            padding-left: 5px;
            padding-right: 5px;
            background: $gray-light;
            border-top: $prim-heading-border solid 1px;
            border-bottom: $prim-heading-border solid 1px;

            .trs-activity-select_left {
                padding-bottom: 0 !important;
                margin-bottom: 0;

                .well-body {
                    .activity-date {
                        .hasCustomSelect { width: 280px !important; }
                        .customSelectInner { width: 268px !important; }
                    }
                }
            }
        }
    }
}

//*-------------- Block css for activity summary page -----------------*/
.trs-activity-select_right .well-body {
    padding: $pad * .68;
}

//*-------------- SSO MOBILE FOR DASHBOARD -----------------*/
@media only screen and (max-width: $mobile) {
    .desktopDashboard {
        display: none;
    }

    .mobileDashboard {
        display: block;

        .panel-default > .panel-heading {
            background: #fff;
        }

        .panel-group .panel {
            border-radius: 0;
            margin-left: 0;
            margin-right: 0;
            border-top: solid #ebebeb 1px;

            + .panel {
                margin-top: -1px;
            }
        }

        .sso--accordion_title {
            font-weight: 600;
            font-size: 16px;
            letter-spacing: 2.2px;

        }

        .panel-body {
            background-color: #f9f9f9;
            padding-top: 0;
            padding-bottom: 0;
        }

        .panel-heading {
            height: 40px;
        }

        .sso--dashboard .glyphicon-minus-sign,
        .sso--dashboard .glyphicon-plus-sign {
            color: #407399;
            font-size: 22px;
        }

        h3 {
            font-size: 18px;
            padding: 10px 0;
        }

        .sso--plan {
            border-bottom: solid 1px #ebebeb;
            // margin: 0 -15px;
            padding-bottom: 20px;

        }

        .sso--plan_headline {
            font-weight: bold;
            text-align: center;
            padding: 25px 0;
        }

        .sso--plan_button {
            text-align: center;
            padding-top: 30px;
        }

        .sso--headline {
            border-bottom: solid 1px #ebebeb;
        }

        .sso--font1 {
            font-size: 14px;
        }

        .sso--font2 {
            font-size: 14px;
        }

        .sso--totals {
            padding: 15px 20px;
            border-bottom: solid 1px #ebebeb;
            text-align: center;
            font-weight: bold;

            div {
                padding: 0;
            }
        }

        .sso--footnotes {
            font-style: italic;
            font-size: 13px;
            padding: 10px 20px;
        }
    }
}

@media only screen and (min-width: $mobile + 1) {
    .mobileDashboard {
        display: none;
    }
}

//*-------------- SSO MOBILE FOR CONTACT US -----------------*/
@media only screen and (max-width: $mobile) {
    .contact-us {
        .sso--box__grey {
            padding: 20px 0;
        }

        p,
        span {
            font-size: 14px;
            line-height: 20px;
        }

        h3 {
            font-size: 16px;
            line-height: 24px;
            text-align: center;
        }

        .margin-top-lg {
            margin-top: 20px !important;
        }

        label {
            float: left;
        }

        .sso--contact_toggle {
            display: block;

            .btn {
                padding: 10px 15px;
                line-height: auto;
                height: auto;
                font-size: 12px;
                border-radius: 0;
            }
        }

    }
}

@media only screen and (min-width: $mobile + 1) {
    .sso--contact_toggle {
        display: none;
    }
}
