//*----------------------------------*\
//    #MENU
//*----------------------------------*/

//**
//* The site's main menu can have two different states:
//*
//* 1) Regular sidebar with collapsible sections, mapping out the entire site map.
//*    The sections are collapsed by default.
//*
//* 2) A full-width site map, with all sections expanded at all times, over a
//*    different background color. It is itself collapsed by default and expands
//*    from a hamburger menu below the page header. This is the tablet/mobile
//*    version of the main menu.
//*
//* This dual state behavior relies on Response.js library and the #REFLOW-MENU
//* section of ddol-responsive.js
//**/

// Menu item's dimensions, clockwise
$menu__padding-top: 10px;
$menu__padding-right: 0;
$menu__padding-bottom: 10px;
$menu__padding-left: 40px;
$menu__padding-left--expanded: 21px; // [2]

$menu__margin-top: 0;
$menu__margin-right: 10px;
$menu__margin-bottom: 10px;
$menu__margin-left: -15px;
$menu__margin-left--expanded: $menu__margin-left + $menu__padding-left--expanded; // [2]

// Corrected dimensions for the headings, clockwise
$menu__heading-top: $menu__padding-top + 5px;
$menu__heading-right: $menu__padding-right;
$menu__heading-bottom: $menu__padding-bottom;
$menu__heading-left: $menu__padding-left + $menu__margin-left;
$menu__heading-left--expanded: $menu__padding-left--expanded; // [2]

// Special cases
$menu__active-item-border-width: 6px;

// Font size
$menu__font-size: 14px;

//*----------------------------------*\
//    #Desktop Primary Nav
//*----------------------------------*/
#sidebar {
    overflow: hidden;
}

.nav--primary {
    font-size: $menu__font-size;

    .panel {
        box-shadow: none;
        border: 0;
        margin: 0;
    }

    .list-group-item {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        padding: 10px 15px;
        margin-bottom: 0;
        border: 0;
        border-bottom: $prim-heading-border solid 1px;
        box-shadow: none;

        &.selected {
            border-left: $emphasis solid $menu__active-item-border-width;

            .icon {
                margin-left: -$menu__active-item-border-width;
            }
        }
    }

    .nav--primary__head {
        color: $black;
        font-weight: 700;
        background-color: $white;
        padding: $menu__heading-top $menu__heading-right $menu__heading-bottom $menu__heading-left;
        border-bottom: $prim-heading-border solid 1px;

        .icon {
            color: $prim-heading-icon;
            font-size: $font-base;
            margin-right: $menu__active-item-border-width;
            margin-top: 2px;
            vertical-align: top;
        }

        .chevron {
            padding-right: 18px;
            color: $gray-med-2;
            font-size: $font-xxsmall;

            &::after {
                content: '\e260';
                float: right;
                color: $gray-med-2;
            }
        }

        .item-name {
            display: inline-block;
            max-width: 165px;
        }
    }

    a.collapsed .chevron::after {
        content: '\e259';
    }

    .nav--primary__sub {
        .list-group-item {
            color: $gray-med-2;
            padding-left: 45px;
            background-color: $header-bg;
            border-top-left-radius: 0;
            border-top-right-radius: 0;
            margin-bottom: 0;
            border: 0;
            box-shadow: none;

            // &:hover {
            //     background-color: $gray-light-2;
            // }

            &.selected {
                color: $black;
                border-left: $emphasis solid 6px;
                padding-left: 39px;
                font-weight: 700;
            }

            &:last-child {
                border-bottom-left-radius: 0;
                border-bottom-right-radius: 0;
            }
        }
    }

    .disabled,
    .disabled:hover {
        text-decoration: line-through;
        color: $gray;
    }

    .nav--primary__contact {
        border-bottom: 0;
    }

    .row {
        margin-right: 0;
        margin-left: 0;
    }
}

.sidebar-offcanvas {
    position: relative;
    padding: 0;
    transition: left .25s ease-out;
    background-color: $white;
    z-index: 20;

    //* collapsed sidebar styles */
    @media screen and (min-width: $tablet) and (max-width: $desktop - 1) {
        position: absolute;
        top: $header-height;
        left: -245px;
        width: 245px;
        border-right: 1px solid $gray-light-3;

        &.slid {
            left: 0;
        }
    }

    @media screen and (min-width: 0) and (max-width: $tablet - 1) {
        position: absolute;
        top: 48px;
        left: -245px;
        width: 245px;
        border-right: 1px solid $gray-light-3;

        &.slid {
            left: 0;
        }
    }
}
//* END Desktop Primary Nav  */

//*----------------------------------*\
//    #TABLET AND MOBILE Primary Nav
//*----------------------------------*/
.mobile {
    .sidebar-offcanvas {
        position: absolute;
        top: 48px;
        left: -100%;
        width: 245px;
        border-right: 1px solid $gray-light-3;

        &.slid {
            left: 0;
        }
    }

    @media screen and (max-width: $mobile) {
        &.menu-is-open {
            overflow-x: hidden;
            width: 100%;
        }
    }

    @media screen and (max-width: $tablet - 1) {
        .menuwrapper {
            width: 100%;
            float: left;
            position: relative;

            .mobile-trigger {
                background-color: $white;
                border: 0;
            }

            &:first-child {
                margin-right: 100px;
            }

            ul {
                padding: 0;
                list-style: none;
                transform-style: preserve-3d;
            }

            li {
                position: relative;

                a {
                    display: block;
                    text-decoration: none;
                    background-color: $white;
                    position: relative;
                    padding: 15px 25px;
                    line-height: 20px;
                    font-weight: bold;
                    color: $black;
                    outline: none;
                    border-bottom: $prim-heading-border solid 1px;

                    &.selected {
                        border-left: $error solid 6px;
                        padding-left: 19px;
                    }
                }

                &.mobile-back > a {
                    color: $link-text;
                    padding-left: 30px;
                    background: $white;
                }

                &.mobile-back::after,
                > a:not(:only-child)::after {
                    position: absolute;
                    top: 0;
                    line-height: 50px;
                    speak: none;
                    font-family: 'Glyphicons Halflings';
                    -webkit-font-smoothing: antialiased;
                    content: '\e258';
                }

                &.mobile-back::after {
                    left: 10px;
                    color: $link-text;
                    transform: rotate(180deg);
                }

                > a::after {
                    right: 10px;
                    color: rgba(0, 0, 0, .15);
                }

                //* Hide the inner submenus */
                .submenu {
                    display: none;
                }

                // DCMA Contact Us
                .dcma__contact-us {

                    .phone-number a,
                    .chat-now a {
                        padding: 0;
                        border: 0;
                    }
                }
            }

            //* Hide desktop chevron */
            .chevron {
                display: none;
            }

            .menu {
                position: absolute;
                width: 100%;
                backface-visibility: hidden;
                pointer-events: auto;
                transform: translateY(0);

                &.menu-toggle {
                    transition: transform .3s ease;
                }
            }

            //* Dynamically added submenu outside of the menu context */
            > .submenu {
                position: absolute;
                width: 100%;
                top: 0;
                left: 0;
                margin: 0;

                &.mobile-animate-in {
                    animation: SubMenuAnimIn .3s ease-in-out;
                }

                &.mobile-animate-out {
                    animation: SubMenuAnimOut .3s ease-in-out;
                }
            }
        }

        //*
        //When a submenu is openend, we will hide all li siblings.
        //For that we give a class to the parent menu called "mobile-subview".
        //We also hide the submenu link.
        //The opened submenu will get the class "mobile-subviewopen".
        //All this is done for any sub-level being entered.
        //*/
        .menu {
            &.mobile-subview li,
            &.mobile-subview li.mobile-subviewopen > a,
            &.mobile-subview li.mobile-subview > a {
                display: none;
            }

            &.mobile-subview li.mobile-subview,
            &.mobile-subview li.mobile-subview .submenu,
            &.mobile-subview li.mobile-subviewopen,
            &.mobile-subview li.mobile-subviewopen > .submenu,
            &.mobile-subview li.mobile-subviewopen > .submenu > li {
                display: block;
            }

            //* Animation classes for moving out and in */
            &.mobile-animate-out {
                animation: MenuAnimOut .3s ease-in-out;
            }

            &.mobile-animate-in {
                animation: MenuAnimIn .3s ease-in-out;
            }
        }

        // Animations
        @keyframes MenuAnimOut {
            0% {
                transform: translateX(0);
                opacity: 1;
            }

            100% {
                transform: translateX(-100%);
                opacity: 0;
            }
        }

        @keyframes MenuAnimIn {
            0% {
                transform: translateX(-100%);
                opacity: 0;
            }

            100% {
                transform: translateX(0);
                opacity: 1;
            }
        }

        @keyframes SubMenuAnimIn {
            0% {
                transform: translateX(100%);
                opacity: 0;
            }

            100% {
                transform: translateX(0);
                opacity: 1;
            }
        }

        @keyframes SubMenuAnimOut {
            0% {
                transform: translateX(0%);
                opacity: 1;
            }

            100% {
                transform: translateX(100%);
                opacity: 0;
            }
        }
    }
}
//* END TABLET AND MOBILE Primary Nav

.menu .icon {
    margin-right: $menu__active-item-border-width;
}

.trs-utility {
    font-size: $font-base;
    line-height: 150%;
    background-color: $white;
    padding: 0;
    margin: 0;

    &.vertical [role="menuitem"] {
        display: block;
        font-weight: 700;
        color: $black;
        padding: 15px 0 10px 25px; //9px 36px;

        &:hover,
        &:focus {
            text-decoration: none;
            color: $link-text;
            background-color: $gray-light;

            .trs-utility--light {
                color: $link-text;
            }
        }

        + [role="menuitem"] {
            border-top: 1px solid $tbl-border;
        }
    }
}

.trs-utility--light {
    display: block;
    font-weight: 500;
    color: $gray-med-2;
    padding-left: 19px;

    a {
        color: $gray-med-2;

        &:hover {
            color: $link-text;
        }
    }
}

//Pagination
.pagination {
    overflow: hidden;

    li {
        float: left;
        border-right: 1px solid $gray-light-2;

        &:last-child {
            border: 0;
        }
    }

    a {
        padding: $pad;
    }
}

.btn-breadcrumb {
    background-color: $breadcrumb-bar;
    overflow: hidden;
    height: 40px;
    padding: 10px 22px;
}

.breadcrumbs {
    background-color: $breadcrumb-bar;
    overflow: visible;
    min-height: 40px;
    height: auto;
    padding: 10px 22px;

    > li {
        color: $white;
        font-weight: 400;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        float: left;
        margin-left: 10px;

        &:first-child {
            margin-left: 0;
        }
    }

    .breadcrumb__link {
        color: $white;
        font-weight: bold;
        text-decoration: underline;

        &:hover,
        &:focus {
            color: $gray-light;
            text-decoration: none;
        }
    }
}

.breadcrumb__acct-name {
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
}

.breadcrumb__item--border {
    padding: 0 0 0 8px;
    border-left: 1px solid $prim-heading-border;
}

.breadcrumb__dropdown {
    button,
    .breadcrumb__button {
        line-height: 14px;
        background-color: transparent;
        padding: 0 5px;
        border: 1px solid $soc-icon-outline;
        border-radius: 4px;
    }

    .dropdown-list {
        display: none;

        li {
            display: list-item;
            white-space: normal;
            float: none;
            margin: 14px 0;
        }
    }

    &.open {
        background-color: transparent !important;
        border: 0;

        button {
            background-color: $gray-dark-4;
        }

        .dropdown-list {
            display: block;
            position: absolute;
            right: 0;
            top: 22px;
            background-color: $gray-warm;
            width: 50%;
            padding: 10px 20px;
            border-radius: 8px;
        }

        .breadcrumb__dropdown__ellipse {
            background-color: $white;
        }
    }
}

.breadcrumb__dropdown__ellipse {
    display: inline-block;
    width: 8px;
    height: 8px;
    border: 1px solid $white;
    margin-bottom: 1px;
    border-radius: 5px;

    + .breadcrumb__dropdown__ellipse {
        margin-left: 5px;
    }
}

//* --- COBRAND BREADCRUMBS --- */
.cobrand--bencor .breadcrumbs {
    background-color: #173074; //turn color into a variable if used frequently.
}

.cobrand--trinity .breadcrumbs {
    background-color: #591072; //turn color into a variable if used frequently.
}

//* --- REVAMP BREADCRUMB SEPARATOR - ADDED SVG TO TRS-ICONS --- */
.breadcrumbs {
    .separator {
        top: -8px;
        font-size: 40px;
        margin-bottom: -20px;
        color: #333;
    }
}

//*----------------------------------*\
//    #PLA MOBILE EXPANDABLE NAV
//\*---------------------------------*/
.btnnavdiv {
    max-width: 720px;
    text-align: center;
    margin: 0 auto;

    > .pla-top-button:first-child {
        border-top-left-radius: 6px !important;
    }

    > .pla-top-button:last-child {
        border-bottom-right-radius: 6px !important;
        border-right: 1px solid $btn-nav-border;
    }

    > .pla-top-button:first-of-type + div + div {
        border-top-right-radius: 6px !important;

        + div + div + div + div {
            border-bottom-left-radius: 6px !important;
        }
    }
}

.btnnavgroup {
    width: 100%;
}

.pla-top-nav-arrow {
    margin-top: 3px;
}

.pla-btn-group-container {
    margin-top: 30px;
    margin-bottom: 60px;
    width: 100%;
    margin: 0 auto;

    h2 {
        font-size: 20px;
        color: #0087f1; //turn color into a variable if used frequently.
    }
}

.pla-btn-group-vert {
    width: 100% !important;
    padding: 0 !important;

    > .pla-btn-group-wrap {
        padding: 0  !important;
    }
}

.pla-btn-group-wrap {
    position: relative;
    overflow: hidden;
    padding: 10px;
}

.gradient {
    width: 100%;
    height: 35px;
    position: absolute;
    bottom: 0;
    left: 0;
    border-bottom: 2px solid $emphasis;
}

.pla-view-more {
    background: $emphasis;
    color: $white;
    padding: 5px;
    text-align: center;
    width: 140px;
    clear: both !important;
    float: none;
    margin: 0 auto;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    font-size: $font-xsmall;
    margin-bottom: 15px;

    a {
        font-weight: 700;
        text-decoration: none;
        text-align: center !important;
        margin: 0 auto !important;
        color: $white;
        text-transform: uppercase;
    }
}

// Account switch box, above the menu
.switch-box {
    border-width: 0 0 1px;
    border-radius: 0;
    margin-bottom: 0;
}
