/* lists */
ol, ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

/* Definition Lists */
dl {
    overflow: hidden;
    margin: 0 0 $space;
}

dt {
    font-weight: bold;
}

dd {
    margin-left: 0;
}

dl.dl-horizontal {
	margin: 0;

	dd, dt {
		max-width: 66%;
	}
	dt {
		float: left;
		clear: both;
	}
	dd {
		float: right;
		clear: right;
		text-align: right;
	}
}
