$header-height: 80px;

@keyframes down-to-up {
    0% {content: '\e259';}
    100% {content: '\e260';}
}

@keyframes up-to-down {
    0% {content: '\e260';}
    100% {content: '\e259';}
}

.trs-header {
    background: $white;
    height: $header-height;
    padding-top: 0;
    padding-bottom: 0;
    border-bottom: 1px solid $gray-light-6;

    .logo img {
        max-height: $header-height;
    }

    .row {
        margin: 0;
    }

    //* TOP SUBNAV */
    .subnavbar {
        .logo {
            max-height: 50px;
            margin-top: 11px;
            margin-left: 8px; // 15px column padding + 8px = 23px 'margin'

            @media all and (max-width: $tablet - 1) {
                margin: 0 auto;
            }
        }
    }

    button,
    [type='button'] {
        float: right;
    }

    button.navbar-toggle {
        float: left;
    }

    ul {
        float: left;
        margin-top: 15px;
        margin-left: 10px;
    }

    // Header TRS links - dropdowns position
    .header-trs-links {
        position: relative;

        .user-messages {
            position: absolute;
            right: 187px; // it's 60px, visually, from the little man icon
        }

        .user-info {
            position: absolute;
            right: 22px; // it's 60px, visually, from the logout button
        }
    }

    a.link {
        float: left;
        font-size: $font-base;
        margin-top: 30px;
        margin-left: 40px;
    }

    .btn-logout {
        margin-top: 22px;
        margin-right: 8px; // 15px column padding + 8px = 23px 'margin'
    }

    a.dropdown-toggle {
        color: $gray-med-2;
        font-weight: 700;

        &[aria-expanded="true"] .glyphicon-menu-down::before {
            animation-name: down-to-up;
            animation-duration: 1s;
            content: '\e260';
        }

        &[aria-expanded="false"] .glyphicon-menu-down::before {
            animation-name: up-to-down;
            animation-duration: 1s;
        }

        &.disabled {
            color: #ccc;
            cursor: pointer;
            pointer-events: none;

            .newMsgAlert {
                background: #aaa;
            }
        }
    }

    .dropdown {
        padding: 15px 20px 10px;
        border: 1px solid transparent;
        width: 155px; // this is needed to go nicely with the actual dropdown

        &.open {
            z-index: 1000;
            background-color: $white !important;
            border-top-right-radius: 5px;
            border-top-left-radius: 5px;
            border: 1px solid $gray-dark-7;
            border-radius: 4px;
            box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
            background-clip: padding-box;
        }
    }

    ul.dropdown-menu li {
        width: 100%;
        padding: 0; // was 10px
        background-color: $white;
        float: left;
        clear: left;
        border-left: 7px solid transparent;

        &:hover {
            border-left: 7px solid $emphasis;
        }
    }

    .open > ul.dropdown-menu {
        z-index: 991;
        display: block;
        transform: scale(1, 1);
        padding: 5px 0 0;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;
        background-color: $white;
        border: 1px solid $gray-dark-7;
        border-top: 1px solid transparent;
        box-shadow: 0 0 6px rgba(0, 0, 0, .24);
    }

    .open > ul.dropdown-menu::before {
        content: '';
        position: relative;
        top: -12px;
        left: 0;
        z-index: 10;
        display: block;
        width: 153px; // related to .dropdown class
        height: 10px;
        background-color: $white;
        border: 1px solid $white;
        box-shadow: none;
    }

    .dropdown-menu {
        position: absolute;
        top: 40px;
        left: -1px;
        z-index: 0;
        display: none;
        float: left;
        padding: 0;
        margin: 2px 0 0;
        list-style: none;
        font-size: $font-base;
        text-align: left;
        background-color: $white;
        border: 1px solid $gray-dark-7;
        border-radius: 4px;
        box-shadow: 0 6px 6px rgba(0, 0, 0, .175);
        background-clip: padding-box;
        border-top-left-radius: 0;
    }
}

.trs-header__menu-link {
    display: block;
    font-weight: 700;
}

.trs-header__menu-link:hover,
.trs-header__menu-link:focus {
    color: inherit;
}

.trs-header .btn-logout .trs-icons-lock {
    display: inline-block;
    margin-top: -2px;
    margin-left: -2px;
}

.trs-icons-lock {
    top: 1px;
    position: relative;
}

.lt-gray {
    background-color: $gray-light-5;
}

.top-bar {
    height: 36px;
    vertical-align: middle;
    line-height: 36px;
    margin-left: -15px;
    margin-right: -15px;
}

.call-us-link {
    font: $font-pri-black;
    font-size: $font-base;
    color: $black;
    font-weight: 700;
}

.fin-pro {
    font: $font;
    font-size: $font-base;
    color: $black;
    font-weight: 500; // there's no such font weight, there is: 300, 400, 600, 700, 800
}

@media screen and (max-width: 1024px) {
    .trs-header__menu-link {
        font-size: $font-base;
        padding-top: 29px;
    }

    .trs-header__logo--mobile {
        height: 62px;
        padding-top: 10px;
    }
}

@media screen and (max-width: $tablet - 1) {
    //* .btn--small located in buttons.scss */
    .trs-header {
        height: 48px;
        padding-top: 8px;
        padding-bottom: 14px;

        .btn-logout {
            margin-top: 3px;
        }
    }

    .trs-header__menu-link {
        font-size: $font-small;
        padding-top: 6px;
    }

    .trs-header__logo--mobile {
        height: 30px;
        padding-top: 0;
    }

    .trs-header .btn-logout.btn--small {
        margin-top: 2px;
    }
}

//************************************************************************/
header nav.nav > ul {
    margin: 37px 0 0 !important;
    height: 0;

    li a {
        padding: 15px 10px;
    }
}

header nav.nav ul li.search a { // shouldn't be "nav.nav > ul" as the one above ?
    padding-left: 35px;
}

nav > ul > li {
    position: relative;
    border: 1px solid transparent;
}

.navbar-default {
    background-color: transparent;
    border-color: transparent;
}

.dropdown.open {
    z-index: 1000;
    border: 1px solid $gray-dark-7;
    border-radius: 4px;
}

.navmenu-default .navmenu-nav > li > a {
    text-align: left;
}

.nav .open > a {
    background-color: transparent;
    border-color: #337ab7;
}

.navbar-default .navbar-nav > .open > a,
.navbar-default .navbar-nav > .open > a:hover,
.navbar-default .navbar-nav > .open > a:focus {
    background-color: $gray-light-5;
    color: $gray-light-5;
    box-shadow: 0 6px 12px rgba(0, 0, 0, .24);
}

.navbar-right .dropdown-menu {
    right: auto;
    left: 0;
}

.navbar-right .navbar-search .dropdown-menu {
    right: 0;
    left: auto;
}

.open > ul.dropdown-menu {
    z-index: 991; // dropdown-backdrop, on mobile, is 990
    display: block;
    transform: scale(1, 1);
    padding: 5px 0 0;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    background-color: $white;
    border: 0;
    box-shadow: 0 6px 12px rgba(0, 0, 0, .24);
}

.open > .dropdown-menu > li > a {
    color: $black;
    cursor: pointer;
}

.open > .dropdown-menu > li > a:hover {
    background-image: none;
    color: $black;
    cursor: pointer;
}

.dropdown-menu > li > a {
    text-align: left;
    color: $white;
    font-weight: 400;
    padding: 10px 20px;
}

.dropup > .dropdown-menu::after {
    border-bottom: 0;
    border-top: 6px solid rgba(39, 45, 51, .9);
    top: auto;
    display: inline-block;
    bottom: -6px;
    content: '';
    position: absolute;
    left: 50%;
    border-right: 6px solid transparent;
    border-left: 6px solid transparent;
}

ul.dropdown-menu li:last-child {
    border-bottom: 0;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
}

.dropdown-menu li:hover a,
.dropdown-menu > li > a:hover,
.dropdown-menu > li > a:focus {
    background-color: $gray-light-2;
    color: $black;
}

ul.dropdown-menu li:hover {
    background-color: $gray-light-2;
    color: $black;
    border-left: 4px solid $water-blue;
}

.nav.navmenu-nav .dropdown.open {
    background-color: rgb(238, 238, 238);
}

.nav.navmenu-nav .dropdown.open::after {
    content: '\e259';
}

.nav > li > a {
    padding: 5px 10px 8px;
    //font-size: $font-base;
    //color: #3a3a3a !important;
    //border-top-right-radius: 5px;
    //border-top-left-radius: 5px;
}

header::after,
header::before {
    content: none;
}

//*------------------------------------*\
//    TA/PLA TOPBAR HEADER
//\*------------------------------------*/
.topbar {
    height: 50px;
    background-color: $gray-med-2;
    padding: 0;
    overflow: hidden;

    li {
        color: $white;
        font-weight: 600;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        float: left;
        margin-left: 0;
        padding: 15px 10px;

        &.separator {
            margin-left: 0;
        }

        &.active {
            background-color: $emphasis;
        }

        div {
            position: relative;
            top: -4px;
            left: -2px;
            width: 28px;
            height: 28px;
            border-top: 1px solid $black;
            border-right: 1px solid $black;
            transform: rotate(45deg);
        }
    }

    li:first-child {
        margin-left: 0;
    }

    a {
        color: $white;

        &:hover {
            color: $gray-light;
        }
    }

    .glyphicon {
        margin-right: 5px;
    }

    .finpro-col {
        text-align: center;
        padding: 12px 0;
    }

    .go-pro,
    .go-pro:hover {
        color: $gray-med-2;
        font-weight: bold;
        text-decoration: underline;
    }

    .finpro,
    .go-pro {
        text-transform: uppercase;
        font-size: $font-small;
    }

    @media all and (max-width: $tablet - 1) {
        height: 42px;
        background-color: $gray-light-5;

        &.trs-icons-close-btn-after::after {
            content: 'b';
            position: absolute;
            top: 15px;
            right: 11px;
            font-family: 'trs-icons' !important;
            font-style: normal !important;
            font-weight: normal !important;
            font-variant: normal !important;
            text-transform: none !important;
            speak: none;
            line-height: 1;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }
    }

    @media all and (max-width: 480px) {
        height: 42px;
        background-color: $gray-light-5;

        .finpro,
        .go-pro {
            text-transform: capitalize;
        }
    }
}
