// ie10, ie11 target hack - NOT USING any .ieXX CSS classes
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    sup {
        font-size: 100%;
        top: -.25em;
    }

    h1 sup {
        font-size: 55%;
        top: 0;
    }

    .btn sup {
        font-weight: normal;
    }

    // custom dropdown fix for the down arrow
    .layout-responsive .form-inline .custom-select,
    [data-ui-compat='responsive'] .form-inline .custom-select {
        margin: 0;
    }

    .layout-responsive .form-inline .custom-select .custom-select-inner,
    [data-ui-compat='responsive'] .form-inline .custom-select .custom-select-inner {
        margin: 0;
    }

    .layout-responsive .form-inline .custom-select .custom-select-inner::after,
    [data-ui-compat='responsive'] .form-inline .custom-select .custom-select-inner::after {
        top: -8px;
    }
}

