//* main */
html {
    font-size: $font-base;
}

body {
    background: $white;
    font-family: $font;
    color: $black;
    font-size: $font-base;
    -webkit-text-size-adjust: 100%;
}

//* bootstrap fixes */
pre code {
    white-space: inherit;
}

// WebSphere Snooper
.snooper {
    display: none;
    width: auto;
    position: fixed;
    bottom: 15px;
    right: 15px;
    background: $snooper-bg;
    color: $black;
    border: 1px $black solid;
    font-weight: bold;
    font-size: 11px;
    line-height: 1.5;
    z-index: 1061;
    padding: 8px 12px;
    box-shadow: 2px 2px 3px $snooper-shadow;

    * {
        font-size: 11px;
    }
}

// Selections
::selection {
    color: $white;
    background: $btn-primary;
}

::-moz-selection {
    color: $white;
    background: $btn-primary;
}
